import { useState, useContext } from "react";
import styled, {
  css,
  FlattenSimpleInterpolation,
  keyframes,
} from "styled-components";
import {
  Input,
  Inline,
  Stack,
  Button,
  Page,
  Modal,
  CloseIcon,
  H3,
  H2,
} from "rzr-components";

import RzR from '../../contexts/RzR'

type FormElement = HTMLFormElement & {
  elements: {
    url: HTMLInputElement;
  };
};

const NewRouterModal: React.FC<{ close: () => void }> = ({ close }) => {
  const rzr = useContext(RzR)
  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const url = (e.target as FormElement).elements.url.value;

    // TODO: make https discovery part of connection matrix
    rzr
      .storeRouter(url)
      .then(close);
  };

  return (
    <Modal $size="sm" $justify="stretch">
      <Stack onSubmit={onSubmit} $justify="space-between" as="form" $expand>
        <Inline $justify="space-between">
          <H2 $font="secondary">New router</H2>
          <CloseIcon onClick={close} />
        </Inline>
        <Inline $justify="stretch" as="label" $gap="md" $align="center">
          <span>https://</span>
          <Input name="url" $grow />
        </Inline>
        <Inline $justify="flex-end">
          <Button $variant="secondary">store</Button>
        </Inline>
      </Stack>
    </Modal>
  );
};

export default NewRouterModal;
