  `}
  padding-left: 1rem;
  width: calc(100% - 2rem);
  margin: 0.5rem;
  margin-bottom: 1rem;

  @media screen and (min-width: 768px) { 
    width: calc(50% - 3rem);
  }

  @media screen and (min-width: 1280px) { 
    width: calc(33% - 3rem);
  }

  @media screen and (min-width: 1440px) { 
    width: calc(25% - 3rem);
  }
`;

