import EventEmitter, { Listener } from "../../../../EventEmitter";
import { SignalServerConnection } from "../RouterConnection";
import { Ethereum } from "../../../lvl2";
import { verifyMessage, concat, hexlify, getBytes, Wallet } from "ethers";
import { once } from "../../../../utils";

const TOKEN_BYTE_LENGTH = 24;

export type ProtocolEventMap = {
  connectionStatusChanged: boolean;
  newMessage: ArrayBuffer;
};

/*
 * Takes care of authentication and provides an interface
 * for further connection negotiation
 * might lift authentication above
 * */
export class Protocol extends EventEmitter<ProtocolEventMap> {
  listeners = {
    connectionStatusChanged: new Set<Listener<boolean>>(),
    newMessage: new Set<Listener<ArrayBuffer>>(),
  };

  isConnecting = false;
  identity: string;
  router: SignalServerConnection;
  isConnected: boolean;
  protocolId = 0b00;

  constructor(router: SignalServerConnection, identity: string) {
    super();
    this.isConnected = false;
    this.identity = identity;
    this.router = router;
  }

  closeConnection() {}
  createOffer(): Promise<Uint8Array> {
    return Promise.resolve(new Uint8Array());
  }

  processAnswer = (answer: Uint8Array) => {};
  processOffer = (offer: Uint8Array): Promise<Uint8Array> => {
    return Promise.resolve(new Uint8Array());
  };

  reset() {
    this.isConnected = false;
    this.emit("connectionStatusChanged", false);
    this.removeAllListeners();
  }

  send(message: ArrayBuffer) {}
}
