import { useContext, useState } from "react";
import {
  Button,
  Modal,
  H2,
  Input,
  Inline,
  Stack,
  CloseIcon,
} from "rzr-components";
import RzR from "../../contexts/RzR";

const UnlockModal: React.FC<{ close: () => void }> = ({ close }) => {
  const rzr = useContext(RzR);
  const [isLoading, setLoading] = useState(false);
  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const { identity: identityField, password: passwordField } =
      e.target as HTMLFormElement;

    setLoading(true);

    (rzr.isIdentityAvailable
      ? Promise.resolve()
      : rzr.storeIdentity(identityField.value)
    ).then(() => rzr.unlock("TEST_PASSWORD").then(close));
  };

  // TODO: ability to unlock for offline view

  return (
    <Modal $size="md" $justify="stretch">
      <Stack
        $gap="lg"
        onSubmit={onSubmit}
        $justify="space-between"
        as="form"
        $expand
      >
        <Inline $align="center" $justify="space-between">
          <H2 $font="secondary">unlock your identity</H2>
          <CloseIcon onClick={close} />
        </Inline>
        <Stack $gap="lg">
          {!rzr.isIdentityAvailable && (
            <Inline $justify="center" as="label" $align="center" $gap="sm">
              <span>identity: </span>
              <Input name="identity" placeholder="0xabcde" />
            </Inline>
          )}
          <Inline $justify="center" as="label" $align="center" $gap="sm">
            <span>password: </span>
            <Input name="password" type="password" />
          </Inline>
        </Stack>
        <Inline $justify="flex-end">
          <Button isLoading={isLoading} $variant="secondary">
            unlock
          </Button>
        </Inline>
      </Stack>
    </Modal>
  );
};

export default UnlockModal;
