import { useContext } from 'react'
import RzR from '../../../contexts/RzR'
import { ABI, ModuleMetadata } from "../../../rzr";
import styled from "styled-components";
import {
  Button,
  Modal,
  Inline,
  Input,
  Textarea,
  CloseIcon,
  H2,
  H3,
  P,
  Label,
  Stack,
} from "rzr-components";

const InstallModule: React.FC<{
  onPreviousStep: () => void;
  onSuccess: () => void;
  metadata: ModuleMetadata;
  module: {
    rootFileContent: string;
    resources: Map<string, Uint8Array>;
  };
  abi: ABI;
  address: string;
}> = ({ onPreviousStep, onSuccess, metadata, module, abi, address }) => {
  const rzr = useContext(RzR)
  const onClick = () => {
    rzr.installModule(
      address,
      new TextEncoder().encode(JSON.stringify(abi)),
      module,
      metadata
    ).then(onSuccess)
  };
  return (
    <Stack $gap="lg">
      <H3>Verify module information</H3>
      <dl>
        <dt>module name</dt>
        <dd>{metadata.name}</dd>
        <dt>tags</dt>
        <dd>
          {metadata.tags.split(",").map((tag) => (
            <span key={tag}>{tag}</span>
          ))}
        </dd>
        <dt>description</dt>
        <dd>{metadata.description}</dd>
        <dt>source url</dt>
        <dd>{metadata.sourceUrl}</dd>
        <dt>address</dt>
        <dd>{address}</dd>
      </dl>
      <Inline $justify="space-between">
        <Button onClick={onPreviousStep}>Back</Button>
        <Button onClick={onClick} $variant="secondary">
          Install module with signature
        </Button>
      </Inline>
    </Stack>
  );
};

export default InstallModule;
