import constants from "../../../constants";
import {
  verifyMessage,
  toNumber,
  dataSlice,
  toBeHex,
  randomBytes,
  zeroPadValue,
  toBeArray,
  Wallet,
  hexlify,
  getBytes,
  concat,
} from "ethers";
import {
  Schema,
  Player,
  Router,
  ModuleMetadata,
  ModuleRegistrationData,
  ModuleStaticMetadata,
  AvailableModuleMetadata,
  Settings,
} from "../../../types";
import {
  DatabaseBackend,
  SerializedRouter,
  DatabaseEventMap,
  SerializedSettings,
} from "./backend";
import { LogEmitter, Log, Clock } from "../../lvl0";
import {
  digestModule,
  hasContent,
  applySchema,
  buildSchema,
  findClosestAddress,
} from "../../../utils";

import { obfuscateIdentity } from "./utils";

// must be 2 bytes max for now
const IDENTITY_DEPTH = 1024;
const IDENTITY_DEPTH_BYTE_LENGTH = 2;
const MODULE_METADATA_BYTE_LENGTH = 1024;
const DEPENDENCY_SIZE_BYTE_LENGTH = 1;

export enum DataType {
  SALE = 0,
  GAME,
  SIGNATURE,
  ROUTER,
  MODULE,
}

export type RzRDBEventMap = DatabaseEventMap & {
  newPlayerStored: Player;
  [DataType.ROUTER]: Router | null;
  [DataType.MODULE]: string | null;
};

const parseRouter = (router: SerializedRouter): Router => ({
  ...router,
  registrationSignature: router.registrationSignature
    ? new Uint8Array(router.registrationSignature)
    : undefined,
});

const serializeRouter = (router: Router): SerializedRouter => ({
  ...router,
  registrationSignature: router.registrationSignature?.buffer,
});

export const moduleMetadataSchema: Schema = {
  signature: {
    type: "hexstring",
    byteLength: constants.SIGNATURE_BYTE_LENGTH,
  },
  updatedAt: {
    type: "number",
    byteLength: constants.TIMESTAMP_BYTE_LENGTH,
  },
  name: {
    type: "string",
    byteLength: 64,
  },
  sourceUrl: {
    type: "string",
    byteLength: 128,
  },
  tags: {
    type: "string",
    byteLength: 256,
  },
  description: {
    type: "string",
    byteLength: 504,
  },
  dependencies: {
    type: "repeating",
    schema: {
      contentHash: {
        type: "hexstring",
        byteLength: constants.HASH_BYTE_LENGTH,
      },
    },
  },
};

export class RzRDB extends LogEmitter<RzRDBEventMap> {
  private loadedIdentitiesCache = new Map<string, Uint8Array>();
  private backend: DatabaseBackend;

  private modules: {
    clock: Clock;
  };

  blockHeight = 0;

  constructor(Backend: typeof DatabaseBackend, clock: Clock, log: Log) {
    super(log, "RzRDB");

    this.backend = new Backend();
    this.modules = { clock };

    this.backend.addEventListener("databaseLoaded", () =>
      this.emit("databaseLoaded", true)
    );

    this.backend.addEventListener("databaseProcessingStatusChanged", (status) =>
      this.emit("databaseProcessingStatusChanged", status)
    );
  }

  initialize() {
    return this._log("initializing database", this.backend.initialize());
  }

  loadLatestBlock() {
    return this.backend.loadLatestBlock();
  }

  reset() {
    return this._log("resetting local database", this.backend.reset());
  }

  async storeModuleRegistration(
    moduleRegistrationData: ModuleRegistrationData
  ) {
    return this._log(
      `storing module registration ${moduleRegistrationData.address}`,
      this.backend.storeModuleRegistration(moduleRegistrationData)
    );
  }

  async loadInstalledModules() {
    return this._log(
      "loading installed modules",
      this.backend.loadInstalledModules()
    );
  }

  async toggleModule(contentHash: string, isInstalled: boolean) {
    const installedModules = await this.loadInstalledModules();

    const module = await this.loadModuleMetadata(contentHash);

    if (!module) return;

    if (isInstalled) {
      const installedModulesHashes = new Set(
        installedModules.map((m) => m.contentHash)
      );
      // when turning on, need to check if all dependices are available
      for (const contentHash of module.dependencies) {
        if (!installedModulesHashes.has(contentHash))
          throw new Error("dependency violation");
      }
    } else {
      // when turning off, need to check if all dependenices are still satisfied
      for (const module of installedModules) {
        if (new Set(module.dependencies).has(contentHash))
          throw new Error("dependency violation");
      }
    }

    return this.loadModuleMetadata(contentHash).then((metadata) => {
      if (!metadata || !(metadata as ModuleMetadata)?.updatedAt)
        throw new Error("module not found");
      return this.storeModuleMetadata({
        ...(metadata as ModuleMetadata),
        isInstalled,
      });
    });
  }

  private async storeModuleMetadata(metadata: ModuleMetadata) {
    metadata.updatedAt = metadata.updatedAt ?? this.modules.clock.now;

    return this._log(
      `storing module metadata ${metadata.contentHash}`,
      this.backend.storeModuleMetadata(metadata)
    ).then(() => this.emit(DataType.MODULE, metadata.contentHash));
  }

  async storeLocalModule(address: string, moduleContent: Uint8Array) {
    const metadata = applySchema<{ signature: string } & ModuleStaticMetadata>(
      moduleMetadataSchema,
      moduleContent.slice(
        DEPENDENCY_SIZE_BYTE_LENGTH,
        MODULE_METADATA_BYTE_LENGTH
      )
    );

    metadata.contentHash = address;

    await this._log(
      `storing local module ${address}`,
      Promise.all([
        this.storeModuleMetadata({
          ...metadata,
          byteSize: moduleContent.length,
          isLocal: true,
        }),
        this.backend.storeModule(address, moduleContent.buffer),
      ])
    );

    return true;
  }

  async storeModule(moduleContent: Uint8Array): Promise<boolean> {
    const dependencyCount = toNumber(
      dataSlice(moduleContent, 0, DEPENDENCY_SIZE_BYTE_LENGTH)
    );

    const metadataEnd =
      MODULE_METADATA_BYTE_LENGTH +
      dependencyCount * constants.HASH_BYTE_LENGTH;

    const metadata = applySchema<{ signature: string } & ModuleStaticMetadata>(
      moduleMetadataSchema,
      moduleContent.slice(DEPENDENCY_SIZE_BYTE_LENGTH, metadataEnd)
    );

    // TODO: this is a problem with schemas
    metadata.dependencies = metadata.dependencies.map(
      ({ contentHash }: any) => contentHash
    );

    const contentHash = digestModule(moduleContent);

    metadata.contentHash = contentHash;

    await this._log(
      `storing module ${contentHash}`,
      Promise.all([
        this.storeModuleMetadata({
          ...metadata,
          byteSize: moduleContent.length,
          isLocal: !hasContent(moduleContent),
        }),
        this.backend.storeModule(contentHash, moduleContent.buffer),
      ])
        .then(() => this.loadModuleRegistration(contentHash))
        .then((moduleRegistration) => {
          if (moduleRegistration) {
            return this.backend.storeModuleRegistration(
              moduleRegistration,
              true
            );
          }
        })
    );

    this.emit(DataType.MODULE, contentHash);

    return true;
  }

  async loadModule(contentHash: string): Promise<Uint8Array | null> {
    const module = await this.backend.loadModule(contentHash);

    if (!module) return null;

    return new Uint8Array(module);
  }

  async loadModuleMetadata(
    contentHash: string
  ): Promise<ModuleMetadata | null> {
    const metadata = await this.backend.loadModuleMetadata(contentHash);

    if (!metadata) return null;

    return metadata;
  }

  async loadModuleRegistration(
    contentHash: string
  ): Promise<ModuleRegistrationData | null> {
    const registrationData = await this.backend.loadModuleRegistration(
      contentHash
    );

    if (!registrationData) return null;

    return registrationData;
  }

  async searchModules(search: string) {
    return this.backend.searchModules(search);
  }

  // this function is also being used during discovery!
  // so it means, that during discovery, always only the latest versions are being distributed
  // consider loading registrations with metadata in chunks
  async loadModules(): Promise<ModuleMetadata[]> {
    return Promise.all([
      this.backend.loadInstalledModules(),
      this.backend.loadMostRecentModules(),
      this.backend.loadLocalModules(),
    ]).then(([installedModules, productionModules, localModules]) => {
      const results = new Map<string, ModuleMetadata>();

      for (const i of [
        ...installedModules,
        ...productionModules.filter((r) => r !== null),
        ...localModules,
      ] as ModuleMetadata[]) {
        results.set(i.contentHash, i);
      }

      return Array.from(results.values());
    });
  }

  async storeLatestBlock(blockNumber: number): Promise<number> {
    this.blockHeight = blockNumber;
    await this._log(
      `upgrading local latest block to ${blockNumber}`,
      Promise.all([
        this.backend.storeLatestBlock(blockNumber),
        this.backend.purgeExpiredEvents(blockNumber),
        this.purgeExpiredPlayers(blockNumber - 1000000),
      ])
    );
    return blockNumber;
  }

  async storeRouter(router: Router) {
    return this._log(
      "storing router",
      this.backend.storeRouter(serializeRouter(router))
    ).then(() => {
      this.emit(DataType.ROUTER, router);
      return true;
    });
  }

  async flagRouter(address: string): Promise<boolean> {
    return this._log(
      `flagging router ${address} as broken`,
      this.loadRouter(address).then((router) =>
        router === null
          ? true
          : this.storeRouter({
              ...router,
              downSince: this.modules.clock.now,
            })
      )
    );
  }

  async removeRouter(address: string) {
    return this._log(
      "removing router",
      this.backend.removeRouter(address)
    ).then(() => this.emit(DataType.ROUTER, null));
  }

  async loadRouters(): Promise<Router[]> {
    return this._log(
      "loading routers",
      this.backend.loadRouters().then((routers) => routers.map(parseRouter))
    );
  }

  async loadMissingModules(n: number): Promise<string[]> {
    return this._log(
      "loading missing modules",
      this.backend.loadMissingModuleHashes(n)
    );
  }

  async loadNextFlaggedRouter(): Promise<Router | null> {
    const slot = this.log("load next flagged router...");
    const router = (
      await this.backend
        .loadRouters()
        .then((routers) =>
          routers
            .filter((r) => r.downSince)
            .sort(
              (r1, r2) => (r1.downSince as number) - (r2.downSince as number)
            )
        )
    )[0];

    this.log("done", slot);

    if (!router) return null;

    return parseRouter(router);
  }

  async loadRouter(address: string): Promise<Router | null> {
    const slot = this.log(`loading router ${address}...`);

    const router = await this.backend.loadRouter(address);

    if (!router) {
      this.log("failed", slot);
      return null;
    }

    this.log("done", slot);

    return parseRouter(router);
  }

  async loadProtectedIdentity(address: string) {
    const result = await this.backend.loadIdentity(address);

    if (!result) return null;
    return new Uint8Array(result);
  }

  async loadIdentity(
    address: string,
    password: string
  ): Promise<Wallet | null> {
    const identity = await this.backend.loadIdentity(address);

    if (!identity) {
      return null;
    }

    return new Wallet(
      hexlify(obfuscateIdentity(password, new Uint8Array(identity)))
    );
  }

  async storeIdentity(
    address: string,
    identity: Uint8Array,
    password?: string // this should be just the hex string
  ): Promise<Wallet> {
    const slot = this.log("storing new identity...");

    const wallet = new Wallet(hexlify(identity));

    await this.backend.storeIdentity(
      address,
      (password
        ? obfuscateIdentity(password, new Uint8Array(identity))
        : identity
      ).buffer
    );

    this.log("done", slot);

    // in case no password was provided can't unlock
    return password ? wallet : (null as any);
  }

  async storeEvent(blockNumber: number, transactionHash: string) {
    return this._log(
      "storing processed event",
      this.backend.storeEvent(blockNumber, transactionHash)
    );
  }

  async hasEventBeenProcessed(transactionHash: string) {
    return this.backend.hasEventBeenProcessed(transactionHash);
  }

  async storePlayer(player: Player) {
    /*
    const existingPlayer = await this.loadPlayer(player.address);

    if (existingPlayer && existingPlayer.identity !== player.identity) {
      // TODO: right, so we would need to expire all entities related to the user, well not anymore, this was only for sales
      // but we can for sure verify whether the identity actually matches the hash, not really necessary, we can do it when, interacting with the addresus
    }*/

    await this._log("storing new player", this.backend.storePlayer(player));

    this.emit("newPlayerStored", player);

    return player;
  }

  loadModuleVersions(contentHash: string): Promise<ModuleRegistrationData[]> {
    return this.backend.loadModuleVersions(contentHash);
  }

  async loadPlayer(address: string) {
    const slot = this.log(`loading player ${address}...`);

    const player = await this.backend.loadPlayer(address);

    if (!player) {
      this.log("failed", slot);
      return null;
    }

    this.log("done", slot);

    return player;
  }

  async loadPlayerByIdentity(identity: string) {
    const slot = this.log(`loading player ${identity}...`);

    const player = await this.backend.loadPlayerByIdentity(identity);

    if (!player) {
      this.log("failed", slot);
      return null;
    }

    this.log("done", slot);

    return player;
  }

  purgeExpiredPlayers(blockLimit: number) {
    return this._log(
      "purging expired players",
      this.backend.purgeExpiredPlayers(blockLimit)
    );
  }

  storeSettings(address: string, settings: Settings) {
    return this._log(
      "storing settings",
      this.backend.storeSettings(address, {
        ...settings.ui,
        ...settings.connectivity,
        ...settings.developers,
      })
    );
  }

  loadSettings(address: string): Promise<null | Settings> {
    return this._log(
      "loading settings",
      this.backend.loadSettings(address).then((rawSettings) =>
        rawSettings
          ? {
              connectivity: {
                peerTimeout: rawSettings.peerTimeout,
                syncFrequency: rawSettings.syncFrequency,
                maxPeerConnections: rawSettings.maxPeerConnections,
                discoveryLevel: rawSettings.discoveryLevel,
              },
              ui: {
                displayLog: rawSettings.displayLog,
                darkModeStatus: rawSettings.darkModeStatus,
                animationsEnabled: rawSettings.animationsEnabled,
                activityStatus: rawSettings.activityStatus,
              },
              developers: {
                allowNonVerifiedApps: rawSettings.allowNonVerifiedApps,
              },
            }
          : null
      )
    );
  }

  async loadClosestRouters(
    addressToCompare: string,
    connectivityLevel = 2
  ): Promise<Set<Router> | null> {
    const slot = this.log("loading router configuration...");

    const routers = (await this.loadRouters()).filter((r) => !r.downSince);

    if (routers.length === 0) {
      this.log("no router found", slot);
      return null;
    }

    const selectedRouters: Router[] = [];

    // TODO: add proximity based on time
    // every 15 minutes a new hash should be created
    // it might cause outage for a minute or two

    for (let i = 0; i < connectivityLevel; i++) {
      selectedRouters.push(
        findClosestAddress<Router>(addressToCompare, routers, selectedRouters)
      );
    }

    this.log("done", slot);

    return new Set(selectedRouters);
  }

  async countInstalledModules(): Promise<number> {
    return this._log(
      "counting installed modules",
      this.backend.countInstalledModules()
    );
  }

  async loadRandomPlayer(
    startBlockNumber: number,
    advance: number
  ): Promise<Player | null> {
    return this._log(
      "loading random player for sync",
      this.backend
        .loadIdentities()
        .then((exceptions) =>
          this.backend.loadRandomPlayer(
            startBlockNumber,
            advance,
            new Set(exceptions)
          )
        )
    );
  }
}
