import { Page, Button, useModal, Inline, Input, Stack } from "rzr-components";
import styled from "styled-components";
import { useState, useEffect, useContext } from "react";
import { ModuleMetadata, DataType } from "../../rzr";
import ModuleInstaller from "../ModuleInstaller";
import Module from "./Module";
import ModuleDetails from "./ModuleDetails";
import RzR from "../../contexts/RzR";

const Modules = styled(Inline)`
  margin-left: -1rem;
  padding: 0;
`;

const ModuleList = () => {
  const rzr = useContext(RzR);
  const [modules, setModules] = useState<ModuleMetadata[]>([]);
  const [activeModule, setActiveModule] = useState("");
  const [searchValue, setSearchValue] = useState("");
  const [isInstallingModule, openInstallModule, closeInstallModule] =
    useModal();

  const openModule = (moduleName: string) => {
    rzr.loadModule(moduleName).then((module) => {
      if (!module) return;
    });
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();

    setSearchValue((e.target as HTMLFormElement).search.value.trim());
  };

  useEffect(() => {
    const onModulesChanged = () => {
      rzr.loadModules(searchValue).then(setModules);
    };

    rzr.addEventListener(DataType.MODULE, onModulesChanged);

    onModulesChanged();

    return () => {
      rzr.removeEventListener(DataType.MODULE, onModulesChanged);
    };
  }, [searchValue]);

  return (
    <Page
      title="applications"
      cta={
        rzr.isProviderReadonly ? null : (
          <Button $variant="secondary" onClick={openInstallModule}>
            install app
          </Button>
        )
      }
    >
      {activeModule && (
        <ModuleDetails
          contentHash={activeModule}
          close={() => setActiveModule('')}
        />
      )}
      <Stack $gap="lg">
        <Inline
          onSubmit={handleSearch}
          as="form"
          $gap="lg"
          $margin="md"
          $align="center"
          $justify="center"
        >
          <Input placeholder="tags or module name" name="search" />
          <Button $size="sm">search</Button>
        </Inline>
        {isInstallingModule && <ModuleInstaller close={closeInstallModule} />}
      </Stack>

      <Modules as="ul" $gap="md" $align="flex-start" $wrap>
        {modules.map((m) => (
          <Module
            setActiveModule={setActiveModule}
            key={m.contentHash}
            module={m}
          />
        ))}
      </Modules>
    </Page>
  );
};

export default ModuleList;
