const protectedModeTheme = {
  ...darkTheme,
  colors: {
    ...darkTheme.colors,
    primary: "#ef5312",
  },
};

const App = () => {
  const rzr = useContext(RzRContext);
  const rzrContext = useRzR();
  // let's use dark mode on/auto
  const [activeTheme, setTheme] = useState<Theme>({
    ...(rzr.settings.ui.darkModeStatus ? darkTheme : lightTheme),
    animationSpeed: rzr.settings.ui.animationsEnabled ? 1 : 0,
  });
  // the above can be set from module metadata eventually
  // almost like manifest

  const [moduleWrappers, setModuleWrappers] = useState<Element[]>([]);
  const [layoutIndex, setLayoutIndex] = useState(0);

  useEffect(() => {
    const targetNode = document.getElementById("modules") as HTMLDivElement;

    const updateLayout = () => setLayoutIndex(targetNode.children.length);

    const observer = new MutationObserver((mutationList: MutationRecord[]) => {
      for (const mutation of mutationList) {
        if (mutation.type === "childList") {
          updateLayout();
        }
      }
    });

    const reloadSettings = () => {
      setTheme({
        ...(rzr.settings.ui.darkModeStatus ? darkTheme : lightTheme),
        animationSpeed: rzr.settings.ui.animationsEnabled ? 1 : 0,
      });
    };

    const updateThemeIfUIChanged = (settingsCluster: string) => {
      if (settingsCluster === "ui") reloadSettings();
    };

    const setProtectedMode = (isProtectedMode: boolean) => {
      if (isProtectedMode) setTheme(protectedModeTheme);
      else reloadSettings();
    };

    updateLayout();

    rzr.addEventListener("settingsUpdated", updateThemeIfUIChanged);
    rzr.addEventListener("protectedModeChange", setProtectedMode);

    observer.observe(targetNode, { childList: true });

    return () => {
      observer.disconnect();
      rzr.removeEventListener("protectedModeChange", setProtectedMode);
      rzr.removeEventListener("settingsUpdated", updateThemeIfUIChanged);
    };
  }, [activeTheme]);

  const [expandedSection, setExpandedSection] = useState<Section | null>(null);

  // when asking for identity, show desktop layout should be activated

  return (
    <ThemeProvider theme={activeTheme}>
      {/*<Sparkles />*/}
      <GlobalStyles
        activeSection={expandedSection}
        selectedLayout={LAYOUTS[layoutIndex]}
        logStatus={rzr.settings.ui.displayLog ? "on" : "blurred"}
        pulseIntensity={rzr.isIdentityAvailable ? 1 : 0}
      />
      {rzr.settings.ui.displayLog ? <Log /> : null}
      {expandedSection === Section.APPLICATIONS ? (
        <ModuleList />
      ) : expandedSection === Section.SETTINGS ? (
        <Settings />
      ) : expandedSection === Section.NETWORK ? (
        <Network />
      ) : null}
      <ControlPanel
        className={expandedSection === null ? "expanded" : "collapsed"}
      >
        <Button onClick={() => setExpandedSection(Section.APPLICATIONS)}>
          applications
        </Button>
        <Button onClick={() => setExpandedSection(Section.SETTINGS)}>
          settings
        </Button>
        <Button onClick={() => setExpandedSection(Section.NETWORK)}>
          network
        </Button>
      </ControlPanel>
      <StatusBar
        moduleWrappers={moduleWrappers}
        setModuleWrappers={setModuleWrappers}
        isDisabled={layoutIndex === 0}
      />
      {expandedSection !== null ? (
        <Button
          className="back-button"
          onClick={() => setExpandedSection(null)}
        >
          back
        </Button>
      ) : null}
      <Notifications />
    </ThemeProvider>
  );
};

export default App;
