import EventEmitter, { Listener } from "../../../EventEmitter";
import { Log, LogMessage, LogPromise } from "./Log";

export default class LogEmitter<T> extends EventEmitter<T> {
  protected log: LogMessage;
  protected _log: LogPromise;

  constructor(log: Log, moduleName: string) {
    super();
    this.log = (message: string, slot?: number) =>
      log.log(message, moduleName, slot);
    this._log = log._log(moduleName);
  }
}
