import { Signer } from "ethers";
import { SignatureDetails } from "../../../types";

import EventEmitter, { Listener } from "../../../EventEmitter";

type Provider = any;

export type AccountChangeEvent = {
  signer: Signer;
  provider: Provider;
  // chainId: string;
};

export type EthereumProviderEventMap = {
  connectionStatusChanged: boolean;
  accountChanged: AccountChangeEvent;
};

export class EthereumProvider extends EventEmitter<EthereumProviderEventMap> {
  provider: Provider;
  signer: Signer | null = null;
  isReadonly?: boolean;

  listeners: {
    [K in keyof EthereumProviderEventMap]: Set<
      Listener<EthereumProviderEventMap[K]>
    >;
  } = {
    connectionStatusChanged: new Set<Listener<boolean>>(),
    accountChanged: new Set<Listener<AccountChangeEvent>>(),
  };

  initialize() {}

  async sign(
    signatureData: Uint8Array,
  ): Promise<string> { return Promise.resolve('')}
}
