import {
  TransactionDetails,
  TransactionReceipt,
  ContractConfig,
  SignatureDetails,
  Player,
} from "../../../types";
import {
  randomBytes,
  sha256,
  Transaction,
  Contract,
  ContractFactory,
  toBeHex,
  Signer,
  Wallet,
  Provider,
  EventLog,
} from "ethers";
import { watch, formatAddress } from "../../../utils";
import { Log, LogEmitter, Clock } from "../../lvl0";
import { RzRDB } from "../../lvl1";
import { EthereumProvider, AccountChangeEvent } from "./provider";

export enum Transactions {
  NEW_IDENTITY = "newIdentity",
  REGISTER_APPLICATION = "registerApplication",
}

export enum Events {
  NEW_IDENTITY = "NewIdentity",
  APPLICATION_REGISTRATION = "ApplicationRegistration",
}

export type NewIdentityEvent = {
  account: string;
  identity: string;
  previousIdentity: string;
  event: EventLog;
};

export type ApplicationRegistrationEvent = {
  address: string;
  owner: string;
  applicationHash: string;
  event: EventLog;
};

export type EthereumEventMap = {
  pendingTransactionChanged: TransactionDetails | null;
  accountChanged: string;
  chainChanged: string;
  activity: boolean;
  newSignature: SignatureDetails | null;
  identityLoaded: string;

  [Transactions.REGISTER_APPLICATION]: TransactionReceipt;
  [Transactions.NEW_IDENTITY]: TransactionReceipt;
  [Events.NEW_IDENTITY]: NewIdentityEvent;
  [Events.APPLICATION_REGISTRATION]: ApplicationRegistrationEvent;
};

type Providers = "metamask" | "local";

/*
 * This module should no longer be referred to as "Ethereum"
 * the idea is that a new blockchain is going to be created using identities
 * each identity is revealed after a new one is generated
 * by doing this, we can always safely validate any identity
 * the blockchain is indexed by identities
 * each identity has its own "blockchain"
 * but only the hash is stored for most, the owners store the entire chain whose merkle tree root = identity hash
 * open identity chains can be stored everywhere, but once they are closed (either at the 30 day or when a new one is created) the hash is calculcated and stored
 *
 * baiscally for each game 32 bytes are stored
 * 1M games = 32MB
 *
 * can further simplify as identity chains can be vertically compressed
 *
 * b b b b b b = c
 *
 * c
 * |
 * c
 * |
 * c
 *
 * horizontal and vertical compression
 *
 * it's more like a blockmatrix than a blockchain
 * */
export class Ethereum extends LogEmitter<EthereumEventMap> {
  address: string = "";
  identity: string | null = null;
  isIdentityAvailable = false;
  private chainId: string = "";
  private pendingSignature: SignatureDetails | null = null;
  private backend: EthereumProvider;
  private modules: {
    rzrDB: RzRDB;
    clock: Clock;
  };
  private contractDefinition: ContractConfig;
  private contract: Contract | null = null;
  private pendingTransaction: null | TransactionDetails = null;

  get isReadonly() {
    return this.backend.isReadonly;
  }

  get account() {
    return this.backend.signer;
  }

  get provider() {
    return this.backend.provider;
  }

  constructor(
    clock: Clock,
    log: Log,
    rzrDB: RzRDB,
    EthereumBackend: typeof EthereumProvider,
    contractDefinition: ContractConfig
  ) {
    super(log, "Ethereum");

    this.contractDefinition = contractDefinition;
    this.backend = new EthereumBackend();
    this.modules = { rzrDB, clock };

    this.attachListeners();
  }

  initialize = () => {
    // technically, we could load and pass in first identity if all else fails
    // waait, this is actually literally just about the address...
    // so we just need to store in the readonly provider the ethereum private key, it is *not* the identity
    this.backend.initialize();
  };

  private setPendingTransaction(tx: TransactionDetails | null) {
    this.pendingTransaction = tx;
    this.emit("pendingTransactionChanged", tx);
  }

  private attachListeners = async () => {
    this.backend.addEventListener("accountChanged", this.onAccountChanged);
    this.backend.addEventListener(
      "connectionStatusChanged",
      this.onConnectionChange
    );

    this.modules.rzrDB.addEventListener("newPlayerStored", (player: Player) => {
      if (player.address === this.address) this.reloadIdentity();
    });
  };

  private onNewIdentity = (
    account: string,
    identity: string,
    previousIdentity: string,
    event: EventLog
  ) => {
    this.emit(Events.NEW_IDENTITY, {
      account: account.toLowerCase(),
      identity: identity.toLowerCase(),
      previousIdentity,
      event,
    });
  };

  private onApplicationRegistration = (
    address: string,
    owner: string,
    applicationHash: string,
    event: EventLog
  ) => {
    this.emit(Events.APPLICATION_REGISTRATION, {
      address: address.toLowerCase(),
      owner: owner.toLowerCase(),
      applicationHash,
      event,
    });
  };

  getContract() {
    if (!this.contract) {
      this.setPendingTransaction(null);
      throw new Error("contract not ready");
    }
    return this.contract;
  }

  private setContract = (signer: Signer) => {
    if (this.contract) {
      this.contract.off(Events.NEW_IDENTITY, this.onNewIdentity);
      this.contract.off(
        Events.APPLICATION_REGISTRATION,
        this.onApplicationRegistration
      );
    }

    this.contract = new Contract(
      this.contractDefinition.address,
      this.contractDefinition.abi,
      signer
    );

    if (signer.provider === null) return;

    this.contract.on(Events.NEW_IDENTITY, this.onNewIdentity);
    this.contract.on(
      Events.APPLICATION_REGISTRATION,
      this.onApplicationRegistration
    );

    signer.getAddress().then((address) => {
      this.emit("accountChanged", address);
    });
  };

  deployContract(bytecode: string, abi: any, params: any[]) {
    if (!this.account) throw new Error("account not initialized");

    const factory = new ContractFactory(abi, bytecode, this.account);

    return factory.deploy(...params);
  }

  reloadIdentity = async () => {
    const [player, identity] = await Promise.all([
      this.modules.rzrDB.loadPlayer(this.address),
      this.modules.rzrDB.loadProtectedIdentity(this.address),
    ]);

    if (!player) return;

    this.identity = player.identity;

    this.isIdentityAvailable = identity !== null;

    this.emit("identityLoaded", this.identity);
  };

  getNewIdentityEvents(start?: number, end?: number): Promise<EventLog[]> {
    return this.getContract().queryFilter(
      Events.NEW_IDENTITY,
      start,
      end
    ) as Promise<EventLog[]>;
  }

  getApplicationRegistrationEvents(
    start?: number,
    end?: number
  ): Promise<EventLog[]> {
    return this.getContract().queryFilter(
      Events.APPLICATION_REGISTRATION,
      start,
      end
    ) as Promise<EventLog[]>;
  }

  getLatestApplicationRegistration(address: string) {
    const contract = this.getContract();
    const filter = contract.filters.ApplicationRegistration(address);
    return contract.queryFilter(filter).then((results) => {
      if (results.length === 0) return null;

      const [applicationHash, address, owner] =
        results[results.length - 1].topics;

      return { applicationHash, owner: toBeHex(BigInt(owner)) };
    });
  }

  private onAccountChanged = async ({
    signer,
    provider,
  }: AccountChangeEvent) => {
    // if (this.address) this.resetLog()logger.resetLog();
    const address = await signer.getAddress();
    const hasChanged = address.toLowerCase() !== this.address;
    this.address = address.toLowerCase();
    await this.reloadIdentity();
    if (hasChanged) {
      this.setContract(signer);
    }
    this.log("application authorized");
  };

  private onConnectionChange = (isConnected: boolean) => {
    if (isConnected) this.log("connected to ethereum");
    else this.log("disconnected from ethereum");
  };

  getTransaction(hash: string): Promise<Transaction> {
    return this.provider.getTransaction(hash);
  }

  sign = async (
    signatureData: Uint8Array,
    signatureDetails: SignatureDetails
  ): Promise<string> => {
    const slot = this.log("requesting signature...");

    try {
      this.emit("newSignature", signatureDetails);
      const signature = await this.backend.sign(signatureData);
      this.log("done", slot);
      return signature;
    } catch (e) {
      this.log("failed", slot);
      throw e;
    } finally {
      this.pendingSignature = null;
      this.emit("newSignature", null);
    }
  };

  createContract(contractAddress: string, contractAbi: any) {
    if (!this.account) throw new Error("ethereum not initialized");

    const contract = new Contract(contractAddress, contractAbi, this.account);

    return contract;
  }

  private transact(
    method: Transactions,
    params: any[],
    transactionDetails: TransactionDetails
  ) {
    this.log(`initiating transaction`);
    let slot = this.log(`checking requirements...`);
    let txSlot = -1;

    this.log(`done`, slot);
    slot = this.log(`waiting for approval...`);
    this.setPendingTransaction(transactionDetails);

    return this.getContract()
      [method](...params)
      .then((tx: Transaction) => {
        this.log("done", slot);
        this.setPendingTransaction(null);
        this.emit("activity", true);

        txSlot = this.log("waiting for transaction completion...");
        return watch<TransactionReceipt>(async () => {
          const receipt = await this.provider.getTransactionReceipt(
            tx.hash as string
          );

          if (receipt?.blockNumber) return receipt;

          return null;
        }, 1000);
      })
      .then((tx: TransactionReceipt) => {
        this.log(`done (block number: ${tx.blockNumber})`, txSlot);
        this.emit(method, tx);
        return tx;
      })
      .catch((e: any) => {
        this.setPendingTransaction(null);
        this.log("failed", slot);
        throw e;
      })
      .finally(() => {
        this.emit("activity", false);
      });
  }

  registerApplication(address: string, applicationHash: string) {
    return this.transact(
      Transactions.REGISTER_APPLICATION,
      [address, applicationHash, this.address],
      {
        name: "Application registration",
        params: [
          {
            name: "address",
            value: formatAddress(address, 5),
          },
          {
            name: "applicationHash",
            value: formatAddress(applicationHash, 5),
          },
          {
            name: "owner",
            value: this.address,
          },
        ],
      }
    );
  }

  newIdentity() {
    return this.modules.rzrDB
      .storeIdentity(this.address, randomBytes(32), "TEST_PASSWORD")
      .then((identity) =>
        this.transact(
          Transactions.NEW_IDENTITY,
          [identity.address, sha256(identity.privateKey)],
          {
            name: "New Identity",
            params: [
              {
                name: "identity",
                value: formatAddress(identity.address, 5),
              },
            ],
          }
        )
      );
  }
}
