import {
  getBytes,
  verifyMessage,
  zeroPadValue,
  concat,
  Wallet,
  toBeArray,
} from "ethers";
import { Log, LogEmitter, Clock } from "../../lvl0";
import { RzRDB } from "../../lvl1";
import { Ethereum } from "../../lvl2";
import { Schema, AuthorizationSignature } from "../../../types";
import { AuthenticationBackend } from "./backend";
import constants from "../../../constants";

export const authorizationSchema: Schema = {
  timestamp: {
    type: "number",
    byteLength: constants.TIMESTAMP_BYTE_LENGTH,
  },
  signature: {
    type: "hexstring",
    byteLength: constants.SIGNATURE_BYTE_LENGTH,
  },
};

export type AuthenticationEventTypes = {};

export class Authentication extends LogEmitter<AuthenticationEventTypes> {
  private backend: AuthenticationBackend;

  isAuthorized = false;

  private modules: {
    clock: Clock;
    rzrDB: RzRDB;
    ethereum: Ethereum;
  };

  constructor(
    clock: Clock,
    log: Log,
    rzrDB: RzRDB,
    ethereum: Ethereum,
    Backend: typeof AuthenticationBackend
  ) {
    super(log, "Auth");

    this.modules = { clock, rzrDB, ethereum };

    this.backend = new Backend();
  }

  initialize(identity?: Wallet): Promise<boolean> {
    if (!identity) {
      // in case it prefers to stay offline

      this.isAuthorized = true
      return Promise.resolve(false)
    }

    return this.backend
      .initialize(getBytes(identity.privateKey))
      .then((success) => {
        if (!success)
          throw new Error("initializing authorization module failed");
        this.isAuthorized = success;

        return success;
      });
  }

  verifyAuthorization(address: string, authorization: AuthorizationSignature) {
    // currently supporting 15 second authorization, could be a setting based on network conditions
    if (
      !this.modules.ethereum.identity ||
      authorization.timestamp < this.modules.clock.now - 15
    )
      return false;

    return (
      verifyMessage(
        getBytes(
          concat([
            zeroPadValue(toBeArray(authorization.timestamp), 6),
            this.modules.ethereum.identity,
          ])
        ),
        authorization.signature
      ).toLowerCase() === address.toLowerCase()
    );
  }

  authorize(identity: string): Promise<AuthorizationSignature> {
    return this.backend.authorize(identity);
  }
}
