import { useState } from "react";
import styled from "styled-components";
import {
  Button,
  Modal,
  Inline,
  Input,
  Textarea,
  CloseIcon,
  Stack,
  H2,
  H3,
  P,
  Label,
  useModal,
} from "rzr-components";
import { ABI, Interaction } from "../../../rzr";

const AbiInput = styled(Textarea)`
  width: 100%;
  display: block;
  min-height: 16em;
  resize: none;
`;

const SubmitButton = styled(Button)`
  align-self: flex-end;
`;

type FormElement = HTMLFormElement & {
  elements: {
    abi: HTMLInputElement;
  };
};

const ABIStep: React.FC<{
  onPreviousStep: () => void;
  onSuccess: (abi: ABI) => void;
  abi: ABI | null;
}> = ({ onSuccess, onPreviousStep, abi }) => {
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const {
      abi: { value },
    } = e.target as FormElement;

    try {
      const abi = JSON.parse(value);

      /*
      if (
        !abi.find(
          (interaction: Interaction) =>
            interaction.type === "event" &&
            interaction.name === "ApplicationRegistration" &&
            interaction.inputs[0].type === "bytes32"
        )
      )
      throw new Error("missing ApplicationRegistration");

      */

      onSuccess(abi);
    } catch (e) {
      console.log(e);
    }
  };

  // use tabs for creating new
  return (
    <Stack onSubmit={handleSubmit} as="form" $gap="md">
      <H3>Contract ABI</H3>
      <P>
        You can find it in your compiled contract's json definition, under the
        "abi" field.
      </P>
      <AbiInput
        placeholder={`[
    {
        "inputs": {
          type: "bytes32"
          name: "applicationHash",
        },
        "type": "event",
        "name": "ApplicationRegistration"
    },
    {...}
]`}
        name="abi"
        defaultValue={abi ? JSON.stringify(abi, null, 4) : ""}
      />
      <P $size="sm">
        Please note that in order for your contract to be RzR compatible, it
        needs to emit an <strong>ApplicationRegistation</strong> event in the
        constructor.
      </P>
      <Inline $justify="space-between">
        <Button type="button" onClick={onPreviousStep}>
          Back
        </Button>
        <Inline $gap="lg">
          <Button type="button" onClick={() => onSuccess([])} $variant="accent">Skip</Button>
          <Button type="submit" $variant="secondary">Next</Button>
        </Inline>
      </Inline>
    </Stack>
  );
};

export default ABIStep;
