import {
  Protocol,
  SignalServerConnection,
  Ethereum,
  SignalMessageType,
  CandidateSchema,
  utils as rzrUtils,
} from "../../../";

import { hexlify, sha256, getBytes, concat } from "ethers";

const { ec, dc } = rzrUtils;

const MAX_MESSAGE_LENGTH = 16384;

// for node connections
// technically rzrSocket still exists
// rzrSocket is just a special websocket rzrPeer that manages connections
// they will eventually become a RzRPeer

export default class WebRTCBackend extends Protocol {
  protocolId = 0b10;
  connection?: RTCPeerConnection;
  dataChannel?: RTCDataChannel;

  receivedFragments = new Map<string, Uint8Array[]>();

  constructor(router: SignalServerConnection, identity: string) {
    super(router, identity);

    router.addEventListener('candidate', this.onCandidate);
  }

  private onCandidate = ({ identity, encodedCandidate }: CandidateSchema) => {
    if (identity !== this.identity) return;
    const connection = this.getConnection();

    new Promise<boolean>((r) => {
      const checkForAnswer = () => {
        if (!this.connection) return r(false);

        if (this.connection.remoteDescription) return r(true);

        setTimeout(checkForAnswer, 500);
      };

      checkForAnswer();
    }).then((hasConnection: boolean) => {
      if (!hasConnection) return;
      try {
        const candidate = JSON.parse(dc.decode(encodedCandidate));
        connection.addIceCandidate(candidate);
      } catch (e) {
        console.log(e);
      }
    });
  };

  private getConnection() {
    if (!this.connection) throw new Error("no connection");

    return this.connection;
  }

  closeConnection() {
    if (this.connection) this.connection.close();
  }

  private createConnection() {
    this.connection = new RTCPeerConnection();
    this.dataChannel = this.connection.createDataChannel("channel1", {
      negotiated: true,
      id: 0,
    });
    this.dataChannel.binaryType = "arraybuffer";
    this.dataChannel.onopen = () => {
      this.isConnected = true;
      this.isConnecting = false;
      this.emit("connectionStatusChanged", true);
    };

    this.dataChannel.onclose = this.reset.bind(this);
    this.connection.oniceconnectionstatechange = () => {
      // this may be delayed need to know what happens if user reconnects in the meantime
      if (
        !this.connection ||
        this.connection.iceConnectionState === "disconnected"
      ) {
        this.reset();
      }
    };

    this.dataChannel.onmessage = ({ data }: { data: ArrayBuffer }) => {
      const _data = new Uint8Array(data);
      const hex = hexlify(_data);
      const fragmentHashes = Array.from(this.receivedFragments.keys());

      if (
        data.byteLength < MAX_MESSAGE_LENGTH &&
        !fragmentHashes.find((hash) => hex.includes(hash.slice(2)))
      )
        return this.emit("newMessage", data);

      const messageId = hexlify(_data.slice(0, 32));
      const currentMessageIndex = _data.slice(32, 33)[0];
      const maxMessageCount = _data.slice(33, 34)[0];
      const messageData = _data.slice(34);

      const fragments = this.receivedFragments.get(messageId) || [];

      fragments[currentMessageIndex] = messageData;

      this.receivedFragments.set(messageId, fragments);

      const message = concat(fragments);

      if (sha256(message) === messageId) {
        this.emit("newMessage", getBytes(concat(fragments)).buffer);
        this.receivedFragments.delete(messageId);
      }
    };

    return this.connection;
  }

  private gatherCandidates() {
    this.getConnection().onicecandidate = (event) => {
      if (event.candidate) {
        this.router.sendCandidate({
          identity: this.identity,
          encodedCandidate: ec.encode(JSON.stringify(event.candidate)),
        });
      } else {
      }
    };
  }

  processOffer = async (offer: Uint8Array): Promise<Uint8Array> => {
    const connection = this.createConnection();

    connection.setRemoteDescription({
      type: "offer",
      sdp: dc.decode(offer),
    });

    const answer = await connection.createAnswer();

    connection.setLocalDescription(answer);

    this.gatherCandidates();

    return ec.encode(answer.sdp);
  };

  processAnswer = async (answer: Uint8Array) => {
    if (!this.connection) throw new Error("no connection");

    this.connection.setRemoteDescription({
      type: "answer",
      sdp: dc.decode(answer),
    });

    this.gatherCandidates();
  };

  async createOffer(): Promise<Uint8Array> {
    if (this.connection) throw new Error("connection in progress");

    const connection = this.createConnection();

    const offer = await connection.createOffer();

    connection.setLocalDescription(offer);

    return ec.encode(offer.sdp);
  }

  reset() {
    if (this.dataChannel) this.dataChannel.close();
    if (this.connection) {
      this.connection.close();
      this.connection.oniceconnectionstatechange = null;
    }
    this.connection = undefined;

    this.router.removeEventListener(
      'candidate',
      this.onCandidate
    );

    if (this.dataChannel) {
      this.dataChannel.onclose = null;
      this.dataChannel.onmessage = null;
      this.dataChannel.onopen = null;
    }

    super.reset();
  }

  private fragmentMessage(message: ArrayBuffer): ArrayBuffer[] {
    const messageCount = Math.ceil(message.byteLength / MAX_MESSAGE_LENGTH);
    const messageId = sha256(new Uint8Array(message));
    const messages: ArrayBuffer[] = [];

    for (let i = 0; i < messageCount; ++i) {
      messages.push(
        getBytes(
          concat([
            messageId,
            new Uint8Array([i, messageCount]),
            new Uint8Array(
              message.slice(
                i * MAX_MESSAGE_LENGTH,
                (i + 1) * MAX_MESSAGE_LENGTH
              )
            ),
          ])
        ).buffer
      );
    }

    return messages;
  }

  send(message: ArrayBuffer) {
    if (!this.dataChannel) throw new Error("data channel not ready");

    // if message is larger than 16K need to do fragmentation
    // fragmentation is done the following way
    // if packet is longer than 16k
    // it will get a 4 byte signature (almost last 4 bytes of each packet)
    // the actual last 2 bytes are -> max byte length, current byte sequence
    if (message.byteLength < MAX_MESSAGE_LENGTH) {
      return this.dataChannel.send(message);
    }

    for (const m of this.fragmentMessage(message)) {
      this.dataChannel.send(m);
    }
  }
}
