import EventEmitter, { Listener } from "../../../EventEmitter";

export type LogEventMap = {
  logs: string[];
};

export type LogPromise = <T>(
  message: string,
  promise: Promise<T>,
  options?: {
    successMessage: string | ((result: T) => string);
    failMessage: string;
  }
) => Promise<T>;

export type LogMessage = (message: string, slot?: number) => number;

export class Log extends EventEmitter<LogEventMap> {
  private lastEmitAt = 0;
  private messages: string[] = [];

  _log(context: string) {
    return <T>(
      message: string,
      p: Promise<T>,
      {
        successMessage,
        failMessage,
      }: {
        successMessage: string | ((result: T) => string);
        failMessage: string;
      } = {
        successMessage: "done",
        failMessage: "failed",
      }
    ): Promise<T> => {
      const slot = this.log(`${message}...`, context);

      return Promise.all([p, new Promise((r) => setTimeout(r, 500))])
        .then(([result]) => {
          this.log(
            typeof successMessage === "function"
              ? successMessage(result)
              : successMessage,
            context,
            slot
          );
          return result;
        })
        .catch((e) => {
          this.log(failMessage, context, slot);
          throw e;
        });
    };
  }

  log(message: string, context: string, slot: number = -1): number {
    let index = -1;
    if (slot >= 0) {
      this.messages[slot] = `${this.messages[slot]} ${message}`;
    } else {
      index = this.messages.push(`[${context}]: ${message}`) - 1;
    }

    // to boost performance,
    // keep track of only 100 logs
    // don't emit too frequently
    const now = new Date().getTime();
    if (slot || now < this.lastEmitAt + 500) {
      this.lastEmitAt = now;
      this.emit("logs", this.messages.slice(-32));
    }

    return index;
  }

  resetLog() {
    this.messages = [];
  }
}
