import { useState, useContext } from "react";
import styled from "styled-components";
import {
  DL,
  Button,
  Modal,
  Inline,
  Input,
  Textarea,
  CloseIcon,
  Stack,
  H2,
  H3,
  P,
  Label,
  useModal,
} from "rzr-components";
import { ABI, Interaction } from "../../../rzr";
import RzR from "../../../contexts/RzR";

const AbiInput = styled(Textarea)`
  width: 100%;
  display: block;
  min-height: 16em;
  resize: none;
`;

enum ContractPath {
  NEW_CONTRACT,
  EXISTING_CONTRACT,
}

const ContractDetails: React.FC<{
  onSuccess: (address: string) => void;
  onPreviousStep: () => void;
  abi: ABI;
  applicationHash: string;
}> = ({ onSuccess, onPreviousStep, abi, applicationHash }) => {
  const rzr = useContext(RzR);
  const [selectedContractPath, setContractPath] = useState<null | ContractPath>(
    null
  );

  const constructor = abi.find((i) => i.type === "constructor") as Interaction;

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (selectedContractPath === ContractPath.EXISTING_CONTRACT) {
      const {
        address: { value: _address },
      } = e.target as HTMLFormElement;
      const address = _address.toLowerCase();

      if (!applicationHash) {
        return onSuccess(address);
      }

      if (address.length === 40) {
        return rzr
          .registerApplication(applicationHash, `0x${address}`)
          .then(onSuccess);
      } else if (address.length === 42) {
        return rzr
          .registerApplication(applicationHash, address)
          .then(onSuccess);
      } else {
        throw new Error("Validation error");
      }
    }

    const form = e.target as HTMLFormElement;

    rzr
      .deployContract(
        form.bytecode.value,
        abi,
        constructor.inputs.map((input: any) => {
          const value = (form.elements[input.name] as HTMLInputElement).value;
          return input.type.includes("[]") ? JSON.parse(value) : value;
        })
      )
      .then(onSuccess);
  };

  // use tabs for creating new
  return (
    <Stack
      onSubmit={handleSubmit}
      as="form"
      $gap="md"
      $justify="space-between"
      $expand
    >
      <H3>Contract Definition</H3>
      {selectedContractPath === ContractPath.EXISTING_CONTRACT ? (
        <Inline as="label" $gap="sm" $align="center">
          <span>application address: </span>
          <Input
            name="address"
            placeholder="0x05286ae0f251b28f4b996751327d812520d071cb"
            $grow
          />
        </Inline>
      ) : selectedContractPath === ContractPath.NEW_CONTRACT ? (
        <>
          <dl>
            <dt>Your application hash</dt>
            <dd>
              <small>{applicationHash}</small>
            </dd>
          </dl>
          <Stack as="label" $gap="sm">
            bytecode
            <AbiInput
              placeholder={`0x8bf32fda248f4c099a184d1eff51f8ae47b25936130aa73b4c9b3ba67ad8a417`}
              name="bytecode"
            />
          </Stack>
          {constructor &&
            constructor.inputs.map((input) => (
              <Stack as="label" $gap="sm" key={input.name}>
                {input.name}
                <Input name={input.name} />
              </Stack>
            ))}
        </>
      ) : (
        <Stack $gap="lg">
          <P>
            Now you have the chance to pick whether you would like to use an
            already deployed instance of your contract, or you'd prefer to let
            RzR take care of the deployment for you
          </P>
          <Inline $justify="space-around">
            <Button
              onClick={() => setContractPath(ContractPath.EXISTING_CONTRACT)}
            >
              update application
            </Button>
            {abi.length ? (
              <Button
                onClick={() => setContractPath(ContractPath.NEW_CONTRACT)}
              >
                deploy new contract
              </Button>
            ) : (
              <Button
                type="button"
                onClick={() =>
                  rzr.registerApplication(applicationHash).then(onSuccess)
                }
              >
                register application
              </Button>
            )}
          </Inline>
        </Stack>
      )}
      {selectedContractPath !== null && (
        <Inline $justify="space-between">
          <Button type="button" onClick={onPreviousStep}>
            Back
          </Button>
          <Button $variant="secondary">Next</Button>
        </Inline>
      )}
    </Stack>
  );
};

export default ContractDetails;
