import EventEmitter, { Listener } from "../../../EventEmitter";
import { AuthorizationSignature } from "../../../types";

export type AuthenticationBackendEventTypes = {};

export class AuthenticationBackend extends EventEmitter<AuthenticationBackendEventTypes> {
  initialize(identity: Uint8Array): Promise<boolean> {
    return Promise.resolve(true);
  }

  authorize(identity: string): Promise<AuthorizationSignature> {
    return Promise.resolve({ timestamp: 0, signature: "" });
  }
}
