import { Log, LogEmitter } from "../../lvl0";
import { RzRDB } from "../../lvl1";
import { Ethereum } from "../../lvl2";
import { Settings as SettingsType } from "../../../types";

const DEFAULT_SETTINGS: SettingsType = {
  connectivity: {
    peerTimeout: 5,
    syncFrequency: 60,
    maxPeerConnections: 5,
    discoveryLevel: 1,
  },
  ui: {
    displayLog: false,
    animationsEnabled: true,
    activityStatus: true,
    darkModeStatus: true,
  },
  developers: {
    allowNonVerifiedApps: false,
  },
};

export type SettingsEventTypes = {
  settingsUpdated: keyof SettingsType;
};

export class Settings extends LogEmitter<SettingsEventTypes> {
  settings: SettingsType;

  private modules: {
    rzrDB: RzRDB;
    ethereum: Ethereum;
  };

  constructor(log: Log, rzrDB: RzRDB, ethereum: Ethereum) {
    super(log, "Settings");
    this.settings = DEFAULT_SETTINGS;
    this.modules = { rzrDB, ethereum };

    this.attachListeners();
  }

  private attachListeners() {
    this.modules.ethereum.addEventListener(
      "accountChanged",
      this.reloadSettings
    );
  }

  initialize() {
    this.reloadSettings();
  }

  private reloadSettings = () => {
    this.modules.rzrDB
      .loadSettings(this.modules.ethereum.address)
      .then((settings: SettingsType | null) => {
        if (!settings) {
          return this.modules.rzrDB.storeSettings(
            this.modules.ethereum.address,
            DEFAULT_SETTINGS
          );
        } else {
          this.settings = Object.assign(this.settings, settings);
          this.emit("settingsUpdated", "ui");
          this.emit("settingsUpdated", "connectivity");
        }
      });
  };

  getSettingsCluster(cluster: keyof SettingsType) {
    return this.settings[cluster];
  }

  updateSetting<
    K1 extends keyof SettingsType,
    K2 extends keyof SettingsType[K1]
  >(cluster: K1, setting: K2, value: SettingsType[K1][K2]) {
    this.settings[cluster][setting] = value;
    this.modules.rzrDB.storeSettings(
      this.modules.ethereum.address,
      this.settings
    );

    this.emit("settingsUpdated", cluster);
  }

  getSetting<K1 extends keyof SettingsType, K2 extends keyof SettingsType[K1]>(
    cluster: K1,
    setting: K2
  ) {
    return this.settings[cluster][setting];
  }
}
