import { BrowserProvider, Signer } from "ethers";

import {
  EthereumProvider,
  SignatureDetails,
  utils
} from "../../";

export type EthereumStatus = {
  isInstalled: boolean | null;
  isAuthorized: boolean;
  isAuthorizing: boolean;
  isConnected: boolean;
};

export default class MetaMask extends EthereumProvider {
  ethereum: any;
  isReadonly = false;
  status: EthereumStatus = {
    isInstalled: null,
    isAuthorized: false,
    isAuthorizing: false,
    isConnected: false,
  };

  initialize() {
    this.detectEthereum().then(this.installListeners);
  }

  private async detectEthereum() {
    if (typeof window === "undefined") return;

    const lookForMetaMask = async () => {
      if (typeof (window as any).ethereum === "undefined") {
        this.status.isInstalled = false;
        await utils.watch<boolean>(
          () => (typeof (window as any).ethereum === "undefined" ? null : true),
          5000
        );
      }

      this.ethereum = (window as any).ethereum;
      this.status.isInstalled = true;

      this.requestAccounts();
    };

    if (!this.status.isInstalled) {
      lookForMetaMask()
      // _log("looking for MetaMask", lookForMetaMask());
    }
  }

  private installListeners = async () => {
    this.ethereum.on("accountsChanged", this.requestAccounts);
    this.ethereum.on("chainChanged", this.requestAccounts);
    this.ethereum.on("connect", this.onConnect);
    this.ethereum.on("disconnect", this.onDisconnect);
  };

  private onConnect = ({ chainId }: { chainId: string }) => {
    this.status.isConnected = true;
    this.requestAccounts();
    this.emit("connectionStatusChanged", true);
  };

  private onDisconnect = () => {
    this.status.isConnected = false;
    this.emit("connectionStatusChanged", false);
  };

  protected requestAccounts = async () => {
    if (this.status.isAuthorizing) return;
    this.status.isAuthorizing = true;
    await this.ethereum.request({
      method: "eth_requestAccounts",
    });
    this.provider = new BrowserProvider(this.ethereum, "any");
    const signer = await this.provider.getSigner() as Signer;
    this.status.isAuthorizing = false;
    this.status.isAuthorized = true;
    this.signer = signer;

    this.emit("accountChanged", {
      signer,
      provider: this.provider,
    });
  };

  async sign(signatureData: Uint8Array): Promise<string> {
    if (!this.signer) throw new Error("no signer");
    return this.signer.signMessage(signatureData);
  }
}
