// This module is responsible for synchronousity
// events were chosen because the idea of the rzr is that the different

type RzRConfiguration = {
  rzrSocket: {
    // this is only for initial contact, all signaling servers should be queried from here and stored in database
    rtc: RTCConfiguration;
  };
  ethereum: {
    contracts: {
      RzR: ContractConfig;
    };
  };
};

export type EventMap = EthereumEventMap &
  RzRDBEventMap &
  RzRSyncEventMap &
  LogEventMap &
  SettingsEventTypes &
  ConnectionMatrixEventTypes & {
    protectedModeChange: boolean;
    notification: RzRNotification;
    accountChanged: string;
    contractActivity: boolean;
  };

type RzRModulesConfiguration = {
  Ethereum: {
    provider: typeof EthereumProvider;
  };
  Authentication: {
    backend: typeof AuthenticationBackend;
  };
  RzRDB: {
    backend: typeof DatabaseBackend;
  };
  RzRPeer: {
    supportedProtocols: CommunicationProtocols;
    supportedSignalingProtocols: SignalingProtocols;
  };
  ModuleMatrix: {
    environment: typeof Environment;
  };
};

export default class RzR<ModuleFormat> extends LogEmitter<EventMap> {
  config: RzRConfiguration;

  modules: {
    authentication: Authentication;
    settings: Settings;
    moduleMatrix: ModuleMatrix<ModuleFormat>;
    connectionMatrix: ConnectionMatrix;
    ethereum: Ethereum;
    rzrDB: RzRDB;
    rzrSync: RzRSync;
    clock: Clock;
    log: Log;
    rzrDiscovery: RzRDiscovery;
  };

  constructor(
    config: RzRConfiguration,
    {
      RzRDB: { backend: DatabaseBackend },
      Ethereum: { provider: SelectedEthereumProvider },
      RzRPeer: { supportedProtocols, supportedSignalingProtocols },
      ModuleMatrix: { environment: ModuleEnvironment },
      Authentication: { backend: SelectedAuthenticationBackend },
    }: RzRModulesConfiguration
  ) {
    const log = new Log();

    super(log, "RzR");

    this.config = config;
    const clock = new Clock();
    const rzrDB = new RzRDB(DatabaseBackend, clock, log);
    const ethereum = new Ethereum(
      clock,
      log,
      rzrDB,
      SelectedEthereumProvider,
      this.config.ethereum.contracts.RzR
    );

    const authentication = new Authentication(
      clock,
      log,
      rzrDB,
      ethereum,
      SelectedAuthenticationBackend
    );

    const settings = new Settings(log, rzrDB, ethereum);

    const connectionMatrix = new ConnectionMatrix(
      supportedSignalingProtocols,
      log,
      clock,
      rzrDB,
      ethereum,
      settings,
      authentication
    );

    const rzrDiscovery = new RzRDiscovery(log, {
      clock,
      rzrDB,
      ethereum,
      settings,
      connectionMatrix,
    });

    setCommunicationProtocols(supportedProtocols);

    this.modules = {
      log,
      rzrDB,
      ethereum,
      authentication,

      settings,

      rzrDiscovery,

      rzrSync: new RzRSync(log, clock, rzrDB, ethereum, settings),
      clock,
      connectionMatrix,
      moduleMatrix: new ModuleMatrix<ModuleFormat>(
        log,
        ModuleEnvironment,
        rzrDB,
        ethereum,
        connectionMatrix
      ),
    };

    this.attachListeners();
  }

  private attachListeners() {
    const redirect =
      <K extends keyof EventMap>(type: K) =>
      (ev: EventMap[K]) => {
        super.emit(type, ev);
      };

    // events
    this.modules.rzrSync.addEventListener(
      "syncStatusChanged",
      redirect("syncStatusChanged")
    );

    this.modules.rzrSync.addEventListener(
      "downstreamStatusChanged",
      redirect("downstreamStatusChanged")
    );

    this.modules.rzrSync.addEventListener(
      "upstreamStatusChanged",
      redirect("upstreamStatusChanged")
    );

    this.modules.log.addEventListener("logs", redirect("logs"));

    this.modules.ethereum.addEventListener(
      "chainChanged",
      redirect("chainChanged")
    );

    this.modules.settings.addEventListener(
      "settingsUpdated",
      redirect("settingsUpdated")
    );

    /*
    this.modules.rzrContract.addEventListener(
      "accountLoaded",
      redirect("accountLoaded")
    );*/

    this.modules.moduleMatrix.addEventListener(
      "processingStatus",
      redirect("contractActivity")
    );
    this.modules.moduleMatrix.addEventListener("notification", (notification) =>
      this.notify(
        notification.title,
        notification.description,
        notification.source,
        notification.severity
      )
    );

    this.modules.rzrDB.addEventListener(
      "databaseLoaded",
      redirect("databaseLoaded")
    );

    this.modules.ethereum.addEventListener(
      "newSignature",
      redirect("newSignature")
    );

    this.modules.ethereum.addEventListener(
      "pendingTransactionChanged",
      redirect("pendingTransactionChanged")
    );

    this.modules.connectionMatrix.addEventListener(
      "routersConnected",
      redirect("routersConnected")
    );

    this.modules.connectionMatrix.addEventListener(
      "newPeerConnection",
      redirect("newPeerConnection")
    );

    this.modules.connectionMatrix.addEventListener(
      "routerSendingMessageStatus",
      redirect("routerSendingMessageStatus")
    );

    this.modules.connectionMatrix.addEventListener(
      "routerReceivingMessageStatus",
      redirect("routerReceivingMessageStatus")
    );

    this.modules.connectionMatrix.addEventListener(
      "peerSendingMessageStatus",
      redirect("peerSendingMessageStatus")
    );

    this.modules.connectionMatrix.addEventListener(
      "peerReceivingMessageStatus",
      redirect("peerReceivingMessageStatus")
    );

    this.modules.connectionMatrix.addEventListener(
      "connectionStatusChanged",
      redirect("connectionStatusChanged")
    );

    this.modules.connectionMatrix.addEventListener(
      "connectionsChanged",
      redirect("connectionsChanged")
    );

    this.modules.connectionMatrix.addEventListener(
      "latestPeersChanged",
      redirect("latestPeersChanged")
    );

    this.modules.connectionMatrix.addEventListener(
      "dataTransferMetricsUpdated",
      redirect("dataTransferMetricsUpdated")
    );

    this.modules.rzrDB.addEventListener(
      "databaseProcessingStatusChanged",
      redirect("databaseProcessingStatusChanged")
    );

    this.modules.ethereum.addEventListener(
      "accountChanged",
      redirect("accountChanged")
    );

    this.modules.rzrDB.addEventListener(
      DataType.MODULE,
      redirect(DataType.MODULE)
    );
  }

  // state queries
  get isOnline() {
    return this.modules.connectionMatrix.isOnline;
  }

  get isProviderReadonly() {
    return this.modules.ethereum.isReadonly;
  }

  get settings(): SettingsType {
    return this.modules.settings.settings;
  }

  get routerConnections() {
    return this.modules.connectionMatrix.routerConnections;
  }

  get latestPeers() {
    return Array.from(this.modules.connectionMatrix.latestPeers.entries());
  }

  get identity() {
    return this.modules.ethereum.identity;
  }

  get isIdentityAvailable() {
    return this.modules.ethereum.isIdentityAvailable;
  }

  get address() {
    return this.modules.ethereum.address;
  }

  get isAuthorized() {
    return this.modules.authentication.isAuthorized;
  }

  // workflows

  // these are redirect workflows, must check for target
  // if target was not me, send them to my peers
  // if one of my peers is in there, send it only to him
  // remember though, this is not enough
  // it's actually not addressAndOffer
  // but rather addressAndSourceAndOffer
  // there can be multiple relays
  // not really required in rzr node based routing
  private onAnswer = (addressAndOffer: Uint8Array) => {};

  private onCandidate = (addressAndOffer: Uint8Array) => {};

  updateSetting<
    K1 extends keyof SettingsType,
    K2 extends keyof SettingsType[K1]
  >(settingCluster: K1, setting: K2, value: SettingsType[K1][K2]) {
    this.modules.settings.updateSetting(settingCluster, setting, value);
  }

  // the sensitive bit
  unlock(password: string) {
    this.emit("protectedModeChange", true);

    return Promise.all([
      this.modules.rzrDB.loadIdentity(this.address, password),
      this.modules.rzrDB.loadPlayer(this.address),
    ]).then(([identity, player]) => {
      if (!identity || !player) {
        return null;
      }

      if (player.identity !== identity.address.toLowerCase()) {
        // this is a big one and we need a new signature or wait
        return null;
      }

      this.modules.authentication
        .initialize(identity)
        .then(() => this.emit("protectedModeChange", false));
    });
  }

  // this is just to make onboarding more seamless
  initialize(defaultRouterAddress?: string) {
    return this.modules.rzrDB.initialize().then(() => {
      this.modules.ethereum.initialize();

      once(this.modules.ethereum, "accountChanged").then(
        this.modules.rzrSync.syncOnChainData
      );

      // intentionally event based to leave password and unlocked identity out of closure for sure
      once(this, "protectedModeChange", {
        precondition: (isProtected) => !isProtected,
      })
        .then(() => this.modules.rzrDB.loadInstalledModules())
        .then((modules) =>
          resolveDependencies(
            modules.reduce(
              (acc, m) => ({ ...acc, [m.contentHash]: m.dependencies }),
              {}
            )
          )
        )
        .then((modules) => resolveInSequence(modules, this.loadModule))
        .then(() => {
          this.modules.connectionMatrix.initialize(defaultRouterAddress);
        });
    });
  }

  private notify(
    title: string,
    description?: string,
    source = "RzR",
    severity: "info" | "warn" | "error" = "info"
  ) {
    this.emit("notification", {
      notificationId: hexlify(randomBytes(4)),
      source,
      severity,
      title,
      description,
    });
  }

  async enableModule(contentHash: string) {
    // need to check if an already existing version is installed
    // if so, first disable that
    return Promise.all([
      this.modules.rzrDB.loadModuleVersions(contentHash),
      this.modules.rzrDB.loadInstalledModules(),
    ])
      .then(([moduleVersions, installedModules]) => {
        const moduleVersionsHashes = moduleVersions.map((v) => v.contentHash);
        return Promise.all(
          installedModules
            .map((m) => m.contentHash)
            .filter((installedModuleHash) =>
              moduleVersionsHashes.includes(installedModuleHash)
            )
            .map((hash) => this.disableModule(hash))
        );
      })
      .then(() =>
        this.modules.rzrDB
          .toggleModule(contentHash, true)
          .then(() => this.loadModule(contentHash))
          .then(() =>
            this.notify(
              "App launched",
              `${formatHex(
                contentHash
              )} is now accesible through the app manager.`
            )
          )
          .catch((e) =>
            this.notify(
              "Missing dependency",
              "one or more dependencies are not enabled for this application."
            )
          )
      );
  }

  disableModule(contentHash: string) {
    return this.modules.rzrDB
      .toggleModule(contentHash, false)
      .then(() => this.stopModule(contentHash))
      .catch(() =>
        this.notify(
          "Critical dependency",
          "One or more applications depend on this app to run."
        )
      );
  }

  deployContract(bytecode: string, abi: any, params: any[]): Promise<string> {
    return this.modules.ethereum
      .deployContract(bytecode, abi, params)
      .then((contract) => contract.getAddress())
      .then((address) => address.toLowerCase());
  }

  loadModules(search?: string) {
    if (search) return this.modules.rzrDB.searchModules(search);
    return this.modules.rzrDB.loadModules();
  }

  loadModuleMetadata(contentHash: string) {
    return this.modules.rzrDB.loadModuleMetadata(contentHash);
  }

  loadModule = (contentHash: string) => {
    return Promise.all([
      this.modules.rzrDB.loadModuleRegistration(contentHash),
      this.modules.rzrDB.loadModule(contentHash),
      this.modules.rzrDB.loadModuleMetadata(contentHash),
    ]).then(([registrationData, module, metadata]) => {
      if (!module || !metadata || (!metadata.isLocal && !registrationData))
        return null;

      return this.modules.moduleMatrix.registerModule(
        registrationData?.address || metadata.contentHash,
        module.slice(getMetadataEnd(module)),
        metadata.isLocal ? metadata.sourceUrl : undefined
      );
    });
  };

  stopModule = (contentHash: string) => {
    return this.modules.rzrDB
      .loadModuleRegistration(contentHash)
      .then((registrationData) => {
        if (!registrationData) return;
        this.modules.moduleMatrix.stopModule(registrationData.address);
      });
  };

  private prepareModuleData = (
    moduleDataAndABI: Uint8Array,
    metadata: ModuleMetadata
  ) => {
    const ec = new TextEncoder();
    return getBytes(
      concat([
        zeroPadValue(
          toBeHex(this.modules.clock.now),
          moduleMetadataSchema.updatedAt.byteLength as number
        ),
        zeroPadValue(
          ec.encode(metadata.name),
          moduleMetadataSchema.name.byteLength as number
        ),
        zeroPadValue(
          ec.encode(metadata.sourceUrl),
          moduleMetadataSchema.sourceUrl.byteLength as number
        ),
        zeroPadValue(
          ec.encode(metadata.tags),
          moduleMetadataSchema.tags.byteLength as number
        ),
        zeroPadValue(
          ec.encode(metadata.description),
          moduleMetadataSchema.description.byteLength as number
        ),

        // first 1024 bytes are metadata

        // then dependencies
        ...metadata.dependencies,

        // then the rest of the content
        moduleDataAndABI,
      ])
    );
  };

  private signAndSaveModuleData = (
    moduleData: Uint8Array,
    dependenciesSize: number
  ) => {
    return this.modules.ethereum
      .sign(getBytes(sha256(moduleData)), {
        title: "Module installation",
        description: "Please sign your module data hash",
      })
      .then((signature) =>
        this.modules.rzrDB.storeModule(
          getBytes(
            concat([new Uint8Array([dependenciesSize]), signature, moduleData])
          )
        )
      );
  };

  // should also request for signature of the entire module data
  installModule(
    address: string,
    abi: Uint8Array,
    module: ModuleFormat,
    metadata: ModuleMetadata
  ) {
    return (
      metadata.isLocal
        ? Promise.resolve(new Uint8Array(0))
        : this.modules.moduleMatrix.parseModule(module)
    ).then((rawModule: Uint8Array) => {
      const moduleData = this.prepareModuleData(
        getBytes(
          concat([
            // contract information if any
            zeroPadValue(toBeHex(abi.length), 4),
            abi,

            // then the module data itself, its hash is stored on chain
            rawModule,
          ])
        ),
        metadata
      );

      // local module
      if (rawModule.length === 0 && address) {
        return this.modules.rzrDB.storeLocalModule(
          address,
          getBytes(concat([new Uint8Array(1), new Uint8Array(65), moduleData]))
        );
      }

      return this.signAndSaveModuleData(
        moduleData,
        metadata.dependencies.length
      );
    });
  }

  storeRouter(url: string) {
    if (!this.modules.connectionMatrix.isConnected) {
      return this.modules.rzrDB.storeRouter({
        address: "0x0000000000000000000000000000000000000000",
        url,
      });
    }

    const ec = new TextEncoder();

    return this.modules.ethereum
      .sign(ec.encode(url), {
        title: "New Router",
        description: "This is to add a new connection",
      })
      .then((signature) =>
        this.modules.rzrDB.storeRouter({
          address: this.modules.ethereum.address,
          url,
          registrationSignature: getBytes(signature),
        })
      );
  }

  revealIdentity(): Promise<string | null> {
    return this.modules.rzrDB
      .loadProtectedIdentity(this.modules.ethereum.address)
      .then((identity) => {
        if (identity) return hexlify(identity);
        return null;
      });
  }

  storeIdentity = (identity: string) => {
    if (identity.length !== 66) identity = `0x${identity}`;
    if (identity.length !== 66) throw new Error("malformed identity");

    return this.modules.rzrDB
      .storeIdentity(this.modules.ethereum.address, getBytes(identity))
      .then(this.modules.ethereum.reloadIdentity);
  };

  generateNewIdentity = () => {
    this.modules.ethereum.newIdentity();
  };

  registerApplication = (applicationHash: string, _address?: string) => {
    const address = _address || new Wallet(applicationHash).address;

    return this.modules.ethereum
      .registerApplication(address, applicationHash)
      .then(() => address.toLowerCase());
  };

  updateModuleMetadata = (metadata: ModuleMetadata) => {
    return Promise.all([
      this.modules.rzrDB.loadModule(metadata.contentHash),
      this.modules.rzrDB.loadModuleMetadata(metadata.contentHash),
    ]).then(([moduleContent, previousMetadata]) => {
      if (!previousMetadata || !moduleContent)
        throw new Error("module not found");

      metadata.byteSize = previousMetadata.byteSize;
      metadata.isInstalled = previousMetadata.isInstalled;

      // calculate pre module content size
      // TODO: use constant here
      const metadataSize = 1024 + moduleContent[0] * constants.HASH_BYTE_LENGTH;

      const moduleData = this.prepareModuleData(
        moduleContent.slice(metadataSize),
        metadata
      );

      return this.signAndSaveModuleData(
        moduleData,
        metadata.dependencies.length
      );
    });
  };

  resetDatabase() {
    return this.modules.rzrDB.reset();
  }

  loadModuleProfile(contentHash: string): Promise<{
    registrations: ModuleRegistrationData[];
    metadata: ModuleMetadata;
  }> {
    return Promise.all([
      this.modules.rzrDB.loadModuleVersions(contentHash),
      this.modules.rzrDB.loadModuleMetadata(contentHash),
    ]).then(([registrations, metadata]) => {
      if (!metadata) throw new Error("module not found");
      return {
        registrations,
        metadata,
      };
    });
  }
}

