import { useCallback, useState, useContext, useEffect, useRef } from "react";
import { Stack } from "rzr-components";
import styled from "styled-components";
import RzRContext from "../../contexts/RzR";
import { RzRNotification } from "../../rzr";
import NotificationItem from "./NotificationItem";

// TODO: notifications position to settings?
export const NotificationsContainer = styled.aside`
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  z-index: 6;

  > ol {
    list-style: none;
    width: 16rem;
    margin: 0;

    > li {
      border-bottom: 2px solid ${({ theme }) => theme.colors.primary};
      border-right: 2px solid ${({ theme }) => theme.colors.primary};
      padding: 0.5rem;
      background: ${({ theme }) => theme.colors.background[2]};
      &:not(:first-of-type) {
        margin-bottom: 1rem;
      }

      transition: transform 0.25s;

      &:hover {
        transform: translateX(2rem);
      }
    }
  }
`;

const Notifications = () => {
  const rzr = useContext(RzRContext);
  const [notifications, setNotifications] = useState<RzRNotification[]>([]);
  const notificationsSetRef = useRef(new Set<RzRNotification>());

  const update = useCallback(
    (notification: RzRNotification, isDelete = false) => {
      notificationsSetRef.current[isDelete ? "delete" : "add"](notification);
      setNotifications(Array.from(notificationsSetRef.current));
    },
    []
  );

  useEffect(() => {
    const notificationsSet = notificationsSetRef.current;

    const onNewNotification = (notification: RzRNotification) => {
      update(notification);
      setTimeout(() => update(notification, true), 15000);
    };

    rzr.addEventListener("notification", onNewNotification);

    return () => {
      rzr.removeEventListener("notification", onNewNotification);
    };
  }, [update]);

  return (
    <NotificationsContainer>
      <Stack as="ol" $reverse>
        {notifications.map((n) => (
          <NotificationItem
            onClick={() => update(n, true)}
            key={n.notificationId}
            notification={n}
          />
        ))}
      </Stack>
    </NotificationsContainer>
  );
};

export default Notifications;
