import { Contract } from "ethers";
import EventEmitter from "../../../EventEmitter";
import { IntentStatus } from "../../lvl4";
import { Notification } from '../../../types'

export type RPCTarget =
  | "provider"
  | "ethereum"
  | "contract"
  | "rzr" // yep, modules have full read access to database, only "load" prefixed functions are allowed to be called for now

export type RPCEventData = {
  target: RPCTarget;
  method: string;
  params: any[];
  resultPath: string;
};

export type IntentUpdateEvent = {
  identity: string;
  intent: number;
  sourceModuleAddress: string;
  intentStatus: IntentStatus;
};

export type ContactPeerEvent = {
  identity: string;
  intents: number[];
  sourceModuleAddress: string;
};

export type PeerMessageEvent = {
  identity: string;
  sourceModuleAddress: string;
  messageType: number;
  message?: string; // as hexstring
};

export type RPCInterface = (
  source: string,
  target: RPCTarget,
  method: string,
  params: any[],
  resultPath: string,
) => Promise<any>;

type EnvironmentEventMap = {
  intentUpdate: IntentUpdateEvent;
  notification: Notification;

  peerMessage: PeerMessageEvent;
  contactPeer: ContactPeerEvent;
};

export class Environment extends EventEmitter<EnvironmentEventMap> {
  installedModules = new Map<string, any>();
  rpcInterface: RPCInterface;

  constructor(rpcInterface: RPCInterface) {
    super();

    this.rpcInterface = rpcInterface;
  }

  parseModule(module: any): Promise<Uint8Array> {
    return Promise.resolve(new Uint8Array());
  }

  registerModule(address: string, moduleData: Uint8Array | string): any {
    return moduleData;
  }

  stopModule(address: string): any {
    return null;
  }

  notify(target: string, eventType: string | number, message: any) {}

  broadcast(eventType: string | number | symbol, json: string) {}

  protected announceModuleChange() {
    this.broadcast(
      "installedModulesChanged",
      JSON.stringify(Array.from(this.installedModules.keys()))
    );
  }
}
