import { useState, useContext, useEffect, useMemo, useCallback } from "react";
import { Wallet, Contract, Transaction } from "ethers";
import useWatchRzREvent from "./useWatchRzREvent";

import RzRContext from '../contexts/RzR'

import {
  RzR,
  TransactionDetails,
  TransactionReceipt,
  ConnectionStatus,
  SignatureDetails,
  WebModuleFormat
} from "../rzr";

const useRzR = () => {
  const rzr = useContext(RzRContext)
  const [chainId] = useWatchRzREvent("chainChanged", "");
  const [isAuthorized] = useWatchRzREvent<
    "routersConnected",
    boolean
  >("routersConnected", false);

  /*
  const [assets] = useWatchRzREvent<"accountLoaded", number[]>(
    "accountLoaded",
    [],
    ({ assets }) => [...assets]
  );*/

  const [accountLoaded] = useWatchRzREvent<"accountChanged", boolean>(
    "accountChanged",
    false,
    () => true
  );

  const [databaseLoaded] = useWatchRzREvent("databaseLoaded", false);

  const [isOnline] = useWatchRzREvent("connectionStatusChanged", rzr.isOnline);

  const [pendingTransaction] = useWatchRzREvent(
    "pendingTransactionChanged",
    null
  );

  const [pendingSignature] = useWatchRzREvent<
    "newSignature",
    SignatureDetails | null
  >("newSignature", null);

  useEffect(() => {
    // rzr.initialize();
  }, []);

  return {
    // clients,
    chainId,
    pendingTransaction,
    status: {
      databaseLoaded,
      accountLoaded,
    },
    connection: {
      isConnected: isOnline,
      isAuthorized,
    },
    // identityChainStatus,
    //ethereum: ethereumStatus,
    pendingSignature,
    /*
    account: {
      assets: assets as number[],
    },*/
  };
};

export default useRzR;
