import { useState } from "react";
import { Modal } from 'rzr-components'
import { createGlobalStyle, ThemeProvider } from "styled-components";
import { darkTheme } from './theme'
import RzRContext from "./contexts/RzR";
import { RzR, WebModuleFormat } from "./rzr";
import SelectProvider from './components/SelectProvider'
import App from "./App";

const GlobalStyles = createGlobalStyle`

html, body {
  height: 100%;
}
body {
    font-family: 'Anonymous Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0;
    padding: 0;

      background: radial-gradient(
        circle at center,
        ${darkTheme.colors.background[0]} 0%,
        ${darkTheme.colors.background[1]} 50%,
        ${darkTheme.colors.background[2]} 100%
      );

    color: ${darkTheme.colors.text};
    overflow: hidden;
  `;

const RzRLoader = () => {
  const [rzr, setRzR] = useState<RzR<WebModuleFormat> | null>(null);

  // rzr should be initialized here, app should receieve initialized rzr object

  if (rzr)
    return (
      <RzRContext.Provider value={rzr}>
        <App />
      </RzRContext.Provider>
    );

  return (
    <ThemeProvider theme={darkTheme}>
      <GlobalStyles />
      <SelectProvider setRzR={setRzR} />
    </ThemeProvider>
  );
};

export default RzRLoader;
