import { useContext, useEffect, useRef, useState, useCallback } from "react";
import styled from "styled-components";
import {
  WindowsIcon,
  DesktopIcon,
  EtherIcon,
  DatabaseIcon,
  P2PIcon,
  RouterIcon,
  GlobeIcon,
} from "../../icons/";
import UnlockModal from "../UnlockModal";
import useWatchRzREvent from "../../hooks/useWatchRzREvent";
import useHighFrequencyEvent from "./useHighFrequencyEvent";
import RzR from "../../contexts/RzR";

import { Tooltip, Inline, Button, useModal } from "rzr-components";

export const Container = styled(Inline)`
  position: fixed;
  bottom: 0;
  z-index: 5;
  left: calc(50% - 3rem);
  right: calc(50% - 3rem);
  width: 6rem;
  height: 3rem;
  padding: 0.25rem;

  svg {
    width: 2rem;
    height: 2rem;
  }

  border: 2px solid ${({ theme }) => theme.colors.primary};
  border-bottom-width: 0px;
  width: 16rem;
  left: calc(50% - 8rem);
  right: calc(50% - 8rem);

  ${Button} {
    padding: 1em;
    min-width: 4rem;
  }
`;

const StatusBar: React.FC<{
  moduleWrappers: Element[];
  isDisabled: boolean;
  setModuleWrappers: (moduleWrappers: Element[]) => void;
}> = ({ moduleWrappers, setModuleWrappers, isDisabled }) => {
  const rzr = useContext(RzR);
  const [isUnlockModalOpen, openUnlockModal, closeUnlockModal] = useModal();

  const [isChainSyncInProgress] = useHighFrequencyEvent(
    "syncStatusChanged",
    false
  );
  // const [downstreamCount] = useHighFrequencyEvent("downstreamStatusChanged", 0);
  // const [upstreamCount] = useHighFrequencyEvent("upstreamStatusChanged", 0);
  const [contractActivity] = useHighFrequencyEvent("contractActivity", false);
  const [databaseProcessingStatus] = useHighFrequencyEvent(
    "databaseProcessingStatusChanged",
    false
  );
  const [isPeerSendingMessage] = useHighFrequencyEvent(
    "peerSendingMessageStatus",
    false
  );
  const [isPeerReceivingMessage] = useHighFrequencyEvent(
    "peerReceivingMessageStatus",
    false
  );
  const [isRouterSendingMessage] = useHighFrequencyEvent(
    "routerSendingMessageStatus",
    false
  );
  const [isRouterReceivingMessage] = useHighFrequencyEvent(
    "routerReceivingMessageStatus",
    false
  );

  const modulesRef = useRef(document.getElementById("modules"));

  const raiseModule = useCallback(
    (e: Event) => {
      // this function must NOT cause a rerender for performance reasons.
      const container = modulesRef.current;
      if (!container || container.classList.contains("show-desktop")) return;

      if (!(e.target as HTMLDivElement).classList.contains("overlay")) {
        // not overlay was clicked, assuming pop out button.
        // need to be careful in the future, if click away is supported

        container.classList.add("show-desktop");
        return;
      }

      for (const moduleWrapper of moduleWrappers) {
        if (
          [moduleWrapper, ...Array.from(moduleWrapper.children)].includes(
            e.target as HTMLElement
          )
        ) {
          moduleWrapper.classList.add("active");
        } else {
          moduleWrapper.classList.remove("active");
        }
      }

      container.classList.remove("overview");
    },
    [moduleWrappers]
  );

  useEffect(() => {
    if (
      modulesRef.current &&
      modulesRef.current.classList.contains("overview")
    ) {
      for (const moduleWrapper of moduleWrappers) {
        moduleWrapper.addEventListener("click", raiseModule);
      }
    }

    return () => {
      for (const moduleWrapper of moduleWrappers) {
        moduleWrapper.removeEventListener("click", raiseModule);
      }
    };
  }, [moduleWrappers, raiseModule]);

  return (
    <Container as="footer" $align="center" $justify="space-around">
      {isUnlockModalOpen && <UnlockModal close={closeUnlockModal} />}
      <EtherIcon active={isChainSyncInProgress || contractActivity} />
      <DatabaseIcon active={databaseProcessingStatus} />
      {rzr.isAuthorized ? (
        <Tooltip text={isDisabled ? "you have no active applications" : ""}>
          <Button
            $variant="secondary"
            onClick={() => {
              const container = modulesRef.current;
              if (!container) return;

              if (
                container.classList.contains("overview") &&
                !container.classList.contains("show-desktop")
              ) {
                // double click
                for (const moduleWrapper of moduleWrappers) {
                  moduleWrapper.classList.remove("active");
                }

                container.classList.add("show-desktop");

                return;
              }

              // coming back from show-desktop

              const children = Array.from(
                container.children || []
              ) as Element[];
              container.classList.add("overview");
              container.classList.remove("show-desktop");

              setModuleWrappers(children);
            }}
            disabled={isDisabled}
          >
            <WindowsIcon />
          </Button>
        </Tooltip>
      ) : (
        <Tooltip
          text={
            rzr.identity
              ? ""
              : "no registration record found for this account, if you just logged in for the first time, this may take a minute"
          }
        >
          <Button
            $variant="accent"
            onClick={openUnlockModal}
            disabled={!rzr.identity}
          >
            <GlobeIcon active={false} />
          </Button>
        </Tooltip>
      )}
      <P2PIcon
        receivingActive={isPeerReceivingMessage}
        sendingActive={isPeerSendingMessage}
        downstreamActive={false}
        upstreamActive={false}
      />
      <RouterIcon
        receivingActive={isRouterReceivingMessage}
        sendingActive={isRouterSendingMessage}
      />
    </Container>
  );
};

export default StatusBar;
