import { Signer, Wallet, WebSocketProvider } from "ethers";

import {
  EthereumProvider,
  SignatureDetails,
} from "../../";

export default class ReadonlyProvider extends EthereumProvider {
  isReadonly = true;
  storePrivateKey(privateKey: string) {
    localStorage.privateKey = privateKey;
  }

  loadPrivateKey() {
    return localStorage.privateKey;
  }

  initialize() {
    const privateKey =
      this.loadPrivateKey() ||
      (document.getElementById("privateKey") as HTMLInputElement).value;

    this.provider = new WebSocketProvider(
      "wss://sepolia.infura.io/ws/v3/e68ca51991a645f7bf0b664f010072d5"
    );
    this.signer = new Wallet(privateKey, this.provider);

    this.emit("connectionStatusChanged", true);

    this.emit("accountChanged", {
      signer: this.signer,
      provider: this.provider,
    });

    this.storePrivateKey(privateKey);
  }

  sign(signatureData: Uint8Array) {
    if (!this.signer) throw new Error("signer not set");
    return this.signer.signMessage(signatureData);
  }
}
