import { useState, useEffect, useCallback, useContext } from "react";
import {
  Tooltip,
  Modal,
  H2,
  H3,
  H4,
  Inline,
  Button,
  Stack,
  P,
  CloseIcon,
} from "rzr-components";
import RzR from "../../contexts/RzR";
import { ModuleMetadata, ModuleRegistrationData } from "../../rzr";
import { default as UpdateModuleMetadata } from "../ModuleInstaller/Steps/ModuleMetadata";
import { utils } from "../../rzr";

const ModuleDetails: React.FC<{ contentHash: string; close: () => void }> = ({
  contentHash,
  close,
}) => {
  const rzr = useContext(RzR);
  const [moduleRegistrations, setModuleRegistrations] = useState<
    ModuleRegistrationData[]
  >([]);
  const [metadata, setMetadata] = useState<ModuleMetadata | null>(null);
  const [selectedVersion, setSelectedVersion] = useState<string>(contentHash);
  const [isLoading, setIsLoading] = useState(false);
  const [isUpdating, setIsUpdating] = useState(false);
  const [moduleHistory, setModuleHistory] = useState<string[]>([]);

  const setMetadataAndStopLoading = (metadata: ModuleMetadata | null) => {
    setMetadata(metadata);
    setIsLoading(false);
  };

  const refreshMetadata = useCallback(() => {
    rzr.loadModuleMetadata(selectedVersion).then(setMetadataAndStopLoading);
  }, [selectedVersion]);

  const enableModule = () => {
    if (!metadata) return;
    setIsLoading(true);
    rzr.enableModule(metadata.contentHash).then(refreshMetadata);
  };

  const disableModule = () => {
    if (!metadata) return;
    setIsLoading(true);
    rzr.disableModule(metadata.contentHash).then(refreshMetadata);
  };

  const goBackOrClose = () => {
    if (moduleHistory.length === 0) close();
    setSelectedVersion(moduleHistory.slice(-1)[0]);
    setModuleHistory(moduleHistory.slice(0, moduleHistory.length - 1));
  };

  useEffect(() => {
    refreshMetadata();
  }, [refreshMetadata]);

  useEffect(() => {
    rzr
      .loadModuleProfile(selectedVersion)
      .then(({ registrations, metadata }) => {
        setMetadata(metadata);
        setModuleRegistrations(
          registrations.sort((r1, r2) =>
            r1.blockNumber < r2.blockNumber ? 1 : -1
          )
        );
      });
  }, [selectedVersion]);

  const moduleAddress = moduleRegistrations[0]?.address || "";
  const moduleOwner = moduleRegistrations[0]?.owner || "";

  return (
    <Modal $size="lg">
      {!metadata ? (
        "loading..."
      ) : isUpdating ? (
        <UpdateModuleMetadata
          applicationHash={selectedVersion}
          onPreviousStep={() => setIsUpdating(false)}
          onSuccess={(results) => {
            rzr.updateModuleMetadata(results).then(() => {
              refreshMetadata();
              setIsUpdating(false);
            });
          }}
          metadata={metadata}
        />
      ) : (
        <Stack $gap="lg" $expand $justify="space-between">
          <Stack $gap="lg">
            <H2>{metadata.name}</H2>
            {metadata.description ? (
              <P>{metadata.description}</P>
            ) : (
              <P $align={"center"}>no description</P>
            )}
            <Stack $gap="sm">
              <Stack $gap="sm">
                <span>source url</span>
                {metadata.sourceUrl ? (
                  <span>{metadata.sourceUrl}</span>
                ) : (
                  <P $align="center">no source available</P>
                )}
              </Stack>
              <span>dependencies</span>
              {metadata.dependencies.length > 0 ? (
                <Inline $gap="sm">
                  {metadata.dependencies.map((hash) => (
                    <Button
                      key={hash}
                      $size="sm"
                      onClick={() => {
                        setModuleHistory([...moduleHistory, selectedVersion]);
                        setSelectedVersion(hash);
                      }}
                    >
                      {utils.formatHex(hash)}
                    </Button>
                  ))}
                </Inline>
              ) : (
                <P $align="center">no dependencies</P>
              )}
            </Stack>
            <Stack $gap="sm">
              <span>Tags</span>
              {metadata.tags.length > 0 ? (
                <Inline $gap="lg" $wrap>
                  {metadata.tags.split(",").map((t) => (
                    <span key={t}>{t}</span>
                  ))}
                </Inline>
              ) : (
                <P $align="center">no tags added</P>
              )}
            </Stack>
            <Stack $gap="sm">
              <span>available versions</span>
              <Inline $gap="sm">
                {moduleRegistrations.map(({ contentHash }) => (
                  <Button
                    key={contentHash}
                    disabled={contentHash === selectedVersion}
                    $size="sm"
                    onClick={() => setSelectedVersion(contentHash)}
                  >
                    {contentHash === selectedVersion
                      ? "current"
                      : utils.formatHex(contentHash)}
                  </Button>
                ))}
              </Inline>
            </Stack>
          </Stack>
          <Stack $gap="lg">
            <Inline $align="flex-end" $justify="space-between" $wrap>
              <Stack>
                <H4>
                  <Inline $gap="sm">
                    <span>address: </span>
                    <Tooltip text={moduleAddress}>
                      <a>{utils.formatHex(moduleAddress, 8)}</a>
                    </Tooltip>
                  </Inline>
                </H4>
                {moduleAddress !== moduleOwner && (
                  <H4>
                    <Inline $gap="sm">
                      <span>owner: </span>
                      <Tooltip text={moduleOwner}>
                        <a>{utils.formatHex(moduleOwner, 9)}</a>
                      </Tooltip>
                    </Inline>
                  </H4>
                )}
                <H4>
                  <Inline $gap="sm">
                    <span>app proof: </span>
                    <Tooltip text={metadata.contentHash}>
                      <a>{utils.formatHex(metadata.contentHash, 7)}</a>
                    </Tooltip>
                  </Inline>
                </H4>
              </Stack>
              <Stack>
                <Inline $gap="md" $justify="flex-end">
                  <span>size:</span>
                  <span> {utils.formatBytes(BigInt(metadata.byteSize))}</span>
                </Inline>
                <Inline $gap="md" $justify="flex-end">
                  <span>updated:</span>
                  <span>
                    {" "}
                    {new Date(metadata.updatedAt * 1000).toLocaleString()}
                  </span>
                </Inline>
              </Stack>
            </Inline>
            <Inline $justify="space-between">
              <Button onClick={goBackOrClose}>back</Button>
              <Inline $gap="lg">
                {moduleRegistrations.length > 0 &&
                  moduleRegistrations[0].owner === rzr.address && (
                    <Button onClick={() => setIsUpdating(true)}>update</Button>
                  )}
                {metadata.isInstalled ? (
                  <Button isLoading={isLoading} onClick={disableModule}>
                    stop application
                  </Button>
                ) : (
                  <Button
                    isLoading={isLoading}
                    onClick={enableModule}
                    $variant="secondary"
                  >
                    launch application
                  </Button>
                )}
              </Inline>
            </Inline>
          </Stack>
        </Stack>
      )}
    </Modal>
  );
};

export default ModuleDetails;
