import { Environment, RPCInterface, RPCEventData } from "../../";

import ModuleProcessor from "./moduleProcessor";

export type WebModuleFormat = {
  rootFileContent: string;
  resources: Map<string, Uint8Array>;
};

export default class WebModuleMatrix extends Environment {
  installedModules = new Map<string, { url: string; window: Window }>();
  moduleContainer = document.getElementById("modules") as HTMLDivElement;

  constructor(rpcInterface: RPCInterface) {
    super(rpcInterface);

    window.addEventListener("message", this.onMessage);

    const observer = new MutationObserver(
      (mutationList: MutationRecord[], observer: MutationObserver) => {
        for (const mutation of mutationList) {
          if (mutation.type === "childList") {
            this.onChildrenChange();
          }
        }
      }
    );

    observer.observe(this.moduleContainer, { childList: true });

    window.addEventListener("beforeunload", () => {
      // need to close all other windows
    });
  }

  private onChildrenChange() {
    for (const [address, { url }] of Array.from(
      this.installedModules.entries()
    )) {
      if (!document.getElementById(address)) {
        this.popoutModule(address, url);
      }
    }
  }

  private onMessage = (event: MessageEvent) => {
    const eventType = event.data.eventType;
    const eventTarget = event.data.eventTarget;

    if (!eventType) return;

    // technically we could use channel messaging
    // but not necessarily required with event source validation

    const sourceModule = Array.from(this.installedModules.entries()).find(
      ([_, { window: contentWindow }]) =>
        contentWindow.location.href === (event.source as Window).location.href
    );

    if (!sourceModule) return;

    if (!eventTarget) {
      switch (eventType) {
        case "rpc":
          return this.handleRPC(
            sourceModule[0],
            sourceModule[1].window,
            event.data.rpcId,
            {
              target: event.data.target,
              method: event.data.method,
              params: event.data.params,
              resultPath: event.data.resultPath,
            }
          );
        case "updateIntent":
          return this.emit("intentUpdate", {
            identity: event.data.identity,
            intent: event.data.intent,
            intentStatus: event.data.intentStatus,
            sourceModuleAddress: sourceModule[0],
          });
        case "contactPeer":
          return this.emit("contactPeer", {
            identity: event.data.identity,
            intents: event.data.intents,
            sourceModuleAddress: sourceModule[0],
          });
        case "notification":
          return this.emit("notification", {
            source: event.data.source, // module name...
            title: event.data.title,
            description: event.data.description,
          });
        case "sendTo":
          return this.emit("peerMessage", {
            identity: event.data.identity,
            sourceModuleAddress: sourceModule[0],
            messageType: event.data.messageType,
            message: event.data.message,
          });
        default:
          return this.broadcast(
            `${sourceModule[0]}-${eventType}`,
            event.data.message
          );
      }
    }

    const targetModule = Array.from(this.installedModules.entries()).find(
      ([address]) => address === eventTarget
    );

    if (!targetModule) return;

    targetModule[1].window.postMessage({
      eventType,
      source: sourceModule[0],
      ...event.data,
    });
  };

  private handleRPC(
    source: string,
    contentWindow: Window,
    rpcId: string,
    { target, method, params, resultPath }: RPCEventData
  ) {
    this.rpcInterface(source, target, method, params, resultPath)
      .then((result) => {
        contentWindow.postMessage({
          rpcId,
          result: JSON.parse(JSON.stringify(result)),
        });
      })
      .catch((e) => {
        contentWindow.postMessage({
          rpcId,
          error: e,
        });
      });
  }

  parseModule({
    rootFileContent,
    resources,
  }: WebModuleFormat): Promise<Uint8Array> {
    return Promise.resolve(
      new ModuleProcessor().processFiles(rootFileContent, resources)
    );
  }

  renderPopoutIcon(address: string): HTMLButtonElement {
    const iconSvg = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "svg"
    );
    const iconPath1 = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "path"
    );
    const iconPath2 = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "path"
    );
    const iconRect = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "rect"
    );

    iconSvg.setAttribute("fill", "orange");
    iconSvg.setAttribute("viewBox", "0 0 36 36");
    iconSvg.setAttribute("width", "48px");
    iconSvg.setAttribute("height", "48px");

    iconPath1.setAttribute(
      "d",
      "M27,33H5a2,2,0,0,1-2-2V9A2,2,0,0,1,5,7H15V9H5V31H27V21h2V31A2,2,0,0,1,27,33Z"
    );
    iconPath2.setAttribute(
      "d",
      "M18,3a1,1,0,0,0,0,2H29.59L15.74,18.85a1,1,0,1,0,1.41,1.41L31,6.41V18a1,1,0,0,0,2,0V3Z"
    );
    iconRect.setAttribute("x", "0");
    iconRect.setAttribute("y", "0");
    iconRect.setAttribute("width", "36");
    iconRect.setAttribute("height", "36");
    iconRect.setAttribute("fill-opacity", "0");

    iconSvg.appendChild(iconPath1);
    iconSvg.appendChild(iconPath2);
    iconSvg.appendChild(iconRect);

    const button = document.createElement("button");

    button.addEventListener("click", () => {
      const element = document.getElementById(address)?.parentElement;
      if (!element) return;
      element.remove();
    });

    button.appendChild(iconSvg);

    return button;
  }

  private popoutModule(address: string, url: string) {
    const contentWindow = window.open(url, "_blank") as Window;

    this.installedModules.set(address, {
      url: url,
      window: contentWindow,
    });

    const checkWindowClosed = setInterval(() => {
      if (contentWindow.closed) {
        if (this.installedModules.get(address)) {
          this.iframeModule(address, url);
        }
        clearInterval(checkWindowClosed);
      }
    }, 1000);
  }

  private iframeModule(address: string, url: string) {
    const moduleWrapper = document.createElement("div");
    const moduleOverlay = document.createElement("div");
    const moduleFrame = document.createElement("iframe");

    moduleFrame.src = url;
    moduleFrame.id = address;

    moduleOverlay.classList.add("overlay");
    moduleWrapper.append(moduleFrame);
    moduleWrapper.append(this.renderPopoutIcon(address));
    moduleWrapper.append(moduleOverlay);

    if (this.moduleContainer.children.length === 0) {
      moduleWrapper.classList.add("active");
    }

    this.moduleContainer.append(moduleWrapper);

    if (!moduleFrame.contentWindow) throw new Error("error opening module");

    this.installedModules.set(address, {
      url,
      window: moduleFrame.contentWindow,
    });
  }

  registerModule(address: string, moduleData: Uint8Array | string) {
    const url =
      typeof moduleData === "string"
        ? moduleData
        : new ModuleProcessor().parseModule(moduleData);

    if (document.getElementById(address)) return;

    this.iframeModule(address, url);

    this.announceModuleChange();
  }

  stopModule(address: string) {
    const module = this.installedModules.get(address);
    const contentWindow = module?.window;
    const element = document.getElementById(address)?.parentElement;

    if (module) {
      this.installedModules.delete(address);
    }

    if (element) {
      element.remove();
    }

    if (contentWindow) {
      contentWindow.close()
    }

    this.announceModuleChange();
  }

  notify(target: string, eventType: string | number, eventData: any) {
    const module = this.installedModules.get(target);
    if (!module) return;

    return module.window.postMessage({
      eventType,
      eventData: JSON.parse(JSON.stringify(eventData)),
    });
  }

  // TODO: use broadcast channels to force single entity
  broadcast(eventType: string | number, data: string) {
    for (const { window: contentWindow } of Array.from(
      this.installedModules.values()
    )) {
      contentWindow.postMessage({
        eventType,
        eventData: JSON.parse(JSON.stringify(data)),
      });
    }
  }
}
