import { useState, useEffect, useContext } from "react";
import { EventMap } from "../../rzr";
import RzR from '../../contexts/RzR'

// This hook is designed to prevent unnecessary rerenders over quick periods of time
// when a value changes, we will basically wait for 250ms
// capture the events but don't necessarily react on them
const useHighFrequencyEvent = <K extends keyof EventMap, T = EventMap[K]>(
  event: K,
  initialValue: T,
  initializer?: () => Promise<T>
): [T, (v: T) => void] => {
  const rzr = useContext(RzR)
  const [value, _setValue] = useState<T>(initialValue);

  // TODO: watch out for reference changes, this will only work on primitives

  useEffect(() => {
    let lastSetValue: EventMap[K] | null = null;
    let lastValue: EventMap[K] | null = null;
    let isOpen = true;

    const setValue = (input: EventMap[K]) => {
      if (isOpen) {
        if (lastValue !== input) _setValue(input as unknown as T);
        lastSetValue = input;
        isOpen = false;

        setTimeout(() => {
          if (lastValue !== lastSetValue) _setValue(lastValue as unknown as T);
          isOpen = true;
        }, 1000);
      }
      lastValue = input;
    };

    rzr.addEventListener(event, setValue);
    return () => {
      rzr.removeEventListener(event, setValue);
    };
  }, [event]);

  useEffect(() => {
    if (initializer) {
      initializer().then(_setValue);
    }
  }, [initializer]);

  return [value, _setValue];
};

export default useHighFrequencyEvent;
