import {
  concat,
  getBytes,
  toNumber,
  dataSlice,
  sha256,
  hexlify,
  toBeArray,
  zeroPadValue,
} from "ethers";
import EventEmitter from "./EventEmitter";
import { Schema, AppliedSchema } from "./types";
import constants from "./constants";

export const ec = new TextEncoder();
export const dc = new TextDecoder();

export class ByteLengthViolation extends Error {
  constructor(message: string) {
    super(message);
    this.name = "ByteLengthViolation";
  }
}

export const formatBytes = (bytes: BigInt) => {
  let n = Number(bytes);
  if (n < 1024) {
    return `${n} bytes`;
  }
  n = Math.round((n / 1024) * 100) / 100;
  if (n < 1024) {
    return `${n} kb`;
  }
  n = Math.round((n / 1024) * 100) / 100;
  if (n < 1024) {
    return `${n} mb`;
  }
  n = Math.round((n / 1024) * 100) / 100;
  if (n < 1024) {
    return `${n} gb`;
  }
  n = Math.round((n / 1024) * 100) / 100;
  return `${n} tb`;
};

export const numberToTimeString = (n: number): string => {
  if (n < 0) return "0m 0s";
  const milliseconds = n % 1000;
  n -= milliseconds;
  n /= 1000;
  const seconds = n % 60;
  n -= seconds;
  if (n === 0) {
    return `0m ${seconds}s`;
  }
  n /= 60;
  const minutes = n % 60;
  n -= minutes;
  if (n === 0) {
    return `${minutes}m ${seconds}s`;
  }
  n /= 60;
  const hours = n % 60;
  n -= hours;
  if (n === 0) {
    return `${hours}h ${minutes}m`;
  }
  n /= 60;
  const days = n % 24;
  return `${days}d ${hours}h ${minutes}m`;
};

export const formatAddress = (address: string, n: number = 3): string =>
  `${address.slice(0, 2 + n)}...${address.slice(-n)}`;

export const formatHex = (address: string, n: number = 3): string =>
  `${address.slice(0, 2 + n)}...${address.slice(-n)}`;

const timer = (timeout: number): Promise<undefined> =>
  new Promise((r) => setTimeout(r, timeout));

export const watch = async <T>(
  watcher: () => T | null | Promise<T | null>,
  t = 250,
  maxRetries = 1000
): Promise<T> => {
  const value = await watcher();

  if (value !== null) {
    return value;
  }

  if (maxRetries === 0) throw new Error("failed");

  await timer(t * 1.1);

  return watch(watcher, t, maxRetries - 1);
};

export const getDistance = (a: Uint8Array, b: Uint8Array): number => {
  let sum = 0;

  for (let i = 0; i < a.length; ++i) {
    sum += Math.abs(a[0] - b[0]) << (a.length - i);
  }

  return sum;
};

export const findClosestAddress = <T extends { address: string }>(
  targetAddress: string,
  entitiesWithAddress: T[],
  exceptions: T[] = []
): T => {
  const toComparable = (address: string) => getBytes(address).slice(0, 10);

  const toCompare = toComparable(targetAddress);

  let minDistance = -1;
  let minEntityWithAddress = entitiesWithAddress[0];

  const exceptionAddresses = exceptions.map((e) => e.address);

  entitiesWithAddress.forEach((entityWithAddress) => {
    if (exceptionAddresses.includes(entityWithAddress.address)) return;
    const d = getDistance(toCompare, toComparable(entityWithAddress.address));
    if (minDistance === -1 || d < minDistance) {
      minDistance = d;
      minEntityWithAddress = entityWithAddress;
    }
  });

  return minEntityWithAddress;
};

export function validateByteLength(
  data: ArrayBuffer,
  targetLength: number,
  minOnly = false
): void {
  if (minOnly && data.byteLength < targetLength) return;
  if (data.byteLength !== targetLength)
    throw new ByteLengthViolation(
      `data byte length verification falied. Target byte length: ${targetLength} Received byte length: ${data.byteLength}`
    );
}

export function splitByLength(
  length: number,
  data: ArrayBuffer
): ArrayBuffer[] {
  const dataArray = [];

  let i = 0;
  do {
    const d = data.slice(i * length, (i + 1) * length);
    if (d.byteLength) dataArray.push(d);
  } while (++i * length < data.byteLength);

  return dataArray;
}

const removePadding = (input: Uint8Array) => {
  let i = 0;

  while (input[i] === 0 && i++ < input.length);

  return input.slice(i);
};

export function applySchema<T extends AppliedSchema>(
  schema: Schema,
  data: ArrayBuffer
): T {
  const result: AppliedSchema = {};
  let needle = 0;

  for (const key in schema) {
    const s = schema[key];

    const dataEnd =
      s.type === "boolean"
        ? needle + 1
        : s.byteLength
        ? needle + s.byteLength
        : data.byteLength;

    const value = data.slice(needle, dataEnd);

    if (s.type === "boolean") {
      result[key] = !!new Uint8Array(value)[0];
    } else if (s.type === "string") {
      result[key] = dc.decode(removePadding(new Uint8Array(value)));
    } else if (s.type === "number" && s.byteLength) {
      result[key] = toNumber(new Uint8Array(value));
    } else if (s.type === "buffer") {
      result[key] = new Uint8Array(value);
    } else if (s.type === "hexstring") {
      result[key] = hexlify(new Uint8Array(value));
    } else if (s.type === "bignumber") {
      result[key] = BigInt(hexlify(new Uint8Array(value)));
    } else if (s.type === "repeating") {
      result[key] = [];
      if (!s.schema) throw new Error(`no schema specified for ${key}`);
      while (needle < data.byteLength) {
        const fullByteLength = Object.keys(s.schema).reduce(
          (sum, k) => sum + ((s.schema as Schema)[k].byteLength as number),
          0
        );
        const end = needle + fullByteLength;
        (result[key] as Array<AppliedSchema>).push(
          applySchema(s.schema, data.slice(needle, end))
        );
        needle = end;
      }
    }

    needle = dataEnd;
  }

  return result as T;
}

export function buildSchema(schema: Schema, _data: AppliedSchema): ArrayBuffer {
  let result = new Uint8Array();
  const data = { ..._data };

  for (const key in schema) {
    if (!(key in data)) throw new Error(`Missing data: ${key}`);
    const s = schema[key];
    const d = data[key];

    if (s.type === "boolean") {
      result = getBytes(concat([result, new Uint8Array([d ? 1 : 0])]));
    } else if (s.type === "string") {
      const bytes = ec.encode(d as string);
      result = getBytes(
        concat([result, zeroPadValue(bytes, s.byteLength ?? bytes.length)])
      );
    } else if (s.type === "number" && s.byteLength) {
      if (typeof d !== "number") {
        throw new Error(`Wrong datatype for ${key}. Expected number.`);
      }

      // not sure why strict type is required
      result = getBytes(
        concat([result, zeroPadValue(toBeArray(d as number), s.byteLength)])
      );
    } else if (s.type === "bignumber" && s.byteLength) {
      // not sure why strict type is required
      result = getBytes(
        concat([result, zeroPadValue(toBeArray(d as number), s.byteLength)])
      );
    } else if (s.type === "buffer" || s.type === "hexstring") {
      const _d = getBytes(d as string | Uint8Array);

      // arraybuffer type, check for byte length
      if (s.byteLength) validateByteLength(_d.buffer, s.byteLength);

      result = getBytes(concat([result, _d]));
    } else if (s.type === "repeating") {
      result = getBytes(
        concat([
          result,
          getBytes(
            concat(
              (d as Array<AppliedSchema>).map(
                (_d) => new Uint8Array(buildSchema(s.schema as Schema, _d))
              )
            )
          ),
        ])
      );
    } else {
      throw new Error(`Unprocessable schema type: ${s.type}`);
    }

    delete data[key];
  }

  if (Object.keys(data).length) {
    throw new Error(`invalid keys supplied to schema: ${Object.keys(data)}`);
  }

  return result.buffer;
}

// can be improved to actually return the values...
export const sequentialProcess = <T>(
  inputs: T[],
  f: (event: T) => Promise<any>
) =>
  inputs.reduce(
    (previousPromise: Promise<any>, input) =>
      previousPromise.then(() => f(input)),
    Promise.resolve(true)
  );

export const once = <EventMap, K extends keyof EventMap>(
  module: EventEmitter<EventMap>,
  event: K,
  config?: {
    precondition?: (result: EventMap[K]) => boolean;
    timeoutIn?: number;
    silent?: boolean;
  }
) =>
  new Promise<EventMap[K]>((resolve, reject) => {
    let resolved = false;
    const resolveAndFulfil = (result: EventMap[K]) => {
      if (config?.precondition && !config.precondition(result)) return false;

      resolve(result);

      resolved = true;

      return true;
    };

    module.addEventListener(event, resolveAndFulfil);

    if (config?.timeoutIn) {
      timer(config.timeoutIn).then(() => {
        if (!resolved) {
          module.removeEventListener(event, resolveAndFulfil);
          if (config?.silent !== true) reject("timeout");
        }
      });
    }
  });

function getModuleContentSeek(moduleContent: Uint8Array): number {
  const startAt = getMetadataEnd(moduleContent);

  const abiLength = toNumber(
    getBytes(dataSlice(moduleContent, startAt, startAt + 4))
  );

  return startAt + 4 + abiLength;
}

export function digestModule(moduleContent: Uint8Array): string {
  return sha256(moduleContent.slice(getModuleContentSeek(moduleContent)));
}

export function hasContent(moduleContent: Uint8Array): boolean {
  return getModuleContentSeek(moduleContent) < moduleContent.length;
}

export function getMetadataEnd(moduleContent: Uint8Array): number {
  return 1024 + moduleContent[0] * constants.HASH_BYTE_LENGTH;
}

export function resolveInSequence<T>(
  inputArray: T[],
  fn: (input: T) => Promise<void>
) {
  if (inputArray.length === 0) return Promise.resolve(true);
  fn(inputArray[0]).then(() => resolveInSequence(inputArray.slice(1), fn));
}

export function resolveDependencies(
  depTree: { [key: string]: string[] },
  key = Object.keys(depTree)[0],
  resolved: Set<string> = new Set(),
  unresolved: Set<string> = new Set(),
  rootIndex: number | null = 0
): string[] {
  if (!depTree[key]) {
    return Array.from(resolved);
  }

  unresolved.add(key);

  if (!resolved.has(key)) {
    for (const dep of depTree[key]) {
      if (!resolved.has(dep)) {
        if (unresolved.has(dep))
          throw new Error(`Circular dependency detected: ${key} -> ${dep}`);
        resolveDependencies(depTree, dep, resolved, unresolved, null);
      }
    }
  }
  unresolved.delete(key);

  resolved.add(key);

  if (rootIndex !== null && resolved.size !== Object.keys(depTree).length) {
    return resolveDependencies(
      depTree,
      (key = Object.keys(depTree)[rootIndex++]),
      resolved,
      unresolved,
      rootIndex
    );
  }

  return Array.from(resolved);
}

