import { useState, useEffect, useContext } from "react";
import { EventMap } from "../rzr";
import RzRContext from '../contexts/RzR'

const useWatchRzREvent = <K extends keyof EventMap, T = EventMap[K]>(
  event: K,
  initialValue: T,
  mutator: null | ((input: EventMap[K]) => T) = (entity) =>
    entity as unknown as T,
  // ^^ this is terrible...
  initializer?: () => Promise<T>
): [T, (v: T) => void] => {
  const rzr = useContext(RzRContext)
  const [value, _setValue] = useState<T>(initialValue);

  // TODO: watch out for reference changes, this will only work on primitives

  useEffect(() => {
    const setValue = (input: EventMap[K]) => {
      _setValue(mutator ? mutator(input) : (input as unknown as T));
    };

    rzr.addEventListener(event, setValue);
    return () => {
      rzr.removeEventListener(event, setValue);
    };
  }, [event, mutator]);

  useEffect(() => {
    if (initializer) {
      initializer().then(_setValue);
    }
  }, [initializer]);

  return [value, _setValue];
};

export default useWatchRzREvent;
