import { AuthenticationBackend, AuthorizationSignature } from "../../";
import {
  Wallet,
  randomBytes,
  hexlify,
  toBeHex,
  toBeArray,
  getBytes,
  verifyMessage,
  zeroPadValue,
  concat,
} from "ethers";

export default class WebAuthentication extends AuthenticationBackend {
  private worker: Worker;

  constructor() {
    super();

    this.worker = new Worker("/authworker.js");
  }

  initialize(identity: Uint8Array) {
    return new Promise<boolean>((r) => {
      const listener = () => {
        r(true);

        this.worker.removeEventListener("message", listener);
      };

      this.worker.addEventListener("message", listener);

      this.worker.postMessage(identity);
    });
  }

  authorize(identity: string): Promise<AuthorizationSignature> {
    return new Promise((r) => {
      const listener = (event: MessageEvent) => {
        if (`0x${event.data[0]}` !== identity) return;
        r({ timestamp: event.data[1], signature: event.data[2] });

        this.worker.removeEventListener("message", listener);
      };

      this.worker.addEventListener("message", listener);

      this.worker.postMessage(getBytes(identity));
    });
  }
}
