// an intent is an id for a thing to do within the module itself
// don't think of it as anything specific, it's simply a flag that's designed to indicate
// a task currently being processed by the peer
//
// intent updates are now async though
//
// possible discovery intents
//  -> I'm looking for a specific fragment of a file (4MB blocks)
//  -> I'm looking for news stories with the following topics
//  -> I'm looking for sales
//  -> I'm looking for a peer (p2p routing module)
//
// the discovery sequence provides an interface to share any information through the p2p network
//
// content moderation isn't really possible, kind of the whole point, the barrier of entry and not-easy to use is actually a feature here
//
// technically intents are the only interface, modules should be allowed to update on a peer
//
// a message should be
// peer identity, intent Id, intent status
// that should cover everything, and does not even warrant a return value
//
// the rest of the interfaces should be generic RPC calls to submodules
// but technically, they are not to be interacted with.
// sales are NOT a part of the core rzr module....
// nor are games, gamesData, or signatures for that matter
// only players, identities, events, routers
// all the rest belong to their own specific module
//
// need to handle use-case where module is not installed for player
//
// speaking of which, given that at most 16 modules can be installed at once, with one identity, it's ok
// every time a new identity is generated you can announce your installed modules,
// those modules MUST stay installed for the lifetime of the identity, if you don't like it, create a new identity, how to try out a module though then? <-- this needs more time
// makes discovery a lot more efficient and data integrity algorithms more sophisticated
//
// don't forget, modules can even be shared p2p! it's just a bunch of javascript, we can even store it in indexeddb
//
// signature requests are async anyway, rpc is not a problem there

// ONLY ONE PEER WITH INTENT GAME IS ALLOWED, why?
//
// matrix view of all data received from peers and sockets
// actually I could simulate rzr sockets by running several routers on local machine with different paths

export * from "./messageTypes";

export type CommunicationProtocols = {
  WebRTC: null | typeof Protocol;
  // WebSocket: typeof Comm;
};

export const supportedProtocols: CommunicationProtocols = {
  WebRTC: null,
  // WebSocket: Comm,
};

export enum IntentStatus {
  REQUESTED = 1,
  IN_PROGRESS,
  COMPLETED,
}

export const decodeIntentRequest = (
  encodedIntent: Array<{ intent: number }>
): number[] => encodedIntent.map(({ intent }) => intent);

export type PeerIntents = Map<number, IntentStatus>;

export const encodeIntents = (
  intents: PeerIntents
): Array<{ intent: number }> => {
  const encodedIntents = [];

  for (const [intentKey, intentStatus] of Array.from(intents.entries())) {
    if (intentStatus === IntentStatus.REQUESTED) {
      encodedIntents.push({ intent: intentKey });
    }
  }

  return encodedIntents;
};

export const setCommunicationProtocols = (
  _supportedProtocols: CommunicationProtocols
) => {
  supportedProtocols.WebRTC = _supportedProtocols.WebRTC;
};

enum ConnectionStatus {
  IDLE = 0,
  CONNECTING,
  AUTHENTICATING,
  READY,
}

// consider new event for specifically disconnects
// and have reason as parameter
// -> size limit reached (careful when playing game)
// -> intents fulfiled
// -> connection closed
export type PeerConnectionEventMap = { [k in PeerMessageType]: Uint8Array } & {
  connectionStatusChanged: boolean;
  peerAuthorized: boolean;
  intentsFulfilled: boolean;
  sendingMessageStatus: boolean;
  receivingMessageStatus: boolean;
  [k: number]: Uint8Array;
};

// any peer is ready for data transfer
// MessageSchemas somehow need to be extendable by external modules
// both game and RzRSync are external modules
// also coupled with intents
// intent definition contains whether peer is considered permanent or not

export class PeerConnection extends LogEmitter<PeerConnectionEventMap> {
  transferredBytes = 0;
  receivedBytes = 0;
  identity: string;
  private protocol: Protocol | null = null;
  router?: SignalServerConnection;
  private connectionStatus = ConnectionStatus.IDLE;

  private modules: {
    ethereum: Ethereum;
    rzrDB: RzRDB;
    clock: Clock;
    authentication: Authentication;
  };

  intents: PeerIntents = new Map();

  get isConnected() {
    if (!this.protocol) return false;
    return this.protocol.isConnected;
  }

  get isAuthorized() {
    return this.connectionStatus === ConnectionStatus.READY;
  }

  get intentsFulfilled() {
    return Array.from(this.intents.values()).every(
      (status) => status === IntentStatus.COMPLETED
    );
  }

  get registeredIntents() {
    return Array.from(this.intents.keys());
  }

  get hasProtocol() {
    return this.protocol !== null;
  }

  constructor(
    log: Log,
    clock: Clock,
    rzrDB: RzRDB,
    ethereum: Ethereum,
    authentication: Authentication,
    identity: string,
    intents: number[] // not sure if this should be necessary, should be ok to add intents during lifecycle
  ) {
    super(log, `Peer (${identity})`);

    this.identity = identity;

    this.addIntents(intents);

    this.modules = {
      clock,
      ethereum,
      rzrDB,
      authentication,
    };
  }

  private setProtocol(encodedSupportedProtocols: number): Protocol {
    if ((encodedSupportedProtocols & 0b10) === 0 || !supportedProtocols.WebRTC)
      throw new Error("connection not available");

    if (!this.router) throw new Error("router not set");

    this.protocol = new supportedProtocols.WebRTC(
      this.router,
      this.identity
    ) as Protocol;

    this.protocol.addEventListener("newMessage", this.onMessage);

    this.protocol.addEventListener(
      "connectionStatusChanged",
      (isConnected: boolean) => {
        this.log(
          `${this.isConnected ? "connected to" : "disconnected from"} ${
            this.identity
          }`
        );

        if (isConnected) {
          // reauthenticate
          this.connectionStatus = ConnectionStatus.AUTHENTICATING;
          this.modules.authentication
            .authorize(this.identity)
            .then((authorization) =>
              this.rawSend(buildSchema(authorizationSchema, authorization))
            );
        } else {
          this.connectionStatus = ConnectionStatus.IDLE;
          this.fulfilNonEssentialIntents();
          this.protocol = null;
        }

        this.emit("connectionStatusChanged", isConnected);
        // TODO: data sending should happen on authorization not connection, this is only to set up listeners
      }
    );

    return this.protocol;
  }

  private authenticate(data: ArrayBuffer) {
    try {
      if (
        this.modules.authentication.verifyAuthorization(
          this.identity,
          applySchema(authorizationSchema, data)
        )
      ) {
        this.connectionStatus = ConnectionStatus.READY;

        // give a little time for listeners to register
        this.modules.clock
          .timer(1)
          .then(() => this.emit("peerAuthorized", true));
      }
    } catch (e) {
    } finally {
      // dropping all data that arrives before auth
      return null;
    }
  }

  private onMessage = (data: ArrayBuffer | string) => {
    // protocol should make sure not to accept too large data
    // also disconnects after 5MB or something
    if (typeof data === "string") return;

    if (this.connectionStatus < ConnectionStatus.READY) {
      this.authenticate(data);
      return;
    }

    const _data = new Uint8Array(data);

    if (!_data) return;

    this.receivedBytes += data.byteLength;

    const moduleId = Number(
      BigInt(hexlify(_data.slice(0, constants.MODULE_ID_BYTE_LENGTH)))
    );

    // distinguish internal modules
    const offset = moduleId === 0 ? 4 : 3;
    const messageType = moduleId === 0 ? _data[offset - 1] : moduleId;
    const set = this.listeners[messageType];

    if (moduleId === 0 && messageType === 0) {
      // dealing with intent registration
      let i = 4;
      while (i < _data.length) {
        this.addIntents([toNumber(_data.slice(i, i + 4))]);

        i += 4;
      }
    }

    if (!set) return;

    this.emit("receivingMessageStatus", true);

    set.forEach((listener) => listener(_data.slice(offset)));

    this.emit("receivingMessageStatus", false);
  };

  rawSend(message: Uint8Array | ArrayBuffer) {
    this.transferredBytes += 1 + (message ? message.byteLength : 0);

    if (!this.protocol) throw new Error("protocol not available");

    this.emit("sendingMessageStatus", true);

    try {
      this.protocol.send(message);
    } catch (e) {
    } finally {
      this.emit("sendingMessageStatus", false);
    }
  }

  send<K extends keyof MessageSchemas>(
    messageType: K,
    data?: MessageSchemas[K]
  ) {
    const message = new Uint8Array(
      data ? buildSchema(messageSchemas[messageType], data) : []
    );

    return this.rawSend(
      getBytes(
        concat([
          // module id is 0 in this case
          new Uint8Array(constants.MODULE_ID_BYTE_LENGTH),
          new Uint8Array([messageType]),
          message,
        ])
      )
    );
  }

  addIntents(intents: number[]) {
    for (const intent of intents) {
      this.intents.set(
        intent,
        IntentStatus.REQUESTED || this.intents.get(intent)
      );
    }
  }

  private fulfilNonEssentialIntents() {
    for (const [intent, intentStatus] of Array.from(this.intents.entries())) {
      if (intentStatus !== IntentStatus.COMPLETED && (intent & 0xff) < 0xc0) {
        this.updateIntent(intent, IntentStatus.COMPLETED);
      }
    }
  }

  updateIntent(intent: number, intentStatus: IntentStatus) {
    this.intents.set(intent, intentStatus);

    this.log(
      `intent ${intent > 255 ? toBeHex(intent) : intent} ${
        intentStatus === IntentStatus.REQUESTED
          ? "requested"
          : intentStatus === IntentStatus.IN_PROGRESS
          ? "processing has begun"
          : "completed"
      }`
    );

    if (this.intentsFulfilled) {
      this.emit("intentsFulfilled", true);
    }
  }

  ping(router: SignalServerConnection, encodedSupportedProtocols: number) {
    // politeness logic
    if (
      this.connectionStatus > ConnectionStatus.IDLE &&
      toNumber(dataSlice(this.modules.ethereum.identity as string, 5, 8)) >
        toNumber(dataSlice(this.identity, 5, 8))
    ) {
      this.log("dropping connection to be polite");
      debugger;
      return;
    }

    this.connectionStatus = ConnectionStatus.CONNECTING;

    if (this.protocol) {
      this.protocol.closeConnection();
    }

    this.router = router;

    const protocol = this.setProtocol(encodedSupportedProtocols);

    this._log(
      `initiating connection using protocol ${protocol.protocolId}`,
      protocol
        .createOffer()
        .then((offerData) =>
          router.sendOffer({
            identity: this.identity,
            protocolId: protocol.protocolId,
            offerData,
          })
        )
        .then((answerSchema) => {
          if (!answerSchema) throw new Error('no reply')

          return protocol.processAnswer(answerSchema.answer)
        })
    );
  }

  reset(removeAllListeners = true) {
    if (this.protocol) this.protocol.reset();
    if (removeAllListeners) this.removeAllListeners();
  }

  initiateConnection() {
    this.connectionStatus = ConnectionStatus.CONNECTING;
  }

  establishConnection(router: SignalServerConnection, offer: OfferSchema) {
    if (this.protocol || this.connectionStatus !== ConnectionStatus.CONNECTING)
      return;

    this.router = router;

    const protocol = this.setProtocol(offer.protocolId);

    return this._log(
      "estabilishing connection",
      protocol.processOffer(offer.offerData)
    ).then((answer) => {
      router.sendAnswer({
        identity: this.identity,
        answer,
      });
    });
  }
}
