import { dataSlice, Log, EventLog } from "ethers";
import { Ethereum, Events, Transactions, NewIdentityEvent } from "../../lvl2";
import { RzRDB, DataType } from "../../lvl1";
import { Settings } from "../../lvl3";
import { Clock, Log as LogModule, LogEmitter } from "../../lvl0";
import { applySchema, sequentialProcess, once } from "../../../utils";
import { Router, ConnectivitySettings } from "../../../types";

// This, connectivity count are both dynamic
// and should change according to network conditions
const MAX_SYNC_THREADS = 1;

// this figure should fluctuate based on the number of data points it's processing
const VERIFICATION_RATE = 1;

export type RzRSyncEventMap = {
  syncStatusChanged: boolean;
  downstreamStatusChanged: number;
  upstreamStatusChanged: number;
};

// All workflows related to data synchronization
export class RzRSync extends LogEmitter<RzRSyncEventMap> {
  blockHeight = 0;

  private lastOnChainSync: number = 0;
  private isSyncing = false;

  private modules: {
    clock: Clock;
    rzrDB: RzRDB;
    ethereum: Ethereum;
    settings: Settings;
  };


  constructor(
    log: LogModule,
    clock: Clock,
    rzrDB: RzRDB,
    ethereum: Ethereum,
    settings: Settings
  ) {
    super(log, "RzRSync");

    this.modules = {
      clock,
      rzrDB,
      ethereum,
      settings,
    };
  }

  private storeEvent = (event: EventLog) =>
    this.modules.rzrDB.storeEvent(event.blockNumber, event.transactionHash);

  private onNewIdentity = ({
    event,
    account,
    identity,
    previousIdentity,
  }: NewIdentityEvent) => {
    this.modules.rzrDB.storePlayer({
      address: account,
      identity: identity,
      blockNumber: event.blockNumber,
      previousIdentity: previousIdentity,
    });
  };

  syncOnChainData = async () => {
    if (this.isSyncing) return;
    this.isSyncing = true;
    this.emit("syncStatusChanged", true);
    const latestBlock = await this.modules.rzrDB.loadLatestBlock();

    let highestBlock = latestBlock;

    const processEvent = (
      event: EventLog | Log,
      processor: (eventArgs: readonly string[]) => Promise<any>
    ) => {
      if (!event.topics) return Promise.resolve(null);
      if (event.blockNumber > highestBlock) highestBlock = event.blockNumber;
      return this.modules.rzrDB
        .hasEventBeenProcessed(event.transactionHash)
        .then((hasBeenProcessed) =>
          hasBeenProcessed ? null : processor(event.topics)
        );
    };

    const processNewIdentityEvent = (event: EventLog | Log) =>
      processEvent(event, (data) =>
        this.modules.rzrDB.storePlayer({
          previousIdentity: event.data,
          address: dataSlice(data[1], 12).toLowerCase(),
          identity: dataSlice(data[2], 12).toLowerCase(),
          blockNumber: event.blockNumber,
        })
      );

    const processApplicationRegistrationEvent = (event: EventLog | Log) =>
      processEvent(event, (data) =>
        this.modules.rzrDB.storeModuleRegistration({
          contentHash: event.data,
          address: dataSlice(data[1], 12).toLowerCase(),
          owner: dataSlice(data[2], 12).toLowerCase(),
          blockNumber: event.blockNumber,
        })
      );

    const processNewIdentityEvents = (events: EventLog[] | Log[]) =>
      sequentialProcess(events, processNewIdentityEvent);

    const processApplicationRegistrationEvents = (events: EventLog[] | Log[]) =>
      sequentialProcess(events, processApplicationRegistrationEvent);

    await this._log(
      "synchronizing on chain data",
      Promise.all([
        this.modules.ethereum
          .getNewIdentityEvents(latestBlock + 1)
          .then(processNewIdentityEvents),
        this.modules.ethereum
          .getApplicationRegistrationEvents(latestBlock + 1)
          .then(processApplicationRegistrationEvents),
      ])
    );

    this.lastOnChainSync = this.modules.clock.now;
    this.blockHeight = highestBlock;

    const timeoutIn = this.modules.settings.getSetting(
      "connectivity",
      "syncFrequency"
    ) * 1000;

    return this.modules.rzrDB
      .storeLatestBlock(highestBlock)
      .then(() => {
        this.emit("syncStatusChanged", false);
      })
      .finally(() => {
        this.isSyncing = false;
        Promise.race([
          once(this.modules.ethereum, Events.NEW_IDENTITY, {
            timeoutIn,
          }),
          once(this.modules.ethereum, Transactions.NEW_IDENTITY, {
            timeoutIn,
          }),
        ])
          .then(this.syncOnChainData)
          .catch(this.syncOnChainData);
      });
  };
}
