import { useState, useEffect, useRef, useCallback, useContext } from "react";
import { sha256 } from "ethers";
import styled from "styled-components";
import {
  Modal,
  Inline,
  Textarea,
  CloseIcon,
  Stack,
  H2,
  H3,
  Label,
} from "rzr-components";
import ModuleProcessor from "../../rzr/modules/ModuleMatrix/moduleProcessor";
import { WebModuleFormat, ABI, ModuleMetadata } from "../../rzr";
import ModuleDetailsStep from "./Steps/ModuleDetails";
import ABIStep from "./Steps/ABI";
import ContractDetailsStep from "./Steps/ContractDetails";
import WelcomeStep from "./Steps/WelcomeStep";
import ModuleMetadataStep from "./Steps/ModuleMetadata";
import InstallModuleStep from "./Steps/InstallModule";
import RzR from '../../contexts/RzR'

enum InstallStep {
  WELCOME,
  ABI,
  CONTRACT_DETAILS,
  MODULE_DATA,
  MODULE_METADATA,
  INSTALL_MODULE,
}

const ModuleInstaller: React.FC<{ close: () => void }> = ({ close }) => {
  const rzr = useContext(RzR)
  const [installStep, setInstallStep] = useState<InstallStep>(
    InstallStep.WELCOME
  );
  const [abi, setAbi] = useState<ABI | null>(null);
  const [address, setAddress] = useState<string | null>(null);
  const [metadata, setModuleMetadata] = useState<ModuleMetadata | null>(null);
  const [moduleFiles, setModuleFiles] = useState<
    WebModuleFormat | null | string
  >(null);

  const applicationHash =
    moduleFiles === null || typeof moduleFiles === "string"
      ? ""
      : sha256(
          new ModuleProcessor().processFiles(
            (moduleFiles as WebModuleFormat).rootFileContent,
            (moduleFiles as WebModuleFormat).resources
          )
      );

  switch (installStep) {
    case InstallStep.WELCOME:
      return (
        <WelcomeStep
          onSuccess={() => setInstallStep(InstallStep.MODULE_DATA)}
        />
      );
    case InstallStep.MODULE_DATA:
      return (
        <ModuleDetailsStep
          onSuccess={(moduleFiles) => {
            setModuleFiles(moduleFiles);
            setInstallStep(InstallStep.ABI);
          }}
        />
      );
    case InstallStep.ABI:
      return (
        <ABIStep
          onPreviousStep={() => setInstallStep(InstallStep.MODULE_DATA)}
          abi={abi}
          onSuccess={(abi) => {
            setAbi(abi);
            setInstallStep(InstallStep.CONTRACT_DETAILS);
          }}
        />
      );
    case InstallStep.CONTRACT_DETAILS:
      return (
        <ContractDetailsStep
          applicationHash={applicationHash}
          onPreviousStep={() => setInstallStep(InstallStep.ABI)}
          onSuccess={(address) => {
            setAddress(address);
            setInstallStep(InstallStep.MODULE_METADATA);
          }}
          abi={abi as ABI}
        />
      );
    case InstallStep.MODULE_METADATA:
      return (
        <ModuleMetadataStep
          applicationHash={applicationHash || moduleFiles as string}
          metadata={metadata}
          onPreviousStep={() => setInstallStep(InstallStep.MODULE_DATA)}
          onSuccess={(metadata) => {
            setModuleMetadata({ ...metadata, contentHash: applicationHash });
            setInstallStep(InstallStep.INSTALL_MODULE);
          }}
        />
      );
    case InstallStep.INSTALL_MODULE:
      return (
        <InstallModuleStep
          onPreviousStep={() => setInstallStep(InstallStep.MODULE_METADATA)}
          abi={abi as ABI}
          address={address as string}
          metadata={metadata as ModuleMetadata}
          module={moduleFiles as WebModuleFormat}
          onSuccess={close}
        />
      );
  }

  return null;
};

const ModuleInstallerModal: React.FC<{ close: () => void }> = ({ close }) => (
  <Modal $size="md">
    <Inline $justify="space-between">
      <H2 $margin="md">Install a new module</H2>
      <CloseIcon onClick={close} />
    </Inline>
    <ModuleInstaller close={close} />
  </Modal>
);

// also need to collect ABI

export default ModuleInstallerModal;
