import {
  Page,
  Button,
  useModal,
  Inline,
  Toggle,
  Stack,
  DL,
  Tooltip,
} from "rzr-components";
import styled from "styled-components";
import { useState, useEffect, useContext } from "react";
import {
  Settings as SettingsType,
  utils as rzrUtils,
  ModuleMetadata,
} from "../../rzr";
import Fieldset from "./Fieldset";
import RevealIdentityModal from "../RevealIdentityModal";
import { BooleanField, NumericField } from "./Field";
import RzR from "../../contexts/RzR";

const Settings = () => {
  const rzr = useContext(RzR);
  const [settings, setSettings] = useState<SettingsType>(rzr.settings);
  const [activeModule, setActiveModule] = useState("");
  const [
    isRevealingIdentity,
    openRevealIdentityModal,
    closeRevealIdentityModal,
  ] = useModal();

  const openModule = (moduleName: string) => {
    rzr.loadModule(moduleName).then((module) => {
      if (!module) return;
    });
  };

  useEffect(() => {
    const copySettings = () => {
      setSettings({ ...rzr.settings });
    };

    rzr.addEventListener("settingsUpdated", copySettings);

    return () => {
      rzr.removeEventListener("settingsUpdated", copySettings);
    };
  }, []);

  return (
    <Page
      title="settings"
      cta={
        rzr.isProviderReadonly ? null : (
          <Button $variant="secondary" onClick={rzr.generateNewIdentity}>
            new identity
          </Button>
        )
      }
    >
      {isRevealingIdentity && (
        <RevealIdentityModal close={closeRevealIdentityModal} />
      )}
      <Stack
        $expand
        $justify="center"
        style={{ maxWidth: 768, alignSelf: "center" }}
      >
        <Inline $wrap $justify="space-around" $align="center">
          <Fieldset title="account">
            <>
              <DL>
                <dt>address</dt>
                <dd>
                  <Tooltip text={rzr.address}>
                    {rzrUtils.formatHex(rzr.address, 6)}
                  </Tooltip>
                </dd>
                <dt>identity</dt>
                <dd>
                  {rzr.identity ? (
                    <Tooltip text={rzr.identity}>
                      {rzrUtils.formatHex(rzr.identity, 6)}
                    </Tooltip>
                  ) : (
                    "N/A"
                  )}
                </dd>
              </DL>
              <BooleanField
                title="dark mode"
                value={settings.ui.darkModeStatus}
                onChange={(value) =>
                  rzr.updateSetting("ui", "darkModeStatus", value)
                }
              />
              <BooleanField
                title="animations"
                value={settings.ui.animationsEnabled}
                onChange={(value) =>
                  rzr.updateSetting("ui", "animationsEnabled", value)
                }
              />
              <BooleanField
                title="hide activity in apps"
                value={settings.ui.activityStatus}
                onChange={(value) =>
                  rzr.updateSetting("ui", "activityStatus", value)
                }
              />
              <Inline $justify="space-between">
                <Button onClick={openRevealIdentityModal} $size="sm">
                  reveal identity
                </Button>
                <Button
                  $size="sm"
                  $variant="accent"
                  onClick={() => rzr.resetDatabase()}
                >
                  reset
                </Button>
              </Inline>
            </>
          </Fieldset>
          <Fieldset title="connectivity">
            <>
              <NumericField
                title="connectivity level"
                value={3}
                onChange={() => null}
              />
              <NumericField
                title="max peer connections"
                value={settings.connectivity.maxPeerConnections}
                onChange={(value: number) =>
                  rzr.updateSetting("connectivity", "maxPeerConnections", value)
                }
              />
              <NumericField
                title="connection timeout (s)"
                value={settings.connectivity.peerTimeout}
                onChange={(value: number) =>
                  rzr.updateSetting("connectivity", "peerTimeout", value)
                }
              />
              <NumericField
                title="sync cycle frequency (s)"
                value={settings.connectivity.syncFrequency}
                onChange={(value: number) =>
                  rzr.updateSetting("connectivity", "syncFrequency", value)
                }
              />
              <NumericField
                title="discovery level"
                value={settings.connectivity.discoveryLevel}
                minValue={0}
                maxValue={5}
                onChange={(value: number) =>
                  rzr.updateSetting("connectivity", "discoveryLevel", value)
                }
              />
            </>
          </Fieldset>
          <Fieldset title="developers">
            <>
              <BooleanField
                title="display log"
                value={settings.ui.displayLog}
                onChange={(value) =>
                  rzr.updateSetting("ui", "displayLog", value)
                }
              />
              <BooleanField
                title="allow non verified apps"
                value={false}
                onChange={() => null}
              />
              <BooleanField
                title="expert mode"
                value={false}
                onChange={() => null}
              />
            </>
          </Fieldset>
        </Inline>
      </Stack>
    </Page>
  );
};

export default Settings;
