import { SignalingProtocol, AuthorizationSignature } from "../../../";

import {
  dataSlice,
  encodeBase64,
  decodeBase64,
  concat,
  getBytes,
  zeroPadValue,
  toBeArray,
} from "ethers";

export default class SSESignalingProtocol extends SignalingProtocol {
  private eventSource: EventSource = null as any;

  url: string;

  constructor(url: string) {
    super(url);

    this.url = `${url.startsWith("localhost") ? "http" : "https"}://${url}`;
  }

  private send(
    endpoint: string,
    data: ArrayBuffer,
    authorization: AuthorizationSignature
  ) {
    const controller = new AbortController();

    const id = setTimeout(() => controller.abort(), 5000);

    this.emit("sendingMessageStatus", true);

    return fetch(`${this.url}/${endpoint}`, {
      method: "POST",
      body: getBytes(
        concat([
          zeroPadValue(toBeArray(authorization.timestamp), 6),
          authorization.signature,
          new Uint8Array(data),
        ])
      ).buffer,
      signal: controller.signal,
      headers: new Headers({
        "Content-Type": "application/octet-stream",
      }),
    }).then((response) => {
      clearTimeout(id);

      this.emit("sendingMessageStatus", false);

      return response;
    });
  }

  getAddress() {
    return fetch(`${this.url}/self`).then((response) => {
      if (response.status !== 200) throw new Error("server down");
      return response.text();
    });
  }

  connect(authorization: AuthorizationSignature): Promise<boolean> {
    return new Promise((r) => {
      this.eventSource = new EventSource(
        `${this.url}/signals?timestamp=${
          authorization.timestamp
        }&signature=${encodeURIComponent(
          encodeBase64(authorization.signature)
        )}`
      );

      this.eventSource.addEventListener("candidate", this.onCandidate);

      this.eventSource.addEventListener("ping", this.onPing);

      this.eventSource.addEventListener("error", this.onError);

      r(true);
    });
  }

  reset() {
    if (this.eventSource) {
      this.eventSource.removeEventListener("ping", this.onPing);
      this.eventSource.removeEventListener("candidate", this.onCandidate);
      this.eventSource.removeEventListener("error", this.onError);
    }
    this.eventSource.close();
    this.removeAllListeners();
  }

  onPing = (event: MessageEvent) => {
    this.emit("ping", decodeBase64(event.data).buffer);
  };

  onError = (event: Event) => {
    if (event.eventPhase === EventSource.CLOSED) {
      // try and reconnect
    }
  };

  onCandidate = (event: MessageEvent) => {
    this.emit("candidate", decodeBase64(event.data).buffer);
  };

  sendOffer(
    identity: string,
    offerData: ArrayBuffer,
    authorization: AuthorizationSignature
  ): Promise<ArrayBuffer | null> {
    return new Promise((r) => {
      const onAnswer = ({ data }: MessageEvent) => {
        const message = decodeBase64(data);
        if (dataSlice(message, 0, 20) !== identity) return;

        this.eventSource.removeEventListener("answer", onAnswer);
        this.emit("receivingMessageStatus", false);

        // TODO: technically it would be great to be able to remove the identity, as it becomes irrelevant here
        r(message.buffer);
      };

      this.eventSource.addEventListener("answer", onAnswer);
      this.emit("receivingMessageStatus", true);

      this.send("offer", offerData, authorization).then((response) => {
        if (response.status !== 200) {
          this.eventSource.removeEventListener("answer", onAnswer);
          this.emit("receivingMessageStatus", false);
          return r(null);
        }
      });
    });
  }

  sendAnswer(
    identity: string,
    answerData: ArrayBuffer,
    authorization: AuthorizationSignature
  ) {
    this.send("answer", answerData, authorization).then((response) => {
      if (response.status !== 200) {
      }
    });
  }

  sendCandidate(
    identity: string,
    candidateData: ArrayBuffer,
    authorization: AuthorizationSignature
  ) {
    this.send("candidate", candidateData, authorization).then((response) => {
      if (response.status !== 200) {
      }
    });
  }

  sendUnavailable(identity: string, authorization: AuthorizationSignature) {
    this.send("unavailable", getBytes(identity).buffer, authorization).then(
      (response) => {
        if (response.status !== 200) {
        }
      }
    );
  }

  sendPing(
    identity: string,
    pingData: ArrayBuffer,
    authorization: AuthorizationSignature
  ): Promise<null | ArrayBuffer> {
    return new Promise((r) => {
      const onOffer = ({ data }: MessageEvent) => {
        const message = decodeBase64(data);
        if (dataSlice(message, 0, 20) !== identity) return;

        this.eventSource.removeEventListener("offer", onOffer);
        this.eventSource.removeEventListener("unavailable", onOffer);
        this.emit("receivingMessageStatus", false);

        r(message.buffer);
      };

      const onUnavailable = ({ data }: MessageEvent) => {
        const message = decodeBase64(data);
        if (dataSlice(message, 0, 20) !== identity) return;

        this.eventSource.removeEventListener("offer", onOffer);
        this.eventSource.removeEventListener("unavailable", onOffer);
        this.emit("receivingMessageStatus", false);

        r(null);
      };

      this.eventSource.addEventListener("offer", onOffer);
      this.eventSource.addEventListener("unavailable", onUnavailable);
      this.emit("receivingMessageStatus", true);

      this.send("ping", pingData, authorization).then((response) => {
        if (response.status !== 200) {
          this.eventSource.removeEventListener("offer", onOffer);
          this.eventSource.removeEventListener("unavailable", onOffer);
          this.emit("receivingMessageStatus", false);
          return r(null);
        }
      });
    });
  }
}
