import { utils } from '../../rzr'
const LogLine: React.FC<{ text: string; index: number }> = ({
  text,
  index,
}) => {
  const isOpen = text.endsWith("...");
  const hasFailed = text.endsWith("failed");
  const hasSucceeded =
    text.includes("... success") ||
    text.includes("... done") ||
    text.includes("... completed");

  // format addresses
  text = text.split(/(0x\w*)/).map(t => t.startsWith('0x') ? utils.formatAddress(t) : t).join('')

  return (
    <li
      key={index}
      className={
        isOpen ? "open" : hasFailed ? "failed" : hasSucceeded ? "success" : ""
      }
    >
      {isOpen ? text.slice(0, -3) : text}
    </li>
  );
};

export default LogLine
