import { ModuleMetadata } from "../../../rzr";
import styled from "styled-components";
import {
  Button,
  Modal,
  Inline,
  Input,
  Textarea,
  CloseIcon,
  H2,
  H3,
  P,
  Label,
  Stack,
} from "rzr-components";

const DescriptionInput = styled(Textarea)`
  width: 100%;
  display: block;
  min-height: 16em;
  resize: none;
`;

const ModuleMetadataStep: React.FC<{
  applicationHash: string;
  metadata: ModuleMetadata | null;
  onSuccess: (metadata: ModuleMetadata) => void;
  onPreviousStep: () => void;
}> = ({ applicationHash, metadata, onSuccess, onPreviousStep }) => {
  const isProduction = applicationHash.startsWith("0x");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const {
      moduleName: { value: moduleName },
      description: { value: description },
      dependencies: { value: _dependencies },
    } = e.target as HTMLFormElement;
    const dependencies = _dependencies
      .split(",")
      .filter((d:string) => d.length === 66);

    if (!isProduction) {
      return onSuccess({
        updatedAt: 0,
        name: moduleName,
        sourceUrl: applicationHash,
        tags: "",
        description,
        byteSize: 0,
        contentHash: applicationHash,
        dependencies,
        isLocal: true,
      });
    }

    const {
      tags: { value: tags },
      sourceUrl: { value: sourceUrl },
    } = e.target as HTMLFormElement;

    onSuccess({
      updatedAt: 0,
      name: moduleName,
      sourceUrl,
      tags,
      description,
      byteSize: 0,
      contentHash: applicationHash,
      dependencies,
    });
  };

  return (
    <Stack
      $justify="space-between"
      onSubmit={handleSubmit}
      as="form"
      $gap="md"
      $expand
    >
      <H3>Module information</H3>

      {isProduction ? (
        <>
          <Stack as="label" $gap="sm">
            <span>application hash</span>
            <span>{applicationHash}</span>
          </Stack>
          <Stack as="label" $gap="sm">
            name
            <Input
              defaultValue={metadata ? metadata.name : ""}
              name="moduleName"
            />
          </Stack>
          <Stack as="label" $gap="sm">
            source url
            <Input
              defaultValue={metadata ? metadata.sourceUrl : ""}
              name="sourceUrl"
            />
          </Stack>
          <Stack as="label" $gap="sm">
            tags
            <Input
              defaultValue={metadata ? metadata.tags : ""}
              name="tags"
              placeholder="comma separated tags"
            />
          </Stack>
          <Stack as="label" $gap="sm">
            dependencies
            <Input
              defaultValue={metadata ? metadata.dependencies : ""}
              name="dependencies"
              placeholder="comma separated dependencies"
            />
          </Stack>
          <Stack as="label" $gap="sm">
            description
            <DescriptionInput
              defaultValue={metadata ? metadata.description : ""}
              placeholder={`a longer summary of what your module is about`}
              name="description"
            />
          </Stack>
        </>
      ) : (
        <>
          <Stack as="label" $gap="sm">
            name
            <Input
              defaultValue={metadata ? metadata.name : ""}
              name="moduleName"
            />
          </Stack>
          <Stack as="label" $gap="sm">
            description
            <DescriptionInput
              defaultValue={metadata ? metadata.description : ""}
              placeholder={`a longer summary of what your module is about`}
              name="description"
            />
          </Stack>
        </>
      )}
      <Inline $justify="space-between">
        <Button type="button" onClick={onPreviousStep}>
          back
        </Button>
        <Button $variant="secondary">next</Button>
      </Inline>
    </Stack>
  );
};

export default ModuleMetadataStep;
