// this module is responsible for recieving incoming projects
// transforming them into loadable modules
import { zeroPadValue, toBeArray, getBytes, toNumber, concat, hexlify } from "ethers";

const fileContentToByteLength = (fileContent: Uint8Array) =>
  zeroPadValue(toBeArray(fileContent.length), 4);

const normalize = (arr: Uint8Array) => {
  while (arr[0] === 0) {
    arr = arr.slice(1);
  }

  return arr;
};

export default class ModuleProcessor {
  ec = new TextEncoder();
  dc = new TextDecoder();
  domParser = new DOMParser();

  acceptedLinkTypes = new Set(["stylesheet", "modulepreload"]);

  onParse: ((dependencies: string[]) => void) | null = null;
  onRootFileContent: ((fileContent: string) => void) | null = null;

  private parseDom(rootFileContent: string) {
    const dom = this.domParser.parseFromString(rootFileContent, "text/html");
    return dom;
  }

  private get baseURI() {
    return document.baseURI;
  }

  private normalizeUrl(url: string) {
    if (url.startsWith(this.baseURI)) return url.slice(this.baseURI.length - 1);
    return url;
  }

  parseModule(module: Uint8Array): string {
    let needle = 0;
    const readBytes = (n: number) => {
      const result = module.slice(needle, needle + n);
      needle += n;
      return result;
    };
    const readFileLength = () => toNumber(readBytes(4))
    const readFileName = () => this.dc.decode(normalize(readBytes(64)));

    const rootFileLength = readFileLength();

    const rootFileContent = this.dc.decode(readBytes(rootFileLength));
    const files = new Map<string, Uint8Array>();

    while (needle < module.length) {
      const fileByteLength = readFileLength();
      const fileName = readFileName();
      files.set(fileName, readBytes(fileByteLength));
    }

    const dom = this.parseDom(rootFileContent);

    const resourceScripts = dom.querySelectorAll("script");
    const imageTags = dom.querySelectorAll("img");
    const sourceTags = dom.querySelectorAll("source");
    const resourceLinks = dom.querySelectorAll("link");
    const fileMap = new Map<string, string>();

    for (const resourceScript of Array.from(resourceScripts)) {
      const fileName = Array.from(files.keys()).find((fn) =>
        resourceScript.src.includes(fn)
      );

      if (!fileName) continue;

      const fileContent = files.get(fileName) as Uint8Array;

      resourceScript.src = window.URL.createObjectURL(
        new Blob([this.dc.decode(fileContent)], {
          type: "appliction/js",
        })
      );

      if (resourceScript.type === "module")
        resourceScript.type = "application/javascript";

      fileMap.set(fileName, resourceScript.src);
    }

    for (const imageTag of Array.from(imageTags)) {
      const fileName = Array.from(files.keys()).find((fn) =>
        imageTag.src.includes(fn)
      );

      if (!fileName) continue;

      const fileContent = files.get(fileName) as Uint8Array;

      imageTag.src = window.URL.createObjectURL(
        new Blob([this.dc.decode(fileContent)])
      );

      fileMap.set(fileName, imageTag.src);
    }

    for (const sourceTag of Array.from(sourceTags)) {
      const fileName = Array.from(files.keys()).find((fn) =>
        sourceTag.srcset.includes(fn)
      );

      if (!fileName) continue;

      const fileContent = files.get(fileName) as Uint8Array;

      sourceTag.srcset = window.URL.createObjectURL(
        new Blob([this.dc.decode(fileContent)])
      );

      fileMap.set(fileName, sourceTag.srcset);
    }

    for (const resourceLink of Array.from(resourceLinks)) {
      if (!this.acceptedLinkTypes.has(resourceLink.rel)) continue;
      const fileName = Array.from(files.keys()).find((fn) =>
        resourceLink.href.includes(fn)
      );
      if (!fileName) continue;

      const fileContent = files.get(fileName) as Uint8Array;

      resourceLink.href = window.URL.createObjectURL(
        new Blob([this.dc.decode(fileContent)], {
          type: "text/css",
        })
      );

      fileMap.set(fileName, resourceLink.href);
    }

    let html = dom.documentElement.outerHTML;

    for (const [originalFile, mappedFile] of Array.from(fileMap.entries())) {
      html = html.replaceAll(`.${originalFile}`, mappedFile);
      html = html.replaceAll(originalFile, mappedFile);
    }

    return window.URL.createObjectURL(
      new Blob([html], {
        type: "text/html",
      })
    );
  }

  parseRootFile(rootFile: File) {
    const fileReader = new FileReader();

    fileReader.onload = (e) => {
      if (!e?.target || !this.onParse) return;

      const fileContent = e.target.result as string;

      if (this.onRootFileContent) this.onRootFileContent(fileContent);

      const dom = this.parseDom(fileContent);

      this.onParse(
        [
          ...Array.from(dom.querySelectorAll("script")).map((scriptTag) =>
            this.normalizeUrl(scriptTag.src)
          ),

          ...Array.from(dom.querySelectorAll("source")).map((sourceTag) =>
            this.normalizeUrl(sourceTag.srcset)
          ),

          ...Array.from(dom.querySelectorAll("img")).map((sourceTag) =>
            this.normalizeUrl(sourceTag.src)
          ),

          ...Array.from(dom.querySelectorAll("link"))
            .filter((linkTag) => this.acceptedLinkTypes.has(linkTag.rel))
            .filter((linkTag) => linkTag.href.startsWith(this.baseURI))
            .map((linkTag) => this.normalizeUrl(linkTag.href)),
        ].filter((fn) => fn)
      );
    };

    fileReader.readAsText(rootFile);
  }

  processFiles(
    rootFileContent: string,
    files: Map<string, Uint8Array>
  ): Uint8Array {
    const dom = this.parseDom(rootFileContent);
    const resourceScripts = dom.querySelectorAll("script");
    const resourceLinks = dom.querySelectorAll("link");
    const imageTags = dom.querySelectorAll("img");
    const sourceTags = dom.querySelectorAll("source");

    for (const resourceScript of Array.from(resourceScripts)) {
      if (
        resourceScript.src &&
        !Array.from(files.keys()).find((key) =>
          resourceScript.src.includes(key)
        )
      ) {
        throw new Error(`missing script file: ${resourceScript.src}`);
      }
    }

    for (const imageTag of Array.from(imageTags)) {
      if (
        imageTag.src &&
        !Array.from(files.keys()).find((key) => imageTag.src.includes(key))
      ) {
        throw new Error(`missing image file: ${imageTag.src}`);
      }
    }

    for (const sourceTag of Array.from(sourceTags)) {
      if (
        sourceTag.srcset &&
        !Array.from(files.keys()).find((key) => sourceTag.srcset.includes(key))
      ) {
        throw new Error(`missing source file: ${sourceTag.srcset}`);
      }
    }

    for (const resourceLink of Array.from(resourceLinks)) {
      if (!this.acceptedLinkTypes.has(resourceLink.rel)) continue;

      if (
        resourceLink.href.startsWith(this.baseURI) &&
        !Array.from(files.keys()).find((key) => resourceLink.href.includes(key))
      ) {
        throw new Error(`missing link file: ${resourceLink.href}`);
      }
    }

    const rootFileBytes = this.ec.encode(rootFileContent);

    let filesAsBytes = concat([
      fileContentToByteLength(rootFileBytes),
      rootFileBytes,
    ]);

    const sortedFiles = Array.from(files.entries()).sort();

    for (const [fileName, fileContent] of sortedFiles) {
      filesAsBytes = concat([
        filesAsBytes,
        fileContentToByteLength(fileContent),
        zeroPadValue(this.ec.encode(fileName), 64),
        fileContent,
      ]);
    }

    return getBytes(filesAsBytes);
  }
}
