export const messageSchemas: { [key in PeerMessageType]: Schema } = {
  [PeerMessageType.INSTALLED_MODULES]: {
    moduleAddresses: {
      type: "repeating",
      schema: {
        address: {
          type: "hexstring",
          byteLength: constants.ADDRESS_BYTE_LENGTH,
        },
      },
    },
  },
  [PeerMessageType.INITIATE_MODULE_TRANSFER]: {
    modules: {
      type: "repeating",
      schema: {
        contentHash: {
          type: "hexstring",
          byteLength: constants.HASH_BYTE_LENGTH,
        },
      },
    },
  },
  [PeerMessageType.NEW_MODULE]: {
    moduleContent: {
      type: "buffer",
    },
  },
  [PeerMessageType.MODULE_TRANSFER_COMPLETE]: {},
  [PeerMessageType.MODULE_TRANSFER_FINISHED]: {},
  [PeerMessageType.NEW_ROUTER]: {
    address: {
      type: "hexstring",
      byteLength: constants.ADDRESS_BYTE_LENGTH,
    },
    registrationSignature: {
      type: "buffer",
      byteLength: constants.SIGNATURE_BYTE_LENGTH,
    },
    url: {
      type: "string",
    },
  },
};

export type MessageSchemas = {
  [PeerMessageType.INSTALLED_MODULES]: ModuleAddressesSchema;
  [PeerMessageType.INITIATE_MODULE_TRANSFER]: ModuleSchema;
  [PeerMessageType.NEW_MODULE]: NewModuleSchema;
  [PeerMessageType.MODULE_TRANSFER_COMPLETE]: AppliedSchema;
  [PeerMessageType.MODULE_TRANSFER_FINISHED]: AppliedSchema;

  [PeerMessageType.NEW_ROUTER]: AppliedSchema & Router;
};

export type NewModuleSchema = AppliedSchema & {
  moduleContent: Uint8Array;
};

export type ModuleAddressesSchema = AppliedSchema & {
  moduleAddresses: Array<{
    moduleAddress: string;
  }>;
};

export type ModuleSchema = AppliedSchema & {
  modules: Array<{
    contentHash: string;
  }>;
};
