import {
  hexlify,
  Wallet,
  getBytes,
  toBeHex,
  concat,
  zeroPadValue,
} from "ethers";
import {
  PeerConnection,
  encodeIntents,
  decodeIntentRequest,
  PeerConnectionEventMap,
  PeerIntents,
  IntentStatus,
  PeerMessageType,
  messageSchemas,
  setCommunicationProtocols,
  CommunicationProtocols,
} from "./PeerConnection";

import {
  PingSchema,
  OfferSchema,
  SignalMessageSchema,
  SignalMessageType,
} from "./RouterConnection";
import {
  SignalServerConnection,
  SignalingProtocols,
} from "./RouterConnection/SignalServerConnection";

import { Settings, Authentication } from "../../lvl3";
import { Ethereum } from "../../lvl2";
import { RzRDB, DataType } from "../../lvl1";
import { Clock, Log, LogEmitter } from "../../lvl0";

import { watch, once, applySchema } from "../../../utils";

import { Router, Player, ConnectivitySettings } from "../../../types";

class NoRouteToPeer extends Error {
  constructor(message?: string) {
    super(message);
    this.name = "NoRouteToPeer";
  }
}

type ContactedPeer = { rx: number; tx: number };
export type TopologyPeer = {
  address: string;
  isConnected: boolean;
  isAuthorized: boolean;
};

export type ConnectionMatrixEventTypes = {
  peersChanged: string[];
  connectionsChanged: [string, TopologyPeer[]][];
  routersConnected: boolean;
  connectionStatusChanged: boolean;
  newPeerConnection: string;
  peerConnected: string;
  peerAuthorized: string;
  peerDisconnected: string;

  peerSendingMessageStatus: boolean;
  peerReceivingMessageStatus: boolean;
  dataTransferMetricsUpdated: [BigInt, BigInt];

  routerSendingMessageStatus: boolean;
  routerReceivingMessageStatus: boolean;

  latestPeersChanged: [string, ContactedPeer][];
};

export class ConnectionMatrix extends LogEmitter<ConnectionMatrixEventTypes> {
  latestPeers = new Map<string, ContactedPeer>();
  connections = {
    peers: new Map<string, PeerConnection>(),
    routers: new Map<string, SignalServerConnection>(),
  };

  transferredBytes = BigInt(0);
  receivedBytes = BigInt(0);

  peerConnections = new Map<string, SignalServerConnection>();

  isOnline: boolean = false;

  // this is dictated by the network
  // usually ratio of broken routers
  // if half or routers are down 6
  // if a third 5
  // if a fourth 4
  // if a fifth 3
  // if a sixth 2
  connectivityLevel = 2;

  signalingProtocols: SignalingProtocols;

  status = {
    isConnecting: false,
  };

  modules: {
    log: Log;
    clock: Clock;
    rzrDB: RzRDB;
    ethereum: Ethereum;
    settings: Settings;
    authentication: Authentication;
  };

  constructor(
    signalingProtocols: SignalingProtocols,
    log: Log,
    clock: Clock,
    rzrDB: RzRDB,
    ethereum: Ethereum,
    settings: Settings,
    authentication: Authentication
  ) {
    super(log, "ConnectionMatrix");

    this.modules = {
      log,
      clock,
      rzrDB,
      ethereum,
      settings,
      authentication,
    };

    // come on this should be a function call.
    this.addEventListener("newPeerConnection", () => {
      this.emit("connectionsChanged", this.routerConnections);
    });

    this.watchOnlineStatus();

    this.signalingProtocols = signalingProtocols;
  }

  initialize(defaultRouterAddress?: string) {
    this.routerCheckSequence();

    if (defaultRouterAddress) {
      this.modules.rzrDB
        .loadClosestRouters(this.modules.ethereum.identity as string)
        .then((routers) => {
          if (!routers) {
            this.modules.rzrDB.storeRouter({
              address: hexlify(new Uint8Array(20)),
              url: defaultRouterAddress,
            });
          }
        });
    }

    this.reinitialize();
  }

  private reinitialize = () => {
    this.status.isConnecting = true;
    const identity = this.modules.ethereum.identity;
    if (!identity) return;

    for (const router of this.permanentRouters) {
      this.releaseRouter(router.address, true);
    }

    return this.loadRouters(identity.toLowerCase())
      .then((routers) =>
        Promise.all(
          Array.from(routers.values()).map((router) =>
            this.getOrCreateRouterConnection(router, true)
          )
        )
      )
      .then((routerConnections: Array<null | SignalServerConnection>) => {
        let hasAtLeastOneConnection = false;
        for (const routerConnection of routerConnections) {
          if (!routerConnection) continue;
          hasAtLeastOneConnection = true;

          routerConnection.addEventListener("ping", (pingData) => {
            this.peerConnections.set(pingData.identity, routerConnection);
            this.onPing(routerConnection, pingData);
          });

          this.connections.routers.set(
            routerConnection.address,
            routerConnection
          );
        }

        this.status.isConnecting = false;

        this.emit("routersConnected", hasAtLeastOneConnection);

        const checkForConnectingPeers = () => {
          const peers = Array.from(this.peers.values()).filter(
            (p) => !p.isConnected
          );

          if (peers.length === 0) return this.reinitialize();

          this.modules.clock.timer(3).then(checkForConnectingPeers);
        };

        this.modules.clock.timer(60).then(checkForConnectingPeers);
      });
  };

  private routerCheckSequence = () => {
    this.modules.rzrDB
      .loadNextFlaggedRouter()
      .then((router) => {
        if (!router) return this.modules.clock.timer(120).then(() => null);

        return this.getOrCreateRouterConnection(router);
      })
      .then((routerConnection) => {
        if (routerConnection) {
          this.modules.rzrDB.storeRouter({
            address: routerConnection.address,
            url: routerConnection.url,
            downSince: 0,
          })
          this.releaseRouter(routerConnection.address);
        }
        return this.modules.clock.timer(15);
      })
      .then(() => this.routerCheckSequence());
  };

  private addContactedPeer(address: string, peer: ContactedPeer) {
    if (!this.latestPeers.has(address) && this.latestPeers.size > 10) {
      this.latestPeers.delete(Array.from(this.latestPeers.keys())[0]);
    }

    this.latestPeers.set(address, peer);

    this.emit("latestPeersChanged", Array.from(this.latestPeers.entries()));
  }

  private watchOnlineStatus = () => {
    if (typeof window === "undefined") return;

    const isOnline = window.navigator.onLine;

    if (isOnline !== this.isOnline) {
      this.emit("connectionStatusChanged", isOnline);
    }

    this.isOnline = isOnline;

    this.modules.clock.timer(5).then(this.watchOnlineStatus);
  };

  private onPing = (router: SignalServerConnection, pingData: PingSchema) => {
    if (
      this.peers.size >=
      this.modules.settings.getSetting("connectivity", "maxPeerConnections")
    ) {
      router.sendUnavailable(pingData.identity);

      return;
    }

    const requestedIntents = decodeIntentRequest(pingData.encodedIntent);

    const peer = this.getOrCreatePeer(pingData.identity, requestedIntents);

    this._log(
      `receiving connection from ${pingData.identity}`,
      this.modules.rzrDB
        .loadPlayerByIdentity(pingData.identity)
        .then((player) => {
          if (!player) throw new Error("player could not be identified");

          peer.ping(router, pingData.encodedSupportedProtocols);

          if (peer.isConnected) return true;

          return once(peer, "connectionStatusChanged", {
            timeoutIn:
              this.modules.settings.getSetting("connectivity", "peerTimeout") *
              1000,
          });
        })
        .then((isConnected) => {
          if (!isConnected)
            return this.releasePeer(pingData.identity, requestedIntents);

          // at this point we need to register all other intents, that were not sent over

          const remainingIntents = peer.registeredIntents.filter(
            (i) => !requestedIntents.includes(i)
          );

          if (remainingIntents.length > 0) {
            peer.rawSend(
              getBytes(
                concat([
                  new Uint8Array(4),
                  ...remainingIntents.map((intent) =>
                    zeroPadValue(toBeHex(intent), 4)
                  ),
                ])
              )
            );
          }

          this.emit("newPeerConnection", pingData.identity);
        })
        .catch(() => this.releasePeer(pingData.identity, requestedIntents))
    );
  };

  get routerConnections(): [string, TopologyPeer[]][] {
    const results = new Map<string, TopologyPeer[]>();
    for (const [peerAddress, routerConnection] of Array.from(
      this.peerConnections.entries()
    )) {
      const entry = results.get(routerConnection.address);

      const peer = this.peers.get(peerAddress);

      const topologyPeer = {
        address: peerAddress,
        isConnected: peer?.isConnected || false,
        isAuthorized: peer?.isAuthorized || false,
      };

      if (!entry) {
        results.set(routerConnection.address, [topologyPeer]);
        continue;
      }

      entry.push(topologyPeer);
    }

    for (const address of Array.from(this.routers.keys())) {
      if (address === hexlify(new Uint8Array(20))) continue;
      if (!results.get(address)) results.set(address, []);
    }

    return Array.from(results.entries());
  }

  get peers() {
    return this.connections.peers;
  }

  getPeer(identity: string) {
    return this.connections.peers.get(identity);
  }

  get routers() {
    return this.connections.routers;
  }

  get permanentRouters() {
    return Array.from(this.connections.routers.values()).filter(
      (router) => router.isPermanent
    );
  }

  get isConnected() {
    return this.permanentRouters.length > 0 && !this.status.isConnecting;
  }

  private loadRouters = (identity: string): Promise<Set<Router>> =>
    this.modules.rzrDB
      .loadClosestRouters(identity, this.connectivityLevel)
      .then(
        (routers) =>
          routers ??
          once(this.modules.rzrDB, DataType.ROUTER, {
            precondition: (router: Router | null) => router !== null,
          }).then(() => this.loadRouters(identity))
      );

  /*
   * At each and every stage connection might be established by other means
   * need to check that
   * */
  private findRouteToPeer(
    peer: PeerConnection,
    routers: Set<Router>,
    triedRouterAddresses: string[] = [],
    hasRouteBeenFound = false
  ): Promise<boolean> {
    if (peer.hasProtocol || hasRouteBeenFound) return Promise.resolve(true);

    const router = Array.from(routers).find(
      (r) => !triedRouterAddresses.includes(r.address)
    );

    if (!router) return Promise.resolve(false);

    triedRouterAddresses.push(router.address);

    const onceConfig = {
      precondition: ({ identity }: SignalMessageSchema) =>
        identity === peer.identity,
      timeoutIn: 2500,
    };

    return this.getOrCreateRouterConnection(router)
      .then((routerConnection) => {
        if (!routerConnection || peer.hasProtocol)
          throw new Error("router not connected");

        peer.initiateConnection();

        return Promise.all([
          Promise.resolve(routerConnection),
          routerConnection.ping({
            identity: peer.identity,
            encodedSupportedProtocols: 0b11, // FIXME, should come from constructor after detecting environment
            encodedIntent: encodeIntents(peer.intents),
          }),
        ]);
      })
      .then(([routerConnection, result]) => {
        if (!result) throw new Error("client unavailable");

        this.peerConnections.set(peer.identity, routerConnection);

        peer.establishConnection(routerConnection, result as OfferSchema);

        return true;
      })
      .catch((e) => {
        return this.findRouteToPeer(peer, routers, triedRouterAddresses, false);
      });
  }

  sendTo(identity: string, message: Uint8Array) {
    const peer = this.peers.get(identity);

    if (!peer) return;

    peer.rawSend(message);
  }

  private getOrCreatePeer(
    identity: string,
    peerIntent: number | number[]
  ): PeerConnection {
    const existingPeer = this.peers.get(identity) as PeerConnection;
    const intents = Array.isArray(peerIntent) ? peerIntent : [peerIntent];

    if (existingPeer) {
      existingPeer.addIntents(intents);
      return existingPeer;
    }

    const peer = new PeerConnection(
      this.modules.log,
      this.modules.clock,
      this.modules.rzrDB,
      this.modules.ethereum,
      this.modules.authentication,
      identity,
      intents
    );

    // this should be the only way to release a peer once its connected
    const initiateReleaseSequence = (): Promise<void> =>
      this.modules.clock
        .timer(3)
        .then(() =>
          peer.intentsFulfilled
            ? this.releasePeer(identity)
            : initiateReleaseSequence()
        );

    once(peer, "intentsFulfilled").then(initiateReleaseSequence);

    peer.addEventListener("sendingMessageStatus", (e) => {
      this.emit("peerSendingMessageStatus", e);
    });

    peer.addEventListener("receivingMessageStatus", (e) => {
      this.emit("peerReceivingMessageStatus", e);
    });

    peer.addEventListener("connectionStatusChanged", (isConnected) => {
      if (!isConnected) this.emit("peerDisconnected", identity);

      // for now peer connection is only emitted once peer is authorized
    });

    peer.addEventListener("peerAuthorized", () => {
      // TODO: move this back to connectionStatusChanged
      this.emit("peerConnected", identity);
      this.emit("peerAuthorized", identity);
    });

    this.peers.set(identity, peer);

    return peer;
  }

  private getOrCreateRouterConnection(
    router: Router,
    isPermanent = false
  ): Promise<SignalServerConnection | null> {
    const existingConnection = this.routers.get(router.address);

    if (existingConnection?.isConnected)
      return Promise.resolve(existingConnection);
    else if (existingConnection) this.releaseRouter(router.address);

    const connection = this.createRouterConnection(router, isPermanent);

    this.emit("connectionsChanged", this.routerConnections);

    connection.addEventListener("sendingMessageStatus", (e) => {
      this.emit("routerSendingMessageStatus", e);
    });

    connection.addEventListener("receivingMessageStatus", (e) => {
      this.emit("routerReceivingMessageStatus", e);
    });

    // yep, all of this logic will move to router...
    return this._log(
      `connecting to router at ${router.address}`,
      connection.connect().then(() => connection)
    ).catch((e) => {
      this.releaseRouter(router.address);

      if (router.downSince === undefined || router.downSince === 0) {
        this.modules.rzrDB.flagRouter(router.address);
      }

      return null;
    });
  }

  private createRouterConnection(
    router: Router,
    isPermanent: boolean
  ): SignalServerConnection {
    // this is to make sure to kill any broken connections
    const existingConnection = this.routers.get(router.address);
    if (existingConnection) this.releaseRouter(existingConnection.address);

    const connection = new SignalServerConnection(
      this.signalingProtocols,
      this.modules.log,
      {
        clock: this.modules.clock,
        rzrDB: this.modules.rzrDB,
        authentication: this.modules.authentication,
      },
      router.address,
      router.url,
      isPermanent
    );

    this.routers.set(router.address, connection);

    return connection;
  }

  // this really doesn't belong here..
  private shareModulesWithPeer(peer: PeerConnection) {}

  contactPeer(
    identity: string,
    peerIntent: number | number[],
    maxRetries = 1,
    retries = 0
  ): Promise<PeerConnection | null> {
    if (!this.isConnected) return Promise.resolve(null);

    const peer = this.getOrCreatePeer(identity, peerIntent);

    if (peer.isConnected) return Promise.resolve(peer);

    return this._log(
      `contacting ${identity}`,
      // FIXME: nope, this is going to make us stuck at this point until we have a router, this is a terrible idea
      this.modules.rzrDB
        .loadClosestRouters(identity)
        .then((routers) => {
          if (!routers) throw new Error("routers not connected");
          return this.findRouteToPeer(peer, routers);
        })
        .then((hasRoute) => {
          if (peer.isConnected) return true;
          if (!hasRoute) throw new NoRouteToPeer();
          return once(peer, "connectionStatusChanged", {
            timeoutIn:
              this.modules.settings.getSetting("connectivity", "peerTimeout") *
              1000,
          });
        })
        .then((isConnected) => {
          if (!isConnected) throw new Error("Connection problem");

          this.emit("newPeerConnection", identity);

          return peer;
        }),
      {
        successMessage: "success",
        failMessage: "player unavailable",
      }
    ).catch((e) => {
      this.releasePeer(
        identity,
        Array.isArray(peerIntent) ? peerIntent : [peerIntent]
      );

      if (e instanceof NoRouteToPeer || maxRetries <= retries) {
        return null;
      }

      return this.modules.clock
        .timer(5 + retries)
        .then(() =>
          this.contactPeer(identity, peerIntent, maxRetries, retries + 1)
        );
    });
  }

  private releasePeer(identity: string, requestedIntents?: number[]) {
    const peer = this.peers.get(identity);
    if (
      !peer ||
      (requestedIntents &&
        peer.registeredIntents.length > requestedIntents.length)
    )
      return;

    this.log(`peer ${identity} released`);

    this.transferredBytes += BigInt(peer.transferredBytes);
    this.receivedBytes += BigInt(peer.receivedBytes);

    this.addContactedPeer(identity, {
      tx: peer.transferredBytes,
      rx: peer.receivedBytes,
    });

    peer.reset();

    const router = this.peerConnections.get(identity);

    if (router) {
      this.releaseRouter(router.address);
    }

    this.peers.delete(identity);

    this.peerConnections.delete(identity);

    this.emit("connectionsChanged", this.routerConnections);

    this.emit("dataTransferMetricsUpdated", [
      this.receivedBytes,
      this.transferredBytes,
    ]);
  }

  private releaseRouter(address: string, forceRelease = false) {
    const router = this.routers.get(address);
    if (!router) return;
    if (!forceRelease && router.isPermanent) return;

    this.log(`router ${address} released`);

    router.reset();

    this.emit("connectionsChanged", this.routerConnections);

    this.routers.delete(address);
  }
}
