import { useState, useEffect } from "react";
import {
  Modal,
  Label,
  H2,
  Button,
  Inline,
  P,
  Input,
  Stack,
  Tooltip,
} from "rzr-components";
import {
  toBeHex,
  concat,
  Wallet,
  sha256,
  randomBytes,
  hexlify,
  getBytes,
} from "ethers";
import {
  RzR,
  WebModuleFormat,
  IndexedDBBackend,
  WebEnvironment,
  MetaMaskProvider,
  ReadonlyProvider,
  WebRTCProtocol,
  WebAuthentication,
  SSESignalingProtocol,
  utils,
} from "../../rzr";
import config from "../../config";

const defaults = {
  Authentication: { backend: WebAuthentication },
  RzRDB: { backend: IndexedDBBackend },
  ModuleMatrix: { environment: WebEnvironment },
  Ethereum: { provider: ReadonlyProvider },
  RzRPeer: {
    supportedProtocols: {
      WebRTC: WebRTCProtocol,
    },
    supportedSignalingProtocols: {
      SSE: SSESignalingProtocol,
    },
  },
};

// TODO: this file is a mess, it's mostly just to make onboarding better

const defaultRouterUrl = window.location.origin.split("://")[1];

const SelectProvider: React.FC<{
  setRzR: (rzr: RzR<WebModuleFormat>) => void;
}> = ({ setRzR }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [identityGenerationStatusCode, setIdentityGenerationStatusCode] =
    useState(0);
  const handleNewIdentityClick = () => {
    setIsLoading(true);

    const depth = 1024;
    const privateKey = hexlify(randomBytes(32));
    const account = new Wallet(privateKey);

    let h = sha256(privateKey);

    const identity = new Wallet(h);

    account
      .signMessage(getBytes(concat([account.address, identity.address])))
      .then((signature) => {
        fetch("/identity", {
          method: "POST",
          headers: new Headers({
            "Content-Type": "application/octet-stream",
          }),
          body: getBytes(
            concat([account.address, identity.address, signature])
          ),
        })
          .then((response) => {
            if (response.status !== 200) return;
            localStorage.setItem("privateKey", privateKey);

            const rzr = new RzR<WebModuleFormat>(config, defaults);

            rzr.initialize(defaultRouterUrl).then(() => {
              setRzR(rzr);
            });

            utils.once(rzr, "accountChanged").then(() => {
              rzr.storeIdentity(
                toBeHex(
                  BigInt(sha256(new TextEncoder().encode("TEST_PASSWORD"))) ^
                    BigInt(h)
                )
              );
            });
          })
          .catch(() => {
            console.log("error!");
          })
          .finally(() => {
            setIsLoading(false);
          });
      });

    // this will be a dedicated domain for now.
  };

  const hasMetamask = (window as any).ethereum;

  useEffect(() => {
    if (hasMetamask) {
      const rzr = new RzR<WebModuleFormat>(config, {
        ...defaults,
        Ethereum: { provider: MetaMaskProvider },
      });

      rzr.initialize(defaultRouterUrl).then(() => setRzR(rzr));
      return;
    }
    if (localStorage.privateKey) {
      const rzr = new RzR<WebModuleFormat>(config, defaults);

      rzr.initialize(defaultRouterUrl).then(() => setRzR(rzr));
      return;
    }

    fetch("/identity").then((res) =>
      setIdentityGenerationStatusCode(res.status)
    );
  }, []);

  if (hasMetamask || localStorage.privateKey) {
    return <>loading...</>;
  }

  return (
    <Modal $gap="lg">
      <H2>Login</H2>
      <P>
        You can use a read only provider to access the rzr network but if you
        are planning to publish your application, you need to install MetaMask
        in order to generate its public proof.
      </P>
      <Inline $justify="center">
        <Tooltip
          text={
            identityGenerationStatusCode === 429
              ? "Identity generation is temporarily unavailable on this domain"
              : identityGenerationStatusCode !== 200
              ? "Identity generation is not supported on this domain, try a different router"
              : ""
          }
        >
          <Button
            isLoading={isLoading}
            $variant="secondary"
            onClick={handleNewIdentityClick}
            disabled={identityGenerationStatusCode !== 200}
          >
            new read-only identity
          </Button>
        </Tooltip>
      </Inline>
    </Modal>
  );
};

export default SelectProvider;
