import {
  ConnectionMatrix,
  PeerConnection,
  ModuleSchema,
  messageSchemas,
  PeerMessageType,
  IntentStatus,
} from "../../lvl4";
import { Ethereum } from "../../lvl2";
import { RzRDB, moduleMetadataSchema, DataType } from "../../lvl1";
import { Clock, Log, LogEmitter } from "../../lvl0";
import { Settings } from "../../lvl3";

import { getBytes, sha256, verifyMessage, hexlify } from "ethers";
import { Router, ModuleMetadata, ConnectivitySettings } from "../../../types";

import { applySchema, once, digestModule } from "../../../utils";
import constants from "../../../constants";

enum ModuleTransferIntents {
  DOWNSTREAM = 0,
  UPSTREAM,
}

// TODO: somehow, need the ability to update signature of same contentHash modules

export class RzRDiscovery extends LogEmitter<{}> {
  // to store in db
  // peers that share information that is proven wrong should be treated as false peers and continue sync cycle
  peerStrikes = new Map<string, number>();

  modules: {
    clock: Clock;
    ethereum: Ethereum;
    rzrDB: RzRDB;
    settings: Settings;
    connectionMatrix: ConnectionMatrix;
  };

  discovery = {
    threads: 0,
    intents: new Set<number>([
      ModuleTransferIntents.DOWNSTREAM,
      ModuleTransferIntents.UPSTREAM,
    ]),
  };

  constructor(
    log: Log,
    modules: {
      clock: Clock;
      rzrDB: RzRDB;
      ethereum: Ethereum;
      settings: Settings;
      connectionMatrix: ConnectionMatrix;
    }
  ) {
    super(log, "Discovery");
    this.modules = modules;

    modules.connectionMatrix.addEventListener(
      "newPeerConnection",
      this.onPeerConnection
    );
  }

  getDiscoveryDelay(latestPeerConnectionSuccessful: boolean | null) {
    const discoveryLevel = this.modules.settings.getSetting(
      "connectivity",
      "discoveryLevel"
    );

    return latestPeerConnectionSuccessful === null // no peer found
      ? 10 + (5 - discoveryLevel) * 10 // 60, 50, 40, 30, 20, 10
      : latestPeerConnectionSuccessful === false // last peer not connected
      ? 5 + (5 - discoveryLevel) * 2 // 15, 13, 12, 11, 9, 7
      : 10 + (5 - discoveryLevel) * 10; // 60, 50, 40, 30, 20, 10
  }

  discoverySequence = async (
    blockHeight: number,
    contactedPlayerCount = 0
  ): Promise<void> => {
    if (
      this.discovery.threads >=
      this.modules.settings.getSetting("connectivity", "discoveryLevel")
    )
      return;
    if (this.modules.ethereum.identity === null) return;
    this.discovery.threads += 1;

    const player = await this.modules.rzrDB.loadRandomPlayer(
      blockHeight,
      contactedPlayerCount
    );

    const restartSequence = (t: number, keepBlockHeight = true) => {
      return this.modules.clock.timer(t).then(() => {
        this.discovery.threads -= 1;
        this.discoverySequence(
          keepBlockHeight ? blockHeight : this.modules.rzrDB.blockHeight,
          keepBlockHeight ? contactedPlayerCount + 1 : 0
        );
      });
    };

    if (!player) {
      this.log("no player found");
      return restartSequence(this.getDiscoveryDelay(null), false);
    }

    const peer = await this.modules.connectionMatrix.contactPeer(
      player.identity,
      Array.from(this.discovery.intents)
    );

    return restartSequence(this.getDiscoveryDelay(peer !== null));
  };

  private onPeerConnection = (identity: string) => {
    const peer = this.modules.connectionMatrix.getPeer(identity);

    if (!peer) return;

    once(peer, PeerMessageType.MODULE_TRANSFER_COMPLETE).then(() => {
      peer.updateIntent(
        ModuleTransferIntents.DOWNSTREAM,
        IntentStatus.COMPLETED
      );

      peer.send(PeerMessageType.MODULE_TRANSFER_FINISHED);
    });

    once(peer, PeerMessageType.MODULE_TRANSFER_FINISHED).then(() => {
      peer.updateIntent(ModuleTransferIntents.UPSTREAM, IntentStatus.COMPLETED);
    });

    once(peer, PeerMessageType.INITIATE_MODULE_TRANSFER).then((message) => {
      this.handleInitiateModuleTransfer(
        peer,
        (
          applySchema(
            messageSchemas[PeerMessageType.INITIATE_MODULE_TRANSFER],
            message
          ) as ModuleSchema
        ).modules
      );
    });

    peer.addEventListener(PeerMessageType.NEW_MODULE, this.handleNewModule);

    peer.addEventListener(PeerMessageType.NEW_ROUTER, (message: Uint8Array) => {
      this.handleNewRouter(
        applySchema(messageSchemas[PeerMessageType.NEW_ROUTER], message)
      );
    });

    Promise.all([
      this.modules.rzrDB.loadMissingModules(10),
      once(peer, "peerAuthorized"),
    ]).then(([hashes]) => {
      if (hashes.length === 0) {
        peer.updateIntent(
          ModuleTransferIntents.DOWNSTREAM,
          IntentStatus.COMPLETED
        );
        return;
      }

      peer.updateIntent(
        ModuleTransferIntents.DOWNSTREAM,
        IntentStatus.IN_PROGRESS
      );

      peer.send(PeerMessageType.INITIATE_MODULE_TRANSFER, {
        modules: hashes.map((contentHash) => ({
          contentHash,
        })),
      });
    });
  };

  /*
        // WTF, I only requested modules...
        this.modules.rzrDB.loadRouters().then((routers) => {
          for (const router of routers) {
            if (!router.registrationSignature) return;

            peer.send(PeerMessageType.NEW_ROUTER, {
              address: router.address,
              url: router.url,
              registrationSignature: router.registrationSignature,
            });
          }
        }),*/

  private loadModules(startAt: number) {
    // loads module registrations with metadata
    // returns first 10
  }

  private async handleInitiateModuleTransfer(
    peer: PeerConnection,
    requestedModules: { contentHash: string }[]
  ) {
    peer.updateIntent(ModuleTransferIntents.UPSTREAM, IntentStatus.IN_PROGRESS);
    for (const { contentHash } of requestedModules) {
      const moduleContent = await this.modules.rzrDB.loadModule(contentHash);
      if (!moduleContent) continue;

      // so we have basically gone to a pull model which is nice, but need to verify that I get what I actually want
      peer.send(PeerMessageType.NEW_MODULE, { moduleContent });
    }

    peer.send(PeerMessageType.MODULE_TRANSFER_COMPLETE);
  }

  private handleNewRouter = (router: Router) => {
    // need to verify connectability and registrationhash
    // and signature
    this.modules.rzrDB.storeRouter(router);
  };

  private handleNewModule = (moduleContent: Uint8Array) => {
    // keep in mind it may happen that more than one event is fired
    // tx hash should be the same still
    const metadata = applySchema<ModuleMetadata>(
      moduleMetadataSchema,
      moduleContent.slice(1, 1024)
    );

    const contentHash = digestModule(moduleContent);

    Promise.all([
      this.modules.rzrDB.loadModuleRegistration(contentHash),
      this.modules.rzrDB.loadModuleMetadata(contentHash),
    ])
      .then(([moduleRegistration, _metadata]) => {
        // TODO: allow non verified app setting here
        if (!moduleRegistration)
          throw new Error("application registration not found");
        const updatedAt = (_metadata as ModuleMetadata)?.updatedAt;

        if (updatedAt && updatedAt >= metadata.updatedAt)
          throw new Error("downgrade attempt");

        const signer = verifyMessage(
          getBytes(
            sha256(moduleContent.slice(1 + constants.SIGNATURE_BYTE_LENGTH))
          ),
          hexlify(moduleContent.slice(1, 1 + constants.SIGNATURE_BYTE_LENGTH))
        ).toLowerCase();

        if (signer !== moduleRegistration.owner)
          throw new Error(
            `malformed signature ${signer} vs ${moduleRegistration.owner}`
          );

        if (
          moduleRegistration &&
          moduleRegistration.contentHash !== digestModule(moduleContent)
        )
          throw new Error("on chain hash verification failed");

        return this.modules.rzrDB.storeModule(moduleContent);
      })
      .catch((e) => {
        console.log(e.message);
      });
  };
}
