import { Clock, Log, LogEmitter } from "../../../lvl0";
import { RzRDB } from "../../../lvl1";
import { Authentication } from "../../../lvl3";
import { SignalingProtocol } from "./SignalingProtocol";
import {
  getBytes,
  concat,
  toBeHex,
  zeroPadValue,
  randomBytes,
  verifyMessage,
  hexlify,
  Wallet,
} from "ethers";

import {
  PingSchema,
  SignalMessageType,
  routerMessageSchemas,
  RouterMessageSchemas,
  SignalMessageSchema,
  OfferSchema,
  AnswerSchema,
  CandidateSchema,
} from "./messageTypes";

import { buildSchema, applySchema } from "../../../../utils";

export type SignalServerConnectionEventMap = {
  sendingMessageStatus: boolean;
  receivingMessageStatus: boolean;
  ping: PingSchema;
  candidate: CandidateSchema;
};

export type SignalingProtocols = {
  SSE: typeof SignalingProtocol;
  //WebSocket: typeof SignalingProtocol;
};

export class SignalServerConnection extends LogEmitter<SignalServerConnectionEventMap> {
  address: string;
  url: string;
  isPermanent: boolean;
  isConnected: boolean = false;

  private protocol: SignalingProtocol;

  private modules: {
    clock: Clock;
    rzrDB: RzRDB;
    authentication: Authentication;
  };

  constructor(
    protocols: SignalingProtocols, // could do some sort of discovery in the future
    log: Log,
    modules: {
      clock: Clock;
      rzrDB: RzRDB;
      authentication: Authentication;
    },
    address: string,
    url: string,
    isPermanent: boolean
  ) {
    super(log, `Router (${address})`);

    this.isPermanent = isPermanent;

    this.modules = modules;

    this.address = address;
    this.url = url;

    // eventually this should be determined here
    // TODO: think about a lvl0 META module, that just basically holds the scaffolding for all these modules, then we don't have to drill down the classes like this.
    this.protocol = new protocols.SSE(url);
  }

  reset() {
    this.protocol.reset();
    this.removeAllListeners();
  }

  onPing = (pingData: ArrayBuffer) => {
    this.emit(
      "ping",
      applySchema<PingSchema>(
        routerMessageSchemas[SignalMessageType.PING],
        pingData
      )
    );
  };

  onCandidate = (candidateData: ArrayBuffer) => {
    this.emit(
      "candidate",
      applySchema<CandidateSchema>(
        routerMessageSchemas[SignalMessageType.CANDIDATE],
        candidateData
      )
    );
  };

  connect() {
    const isWithoutAddress = this.address === hexlify(new Uint8Array(20));
    return this.protocol
      .getAddress()
      .then((address: string | null) => {
        if (
          !address ||
          (!isWithoutAddress && this.address !== address.toLowerCase())
        ) {
          // TODO: consider this for a second, should only flag if connection was at some point successful
          if (!isWithoutAddress) this.modules.rzrDB.flagRouter(this.address);
          throw new Error("connection failed");
        }

        this.address = address.toLowerCase();

        return this.modules.authentication.authorize(this.address);
      })
      .then((authorization) => this.protocol.connect(authorization))
      .then(() => {
        this.isConnected = true;

        if (isWithoutAddress) {
          this.modules.rzrDB.removeRouter(hexlify(new Uint8Array(20)));
          this.modules.rzrDB.storeRouter({
            address: this.address,
            url: this.url,
            downSince: 0,
          });
        }

        this.protocol.addEventListener("ping", this.onPing);
        this.protocol.addEventListener("candidate", this.onCandidate);
        this.protocol.addEventListener("sendingMessageStatus", (status) =>
          this.emit("sendingMessageStatus", status)
        );
        this.protocol.addEventListener("receivingMessageStatus", (status) =>
          this.emit("receivingMessageStatus", status)
        );

        // watch for connection drop...
        // once(this.protocol.connectionStatusChanged)
      });
  }

  ping(pingData: PingSchema): Promise<OfferSchema | null> {
    if (!this.isConnected) return Promise.resolve(null);

    // probably best to add timeouts here...
    return this._log(
      `pinging ${pingData.identity}`,
      this.modules.authentication
        .authorize(this.address)

        .then((authorization) =>
          this.protocol.sendPing(
            pingData.identity,
            buildSchema(routerMessageSchemas[SignalMessageType.PING], pingData),
            authorization
          )
        )
        .then((result) => {
          if (!result) return null;

          return applySchema<OfferSchema>(
            routerMessageSchemas[SignalMessageType.OFFER],
            result
          );
        })
    );
  }

  sendOffer(offerData: OfferSchema): Promise<AnswerSchema | null> {
    if (!this.isConnected) return Promise.resolve(null);

    return this._log(
      `sending offer to ${offerData.identity}`,
      this.modules.authentication.authorize(this.address)
    )
      .then((authorization) =>
        this.protocol.sendOffer(
          offerData.identity,
          buildSchema(routerMessageSchemas[SignalMessageType.OFFER], offerData),
          authorization
        )
      )
      .then((result) => {
        if (!result) return null;

        return applySchema<AnswerSchema>(
          routerMessageSchemas[SignalMessageType.ANSWER],
          result
        );
      });
  }

  // TODO: the below is basically just generic sending data functions

  sendAnswer(answerData: AnswerSchema) {
    if (!this.isConnected) return Promise.resolve(null);

    return this._log(
      `sending answer to ${answerData.identity}`,
      this.modules.authentication.authorize(this.address)
    ).then((authorization) =>
      this.protocol.sendAnswer(
        answerData.identity,
        buildSchema(routerMessageSchemas[SignalMessageType.ANSWER], answerData),
        authorization
      )
    );
  }

  sendCandidate(candidateData: CandidateSchema) {
    if (!this.isConnected) return Promise.resolve(null);

    return this._log(
      `sending candidate to ${candidateData.identity}`,
      this.modules.authentication.authorize(this.address)
    ).then((authorization) =>
      this.protocol.sendCandidate(
        candidateData.identity,
        buildSchema(
          routerMessageSchemas[SignalMessageType.CANDIDATE],
          candidateData
        ),
        authorization
      )
    );
  }

  sendUnavailable(identity: string) {
    if (!this.isConnected) return Promise.resolve(null);

    return this._log(
      `rejecting connection to ${identity}`,
      this.modules.authentication.authorize(this.address)
    ).then((authorization) =>
      this.protocol.sendUnavailable(identity, authorization)
    );
  }
}
