import { Button, Inline, Label, Toggle, Input } from "rzr-components";
import styled from "styled-components";

const generateId = (title: string) => title.replaceAll(" ", "_");

export const BooleanField: React.FC<{
  onChange: (value: boolean) => void;
  value: boolean;
  title: string;
}> = ({ value, onChange, title }) => {
  const id = generateId(title);
  return (
    <Inline as="label" $justify="space-between">
      <Label htmlFor={id}>{title}</Label>
      <Toggle id={id} onChange={onChange} isOn={value} />
    </Inline>
  );
};

const NumericFieldContainer = styled(Inline)`
  > ${Inline} > input {
    max-width: 1.5rem;
    text-align: center;
  }
`;

export const NumericField: React.FC<{
  onChange: (value: number) => void;
  value: number;
  title: string;
  maxValue?: number;
  minValue?: number;
  disabledReason?: string;
}> = ({ value, minValue, maxValue, onChange, title, disabledReason }) => {
  const id = generateId(title);
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const input = (e.target as HTMLFormElement)[0] as HTMLInputElement;
    const value = parseInt(input.value);

    if ((maxValue && value > maxValue) || (minValue && value < minValue)) {
      // set error
      return;
    }

    onChange(value);
  };
  return (
    <NumericFieldContainer
      $align="center"
      as="form"
      onSubmit={handleSubmit}
      $justify="flex-end"
      $gap="lg"
    >
      <Label htmlFor={id}>{title}</Label>
      <Inline $gap="md">
        <Input id={id} defaultValue={value} />
        <Button type="submit" $size="sm">
          set
        </Button>
      </Inline>
    </NumericFieldContainer>
  );
};
