import EventEmitter from "../../../../EventEmitter";
import {
  AnswerSchema,
  OfferSchema,
  PingSchema,
  CandidateSchema,
} from "./messageTypes";
import { AuthorizationSignature } from "../../../../types";

export type SignalingProtocolEventMap = {
  ping: ArrayBuffer;
  candidate: ArrayBuffer;
  connectionStatusChanged: boolean;
  sendingMessageStatus: boolean;
  receivingMessageStatus: boolean;
};

/*
 * responsible for low-level signaling
 *
 * it's only the protocol itself
 * not the implementation
 *
 * there is absolutely no need for authentication
 * signaling servers aren't meant to be trusted entities
 * no sensitive information is sent through
 * */

export class SignalingProtocol extends EventEmitter<SignalingProtocolEventMap> {
  url: string;

  constructor(url: string) {
    super();

    this.url = url;
  }

  getAddress(): Promise<string | null> {
    return Promise.resolve(null);
  }

  connect(authorization: AuthorizationSignature): Promise<boolean> {
    return Promise.resolve(false);
  }

  async sendPing(
    identity: string,
    pingData: ArrayBuffer,
    authorization: AuthorizationSignature
  ): Promise<null | ArrayBuffer> {
    return null;
  }

  async sendOffer(
    identity: string,
    offerData: ArrayBuffer,
    authorization: AuthorizationSignature
  ): Promise<null | ArrayBuffer> {
    return null;
  }

  sendAnswer(
    identity: string,
    answerData: ArrayBuffer,
    authorization: AuthorizationSignature
  ) {}

  sendCandidate(
    identity: string,
    candidate: ArrayBuffer,
    authorization: AuthorizationSignature
  ) {}

  sendUnavailable(
    identity: string,
    authorization: AuthorizationSignature
  ) {}

  reset() {
    // TODO: this should probably be done a level higher
    this.removeAllListeners();
  }
}
