import { useState, useRef, useCallback } from "react";
import styled from "styled-components";
import {
  Modal,
  Inline,
  Input,
  Button,
  Stack,
  H2,
  H3,
  P,
  Label,
  FileInput,
} from "rzr-components";
import ModuleProcessor from "../../../rzr/modules/ModuleMatrix/moduleProcessor";
import { WebModuleFormat, ModuleMetadata } from "../../../rzr";

type FormElement = HTMLFormElement & {
  elements: {
    rootFile: HTMLInputElement;
    [k: string]: HTMLInputElement;
  };
};

enum ModulePath {
  PRODUCTION_MODULE,
  LOCAL_MODULE,
}

const ModuleDetails: React.FC<{
  onSuccess: (moduleFiles: WebModuleFormat | string) => void;
}> = ({ onSuccess }) => {
  const [modulePath, setModulePath] = useState<ModulePath | null>(null);
  const [dependencies, setDependencies] = useState<string[]>([]);
  const files = useRef(new Map<string, Uint8Array>());
  const [rootFileContent, setRootFileContent] = useState("");
  const [rootFileName, setRootFileName] = useState("");
  const [hasFileCollctionFinished, setFileCollectionStatus] =
    useState<boolean>(false);

  const handleDependenciesChanged = useCallback(
    (uploadedFiles: File[]) => {
      let processedFiles = 0;
      let acceptedFiles = 0;

      for (const file of uploadedFiles as File[]) {
        const fileReader = new FileReader();
        const fileName = dependencies.find((fn) => fn.includes(file.name));

        fileReader.onload = (e) => {
          if (fileName && e?.target?.result) {
            acceptedFiles++;
            files.current.set(
              fileName,
              new Uint8Array(e.target.result as ArrayBuffer)
            );
          }

          if (++processedFiles === uploadedFiles.length) {
            setDependencies(
              dependencies.filter(
                (fn) => !Array.from(files.current.keys()).includes(fn)
              )
            );

            if (dependencies.length === acceptedFiles) {
              onSuccess({ rootFileContent, resources: files.current });
            }
          }
        };

        fileReader.readAsArrayBuffer(file);
      }
    },
    [dependencies, rootFileContent, onSuccess]
  );

  const handleRootFileChanged = (files: File[]) => {
    const rootFile = files[0];

    if (!rootFile || rootFile.size > 8192 || rootFile.type !== "text/html") {
      // appropriately handle error
      return;
    }

    const processor = new ModuleProcessor();

    processor.onParse = setDependencies;
    processor.onRootFileContent = setRootFileContent;

    processor.parseRootFile(rootFile);

    setRootFileName(rootFile.name);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    onSuccess((e.target as HTMLFormElement).modulePath.value);
  };

  return modulePath === null ? (
    <Stack
      onSubmit={handleSubmit}
      as="form"
      $gap="md"
      $margin="md"
      $justify="space-between"
      $expand
    >
      <P>
        If you are experimenting locally, set your build path relative to the
        directory where RzR is running from. Otherwise proceed with production
        app deployment.
      </P>
      <Inline as="label" $gap="sm">
        module path:
        <Input name="modulePath" placeholder="/modules/build" />
      </Inline>
      <Inline $justify="space-between" $align="center">
        <Button
          type="button"
          onClick={() => setModulePath(ModulePath.PRODUCTION_MODULE)}
        >
          install production app
        </Button>
        <Button $variant="secondary">next</Button>
      </Inline>
    </Stack>
  ) : (
    <Stack $gap="md" $margin="md" $justify="space-between" $expand>
      <>
        <Inline as="label" $margin="lg" $expand>
          <FileInput
            label={rootFileName ? rootFileName : "select root file"}
            name="rootFile"
            onChange={handleRootFileChanged}
          />
        </Inline>
        {rootFileContent && (
          <>
            <H3 $margin="md">Missing files</H3>
            <ul>
              {dependencies.map((filename) => (
                <li key={filename}>{filename}</li>
              ))}
            </ul>
            <FileInput
              onChange={handleDependenciesChanged}
              name="dependencies"
              multiple
            />
          </>
        )}
      </>
    </Stack>
  );
};

export default ModuleDetails;
