import {
  Signer,
  toNumber,
  hexlify,
  getBytes,
  toBeHex,
  zeroPadValue,
  dataSlice,
  concat,
  Contract,
} from "ethers";
import { LogEmitter, Log } from "../../lvl0";
import { RzRDB } from "../../lvl1";
import {
  IntentStatus,
  PeerMessageType,
  ModuleAddressesSchema,
  ConnectionMatrix,
} from "../../lvl4";
import { Ethereum } from "../../lvl2";
import {
  Environment,
  IntentUpdateEvent,
  PeerMessageEvent,
  RPCTarget,
  ContactPeerEvent,
} from "./environment";

import { once } from "../../../utils";
import { Notification } from "../../../types";
import constants from "../../../constants";

const moduleSyncIntent = 3;

const getProperty = (o: any, path: string[]): any => {
  if (path.length === 0 || path[0] === "") return o;

  return getProperty(o[path[0]], path.slice(1));
};

const shiftIntent = (moduleIdWithIntent: number, isRightShift?: true) =>
  Number(
    isRightShift
      ? BigInt(moduleIdWithIntent) >> BigInt(8)
      : BigInt(moduleIdWithIntent) << BigInt(8)
  );

const moduleAddressToId = (moduleAddress: string) => {
  return toNumber(getBytes(dataSlice(moduleAddress, 0, 3)));
};

export class ModuleMatrix<ModuleFormat> extends LogEmitter<{
  processingStatus: boolean;
  notification: Notification;
}> {
  contracts = new Map<string, Contract>();
  environment: Environment;

  modules: {
    rzrDB: RzRDB;
    ethereum: Ethereum;
    connectionMatrix: ConnectionMatrix;
  };

  constructor(
    log: Log,
    ModuleEnvironment: typeof Environment,
    rzrDB: RzRDB,
    ethereum: Ethereum,
    connectionMatrix: ConnectionMatrix
  ) {
    super(log, "ModuleMatrix");

    this.environment = new ModuleEnvironment(this.handleRPC);
    this.modules = {
      rzrDB,
      ethereum,
      connectionMatrix,
    };

    this.attachListeners();
  }

  private attachListeners() {
    const redirect =
      <
        EventMap extends { [k: string | number]: any },
        K extends keyof EventMap
      >(
        type: K
      ) =>
      (ev: EventMap[K]) => {
        this.environment.broadcast(type, ev);
      };

    this.modules.connectionMatrix.addEventListener(
      "newPeerConnection",
      this.handleNewPeerConnection
    );

    this.modules.connectionMatrix.addEventListener(
      "peerAuthorized",
      redirect("peerAuthorized")
    );

    this.modules.connectionMatrix.addEventListener(
      "peerConnected",
      redirect("peerConnected")
    );

    this.modules.connectionMatrix.addEventListener(
      "peerDisconnected",
      redirect("peerDisconnected")
    );

    this.modules.ethereum.addEventListener(
      "chainChanged",
      redirect("chainChanged")
    );

    this.modules.ethereum.addEventListener(
      "accountChanged",
      redirect("accountChanged")
    );

    // For testing only
    once(this.modules.connectionMatrix, "routersConnected", {
      precondition: (online) => online,
    });

    // these are all, non-interactive forget events
    // probably still would be better to make it non-events
    this.environment.addEventListener("contactPeer", this.onContactPeer);
    this.environment.addEventListener("notification", this.onNotification);
    this.environment.addEventListener("peerMessage", this.onPeerMessage);
    this.environment.addEventListener("intentUpdate", this.onIntentUpdate);
  }

  private handleNewPeerConnection = (identity: string) => {
    const peer = this.modules.connectionMatrix.getPeer(identity);

    if (!peer) return;

    peer.updateIntent(moduleSyncIntent, IntentStatus.REQUESTED);

    // make sure to only notify modules that are installed on both peers
    // so this one is only using addresses, need to send the corresponding contentHash, different contentHash might not be able to communicate?
    // question: should we enforce, or let modules deal with it
    // I'd say let modules decide which modules are compatible
    once(peer, PeerMessageType.INSTALLED_MODULES)
      .then((addresses) => {
        for (
          let i = 0;
          i < addresses.length;
          i += constants.ADDRESS_BYTE_LENGTH
        ) {
          const address = hexlify(
            addresses.slice(i, i + constants.ADDRESS_BYTE_LENGTH)
          );

          if (!this.contracts.has(address)) continue;

          const moduleId = moduleAddressToId(address);

          // notify modules about new connection
          // if there are possible intents, forward them as well.
          this.environment.notify(address, "newPeerConnection", {
            identity,
            intents: Array.from(peer.intents.keys())
              .filter(
                (intentId: number) => shiftIntent(intentId, true) === moduleId
              )
              .map((intentId: number) => intentId - shiftIntent(moduleId)),
          });

          // to redirect messages to module
          peer.addEventListener(moduleAddressToId(address), (message) => {
            this.environment.notify(address, message[0], {
              from: identity,
              message: hexlify(message.slice(1)),
            });
          });
        }
      })
      .then(() => peer.updateIntent(moduleSyncIntent, IntentStatus.COMPLETED));

    once(peer, "peerAuthorized").then(() =>
      this.modules.connectionMatrix.sendTo(
        identity,
        getBytes(
          concat([
            new Uint8Array(constants.MODULE_ID_BYTE_LENGTH),
            new Uint8Array([PeerMessageType.INSTALLED_MODULES]),
            ...Array.from(this.contracts.keys()),
          ])
        )
      )
    );

    peer.updateIntent(moduleSyncIntent, IntentStatus.IN_PROGRESS);
  };

  private onNotification = (notification: Notification) => {
    this.emit("notification", notification);
  };

  private onIntentUpdate = ({
    identity,
    intent,
    sourceModuleAddress,
    intentStatus,
  }: IntentUpdateEvent) => {
    const peer = this.modules.connectionMatrix.getPeer(identity);

    if (!peer) return;

    peer.updateIntent(
      shiftIntent(moduleAddressToId(sourceModuleAddress)) + intent,
      intentStatus
    );
  };

  onContactPeer = ({
    identity,
    intents,
    sourceModuleAddress,
  }: ContactPeerEvent) => {
    const moduleId = shiftIntent(moduleAddressToId(sourceModuleAddress));

    this.modules.connectionMatrix.contactPeer(
      identity,
      intents.map((i: number) => i + moduleId)
    );

    const announceConnection = () =>
      this.environment.notify(sourceModuleAddress, "peerConnected", identity);

    const peer = this.modules.connectionMatrix.getPeer(identity);

    if (peer?.isConnected) {
      if (peer.isAuthorized) {
        announceConnection();
        return;
      }
      once(peer, "peerAuthorized").then(announceConnection);
      return;
    }

    once(this.modules.connectionMatrix, "newPeerConnection", {
      timeoutIn: 5000,
    })
      .then(announceConnection)
      .catch(() => {
        this.environment.notify(
          sourceModuleAddress,
          "peerConnectionFailed",
          identity
        );
      });
  };

  handleRPC = (
    source: string,
    target: RPCTarget,
    method: string,
    params: any[],
    resultPath: string
  ) => {
    const contract = this.contracts.get(source);
    if (!contract) return Promise.reject("contract not initialized");
    if (target === "rzr" && !method.startsWith("load"))
      return Promise.reject("only read methods allowed");
    this.emit("processingStatus", true);
    const path = method.split(".");
    try {
      const targetProperty =
        target === "ethereum"
          ? getProperty(this.modules.ethereum as any, path)
          : target === "provider"
          ? getProperty(this.modules.ethereum.provider, path)
          : target === "contract"
          ? getProperty(contract, path)
          : getProperty(this.modules.rzrDB, path);

      const result =
        typeof targetProperty === "function"
          ? targetProperty.bind(
              target === "ethereum"
                ? this.modules.ethereum
                : target === "rzr"
                ? this.modules.rzrDB
                : target === "provider"
                ? this.modules.ethereum.provider
                : contract
            )(...params)
          : targetProperty;

      return (() => (result.then ? result : Promise.resolve(result)))().then(
        (r: any) => getProperty(r, resultPath.split("."))
      );
    } catch (e) {
      return Promise.reject(e);
    } finally {
      this.emit("processingStatus", false);
    }
  };

  onPeerMessage = ({
    sourceModuleAddress,
    identity,
    messageType,
    message,
  }: PeerMessageEvent) => {
    this.modules.connectionMatrix.sendTo(
      identity,
      getBytes(
        concat([
          zeroPadValue(
            toBeHex(moduleAddressToId(sourceModuleAddress)),
            constants.MODULE_ID_BYTE_LENGTH
          ),
          new Uint8Array([messageType]),
          message ? getBytes(message) : new Uint8Array(),
        ])
      )
    );
  };

  parseModule(moduleData: ModuleFormat) {
    return this.environment.parseModule(moduleData);
  }

  registerModule(address: string, moduleData: Uint8Array, sourceUrl?: string) {
    const abiLength = toNumber(moduleData.slice(0, 4));

    const abi = JSON.parse(
      new TextDecoder().decode(moduleData.slice(4, abiLength + 4))
    );

    const contract = this.modules.ethereum.createContract(address, abi);

    this.contracts.set(address, contract);

    const eventNames = abi
      .filter((p: any) => p.type === "event")
      .map((e: any) => e.name);

    for (const name of eventNames) {
      contract.on(name, (...params) =>
        this.environment.notify(address, name, params)
      );
    }

    return this.environment.registerModule(
      address,
      sourceUrl || moduleData.slice(4 + abiLength)
    );
  }

  stopModule(address: string) {
    this.contracts.delete(address);

    this.environment.stopModule(address);

    const moduleId = moduleAddressToId(address);

    for (const [identity, peer] of Array.from(
      this.modules.connectionMatrix.peers.entries()
    )) {
      for (const intent of Array.from(peer.intents.keys())) {
        if (intent >> 8 === moduleId) {
          peer.updateIntent(intent, IntentStatus.COMPLETED);
        }
      }
    }
    // TODO: this is fine, but other end should be notified as well?
  }
}

export { Environment };
